import * as api from '../api';
import {LLM} from './llm';
import {scripts} from './playback-scripts/scripts';
import * as utils from './playback-scripts/utils';

const sleepTime = 500;
async function abortableSleep(abortSignal: AbortSignal | undefined) {
  await new Promise((resolve, reject) => {
    if (abortSignal?.aborted) {
      reject(abortSignal.reason);
    }
    abortSignal?.addEventListener('abort', () => {
      reject(abortSignal.reason);
    });
    setTimeout(resolve, sleepTime);
  });
}

function visitScriptNames(
  scriptNames: string | undefined,
  onScript: (name: string) => void,
  onUnrecognized: (name: string) => void
) {
  for (const scriptName of scriptNames?.split(',') ?? []) {
    let cleanScriptName = scriptName.trim();
    if (cleanScriptName.startsWith('"') || cleanScriptName.startsWith("'")) {
      cleanScriptName = cleanScriptName.substring(1);
    }
    if (cleanScriptName.endsWith('"') || cleanScriptName.endsWith("'")) {
      cleanScriptName = cleanScriptName.substring(0, cleanScriptName.length - 1);
    }
    if (cleanScriptName in scripts) {
      onScript(cleanScriptName);
    } else {
      onUnrecognized(scriptName);
    }
  }
}

class StreamingResponse {
  constructor(public payload: utils.Response, public index: number = 0) {}
}

export class PlaybackLLM implements LLM {
  currentScript: utils.Response[] | undefined;
  currentScriptIndex: number = -1;
  currentScriptName: string = '';
  currentScriptListIndex: number = -1;

  constructor(private scriptList: string[] = []) {}

  systemMessage(text: string): utils.Response {
    return utils.textMessage(text);
  }

  scriptString(): string {
    const scriptNames = Object.getOwnPropertyNames(scripts).join('", "');
    return `Please select one or more of the available scripts: "${scriptNames}"`;
  }

  helpMessage(text: string | undefined): utils.Response {
    const unrecognizedScripts: string[] = [];
    visitScriptNames(
      text,
      _ => undefined,
      name => unrecognizedScripts.push(name)
    );
    const unrecognizedScriptsString = unrecognizedScripts.join(', ');
    return this.systemMessage(
      `Sorry, I did not recognize "${unrecognizedScriptsString}". ${this.scriptString()}`
    );
  }

  nextScriptMessage(): utils.Response {
    return this.systemMessage(`\
"${this.currentScriptName}" is all done. \
Moving on to "${this.scriptList[this.currentScriptListIndex + 1]}".`);
  }

  lastMessage(): utils.Response {
    return this.systemMessage(`"${this.currentScriptName}" is all done. ${this.scriptString()}`);
  }

  ensureScriptInitialized(text: string | undefined): boolean {
    // Assume if we don't have a script loaded, then we've been given a script name, or list of
    // script names seperated by commas.
    if (text && this.currentScript === undefined) {
      let unrecognizedScript = false;
      this.scriptList = [];
      visitScriptNames(
        text,
        name => this.scriptList.push(name),
        _ => (unrecognizedScript = true)
      );
      if (unrecognizedScript) {
        return false;
      }
      this.currentScriptListIndex = 0;
      this.currentScriptIndex = 0;
      this.currentScriptName = this.scriptList[this.currentScriptListIndex];
      this.currentScript = scripts[this.currentScriptName];
    }
    return true;
  }

  scriptIteration(): utils.Response {
    if (this.currentScriptIndex! < this.currentScript!.length) {
      return this.currentScript![this.currentScriptIndex++];
    } else if (this.currentScriptListIndex < this.scriptList.length - 1) {
      const nextScriptMessage = this.nextScriptMessage();
      this.currentScriptName = this.scriptList[++this.currentScriptListIndex];
      this.currentScript = scripts[this.currentScriptName];
      this.currentScriptIndex = 0;
      return nextScriptMessage;
    } else {
      const lastMessage = this.lastMessage();
      this.currentScript = undefined;
      return lastMessage;
    }
  }

  handleErrorSentinel(response: api.GenerateContentResponse): api.GenerateContentResponse {
    if (response === utils.errorSentinel) {
      throw Error('Mock system error!!');
    }
    return response;
  }

  async generateContent(
    generateContentRequest: api.GenerateContentRequest,
    token?: string,
    abortSignal?: AbortSignal
  ): Promise<api.GenerateContentResponse | undefined> {
    const lastMessage =
      generateContentRequest.contents[generateContentRequest.contents.length - 1].parts[0].text;
    if (!this.ensureScriptInitialized(lastMessage)) {
      return this.helpMessage(lastMessage)[0];
    }

    await abortableSleep(abortSignal);
    return this.handleErrorSentinel(this.scriptIteration()[0]);
  }

  async streamGenerateContent(
    generateContentRequest: api.GenerateContentRequest,
    abortSignal?: AbortSignal
  ): Promise<ReadableStream<api.GenerateContentResponse>> {
    const lastMessage =
      generateContentRequest.contents[generateContentRequest.contents.length - 1].parts[0].text;
    let streamingResponse: StreamingResponse;
    if (!this.ensureScriptInitialized(lastMessage)) {
      streamingResponse = new StreamingResponse(this.helpMessage(lastMessage));
    } else {
      streamingResponse = new StreamingResponse(this.scriptIteration());
    }
    const that = this;
    return new ReadableStream<api.GenerateContentResponse>({
      start(controller) {
        async function pump(): Promise<void> {
          if (streamingResponse.index < streamingResponse.payload.length) {
            controller.enqueue(
              that.handleErrorSentinel(streamingResponse.payload[streamingResponse.index++])
            );
            return abortableSleep(abortSignal).then(pump);
          } else {
            controller.close();
            return Promise.resolve();
          }
        }
        return pump();
      },
    });
  }

  getGenerationConfig(): api.GenerationConfig {
    return {};
  }
}
