import {
  modelText,
  userText,
  functionResponseToContent,
  listProjectFilesSuccess,
  listProjectFilesCall,
} from './conversation-utils';
import * as api from './api';
import {ChatHistory} from '../chat-history';
import {listFilesRecursive} from '../file-utils';
import {ignoredNames} from './functions/list-project-files';

export async function initializeAida(history: ChatHistory) {
  // On AIDA we pass the system instruction properly, and not via the chat history. Also,
  // currently for AIDA we seed the conversation with a call to list_project_files This is to
  // ensure the first user request in the context involves tool use. It also helps give the model
  // some context on the codebase. However, this really doesn't work well for large code bases.
  // We've tried a simple truncation approach, but this seems to do more harm than good.
  // TODO(joshualitt): Try getting an LLM generated high level summary of the codebase.
  // Alternatively, add more enticing codebase exploration tools. With the right set of tools, we
  // have seen the model show initiative in exploring a codebase.
  let files = await listFilesRecursive('.', ignoredNames);
  const filesString = files.join(', ');
  const filesJSONString = JSON.stringify(files);
  history.pushInternalMessage(userText('Please list project files.'));
  history.pushInternalMessage(listProjectFilesCall('.'));
  history.pushInternalMessage(listProjectFilesSuccess(filesJSONString));
  history.pushInternalMessage(modelText(`The project files are: ${filesString}`));
}

export function initializeGenAi(history: ChatHistory) {
  history.pushInternalMessage(
    userText(`\
You are a conversational coding assistant in an IDE. \
Please use the available tools to help me solve problems. \
Remember to think step-by-step.\
`)
  );
  history.pushInternalMessage(
    modelText(`\
OK, I will use the available tools to help you solve problems. \
I will remember to think step-by-step.\
`)
  );
}

export function rejectFunctionCall(
  history: ChatHistory,
  functionResponse: api.FunctionResponse,
  reason: string
) {
  history.pushInternalMessage(functionResponseToContent(functionResponse));
  history.pushInternalMessage(modelText('Why did you reject the last function call?'));
  history.pushInternalMessage(userText(`The last function call was rejected because ${reason}`));
}
