/// <reference lib="dom" />
import * as api from '../api';
import {IdxAgentSettings} from '../idx-agent-settings';

export abstract class LLM {
  abstract generateContent(
    generateContentRequest: api.GenerateContentRequest,
    token?: string,
    abortSignal?: AbortSignal
  ): Promise<api.GenerateContentResponse | undefined>;
  abstract streamGenerateContent(
    generateContentRequest: api.GenerateContentRequest,
    abortSignal?: AbortSignal
  ): Promise<ReadableStream<api.GenerateContentResponse | undefined>>;
  abstract getGenerationConfig(overrideConfig?: api.GenerationConfig): api.GenerationConfig;
}

export class LiveLLM extends LLM {
  constructor(public idxAgentSettings: IdxAgentSettings) {
    super();
  }

  async generateContent(
    generateContentRequest: api.GenerateContentRequest,
    token?: string,
    abortSignal?: AbortSignal
  ): Promise<api.GenerateContentResponse | undefined> {
    const bodyJsonString: string = JSON.stringify(generateContentRequest);
    const url = this.idxAgentSettings.urlIsAida
      ? this.idxAgentSettings.url
      : `${this.idxAgentSettings.url}/models/${this.idxAgentSettings.llmModel}:generateContent`;
    const response = await fetch(url, {
      method: 'POST',
      headers:
        this.idxAgentSettings.urlIsAida && this.idxAgentSettings.urlNeedsProjectId
          ? {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${token}`,
            }
          : {
              'Content-Type': 'application/json',
              'X-Goog-Api-Key': this.idxAgentSettings.apiKey,
            },
      body: bodyJsonString,
      signal: abortSignal,
    });

    return (await response.json()) as api.GenerateContentResponse | undefined;
  }

  async streamGenerateContent(
    generateContentRequest: api.GenerateContentRequest,
    abortSignal?: AbortSignal
  ): Promise<ReadableStream<api.GenerateContentResponse>> {
    const bodyJsonString: string = JSON.stringify(generateContentRequest);
    const response = await fetch(
      `${this.idxAgentSettings.url}/models/${this.idxAgentSettings.llmModel}:streamGenerateContent?alt=sse`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Goog-Api-Key': this.idxAgentSettings.apiKey,
        },
        body: bodyJsonString,
        signal: abortSignal,
      }
    );
    const responseReader = response.body!.pipeThrough(new TextDecoderStream()).getReader();

    // In earlier attempts to implement streaming, incomplete chunks of JSON were returned.
    // This doesn't seem to happen all of the time. In case it does, we collect the JSON fragments
    // in `data`.
    let data = '';
    return new ReadableStream<api.GenerateContentResponse>({
      async pull(controller) {
        const {value, done} = await responseReader.read();
        while (true) {
          if (done) {
            if (data) {
              controller.enqueue(JSON.parse(data));
            }
            controller.close();
          }

          // For some reason, we see a 'data: ' prefix returned before the JSON, so we remove it
          // here.
          if (value?.startsWith('data: ')) {
            data += value?.substring('data: '.length);
          } else {
            data += value;
          }
          try {
            controller.enqueue(JSON.parse(data));
            data = '';
            return;
          } catch {}
        }
      },
    });
  }

  getGenerationConfig(overrideConfig: api.GenerationConfig): api.GenerationConfig {
    return {
      maxOutputTokens: overrideConfig.maxOutputTokens ?? this.idxAgentSettings.llmMaxOutputTokens,
      temperature: overrideConfig.temperature ?? this.idxAgentSettings.llmMinTemperature,
      candidateCount: overrideConfig.candidateCount ?? this.idxAgentSettings.candidateCount,
      stopSequences: overrideConfig.stopSequences ?? [],
      topK: overrideConfig.topK ?? this.idxAgentSettings.llmMinTopK,
      topP: overrideConfig.topP ?? this.idxAgentSettings.llmMinTopP,
    };
  }
}
