async function waitForIDXPluginToBeReady(context: ExtensionContext) {
  const delayMs = 500;

  while (true) {
    let monospaceextension = vscode.extensions.getExtension('google.monospace');
    try {
      if (!monospaceextension) {
        throw new Error('Monospace Extension not found, wait for it to load');
      }
      await monospaceextension.exports.isIdxPluginReady();
      context.idx = monospaceextension;
      console.log('IDX Extension is ready, loading IDX AI plugin');
      return;
    } catch (error) {
      //probably one of those cases where better to log to console?
      console.log('IDX vscode Plugin is not ready, waiting');
      await new Promise(resolve => setTimeout(resolve, delayMs));
    }
  }
}

export async function activate(context: vscode.ExtensionContext) {
  const ctx = {} as ExtensionContext;
  ctx.vsc = context;
  //IDX Plugin needs to be ready for AI plugin to do much, wait for it to be ready
  if (!outsideOfIDX) {
    await waitForIDXPluginToBeReady(ctx);
  }

  ctx.broker = createBroker<ExtensionToWebview, WebviewToExtension, vscode.Webview>(
    new ExtensionBroker()
  );
  ctx.logger = new OutputChannel(OUTPUT_CHANNEL_NAME, LogLevel.ERROR);
  ctx.engine = defaultEngine(ctx);
  ctx.features = new FeatureFlags(ctx);
  ctx.aiExclude = new AiExcludeProvider(ctx);
  ctx.idxAgentSettings = initializeIdxAgentSettings();
  ctx.conversationalAgent = new ConversationalAgent(
    await ctx.features.isDoNotTrackRegion(),
    ctx.idxAgentSettings,
    initializeIdxAgentSystemInterface(),
    ctx.broker,
    async () => {
      if (ctx.idxAgentSettings.urlNeedsProjectId) {
        const exports = ctx.idx?.exports;
        return {projectId: exports?.getProjectId(), token: await exports?.getToken()};
      }
      return undefined;
    }
  );

  initializeThreadManager(context, ctx.idxAgentSettings.isDefault);
  setupPromptProvider(ctx);
  const aiExport = setupAiChat(ctx);
  setupSidekick(ctx);
  setupAiRecitation(ctx);
  setupWebViewThemingSync(ctx);
  initializeTelemetryLogger(ctx);

  return aiExport;
}

function defaultEngine(ctx: ExtensionContext) {
  return makePromptEngine({
    llm: {
      async complete(input: any) {
        ctx.logger.debug('executing prompt:\n' + input.prompt);
        const response = await ctx.idx?.exports.generateCode(
          input.prompt,
          input.stopSequences?.[0] ?? 'Explanation of changes:'
        );
        ctx.logger.debug('generate code response:\n' + JSON.stringify(response));
        const candidates = response?.map(r => ({text: r.summary, ...r})) ?? [];
        return {candidates};
      },
    },
    roleDecorator(role: any) {
      // These role decorators are for generateCode only. Do not use for
      // doConversation.
      if (role === 'model') {
        return [`<start_of_turn>${role}\n`, '\n'];
      }
      return [`<start_of_turn>${role}\n`, `<end_of_turn>\n`];
    },
    retrievers: outsideOfIDX
      ? {}
      : {
          idxdocs: retrieverSemantic({corpusId: CORPUS_ID, apiKey: SEMANTIC_RETRIEVER_API_KEY}),
        },
  });
}
