import * as vscode from 'vscode';
import {ExtensionContext} from './context';
import {getHtmlForWebview} from './html-scaffold';
import {ExtensionBrokerImpl} from './messaging/extension-broker';
import {getLicenseAttributionList} from 'vscode-shared/ai-recitation';

const RECITATION_VIEW_TYPE = 'airecitation.view';

let recitationPanel: vscode.WebviewPanel | undefined;
let focusId: string = '';

export async function setupAiRecitation(ctx: ExtensionContext) {
  let {vsc, broker} = ctx;

  vsc.subscriptions.push(
    vscode.commands.registerCommand('monospace.showLicenseAttribution', args => {
      ensureAndShowRecitationPanel(ctx);
      focusId = args?.anchor || '';
    })
  );

  broker.on('getRecitationLog', async () => sendRecitationLogToWebview(ctx));
}

async function sendRecitationLogToWebview({broker}: ExtensionContext) {
  const attributionsList = await getLicenseAttributionList();
  broker.send('notifyRecitationLog', attributionsList, focusId);
}

function ensureAndShowRecitationPanel(ctx: ExtensionContext) {
  if (recitationPanel) {
    recitationPanel.reveal();
    setTimeout(() => sendRecitationLogToWebview(ctx), 500);
    return recitationPanel;
  }

  const {vsc: context, broker} = ctx;

  recitationPanel = vscode.window.createWebviewPanel(
    RECITATION_VIEW_TYPE,
    'AI License Log',
    vscode.ViewColumn.Two,
    {
      retainContextWhenHidden: true,
    }
  );
  recitationPanel.iconPath = vscode.Uri.joinPath(
    context.extensionUri,
    'resources',
    'icon-small.svg'
  );
  recitationPanel.onDidDispose(() => {
    recitationPanel = undefined;
  });

  setupWebView(broker, recitationPanel.webview, context.extensionUri);
}

function setupWebView(
  broker: ExtensionBrokerImpl,
  webview: vscode.Webview,
  extensionUri: vscode.Uri
) {
  broker.registerReceiver(webview);
  webview.options = {
    enableScripts: true,
    localResourceRoots: [extensionUri],
  };
  webview.html = getHtmlForWebview('airecitationlog', extensionUri, webview);
}
