import * as api from '../../api';
import {Script} from './scripts';
import {
  functionCall,
  textMessage,
  readFile,
  Response,
  runTerminalCommand,
  writeFile,
} from './utils';

export const badFunctionArgumentsScript: Script = {
  name: 'bad function arguments',
  responses: [
    textMessage('tests a few invalid arguments to tools.'),
    // Case 1: write file with invalid path.
    writeFile('\0', 'foo'),
    textMessage('Wrote file with invalid path.'),
    // Case 2: read file that doesn't exist.
    readFile('NON EXISTENT'),
    textMessage('Read non-existent file.'),
    // Case 3: run terminal command that doesn't exist.
    runTerminalCommand('booboofoo'),
    textMessage('Ran non-existent terminal command.'),
  ],
};

function badResponse(payload: any): Response {
  return [payload];
}

export const badResponsesFromLLMScript: Script = {
  name: 'bad responses from llm',
  responses: [
    textMessage('tests a few bad responses from the llm.'),
    // Case 1: undefined response.
    badResponse(undefined),
    textMessage('Sent undefined response.'),
    // Case 2: empty message.
    badResponse({}),
    textMessage('Sent empty message.'),
    // Case 3: message missing parts.
    badResponse({role: 'model'}),
    textMessage('Sent a message missing parts.'),
    // Case 4: message with empty part list.
    badResponse({parts: [], role: 'model'}),
    textMessage('Sent a message with an empty parts list.'),
  ],
};

export const badFunctionCallsScript: Script = {
  name: 'bad function calls',
  responses: [
    textMessage('tests a few bad function calls.'),
    // Case 1: empty function call.
    functionCall('' as api.FunctionName, undefined as unknown as api.FunctionCallArgs),
    textMessage('Sent an empty function call.'),
    // Case 2: invalid function name.
    functionCall('do_stuff' as api.FunctionName, {}),
    textMessage('Sent a message with an invalid function name.'),
    // case 3: correct function name, empty arguments.
    functionCall('write_file' as api.FunctionName, {}),
    textMessage('Sent a message with a real function name but empty arguments.'),
    // case 4: correct function name, wrong arguments.
    functionCall('write_file' as api.FunctionName, {badArg1: 'foo', badArg2: 'foo'}),
    textMessage('Sent a message with a real function name but the wrong arguments'),
    // case 5: correct function name, missing one argument.
    functionCall('write_file' as api.FunctionName, {path: 'foo'}),
    textMessage('Sent a message with a real function name but missing an argument'),
    // case 6: correct function name, with an invalid argument.
    functionCall('write_file' as api.FunctionName, {path: 'foo', textForFile: 'bar'}),
    textMessage('Sent a message with a real function name but with an invalid argument'),
    // case 7: correct function name, correct argument names, but one argument has the wrong value.
    functionCall('write_file' as api.FunctionName, {path: 'foo', content: 3}),
    textMessage('Sent a message with a real function name but one argument has the wrong value.'),
    // case 8: correct function name, correct argument names, but both arguments have the wrong value.
    functionCall('write_file' as api.FunctionName, {path: null, content: 3}),
    textMessage('Sent a message with a real function name but arguments have the wrong values.'),
  ],
};

