import {Uri, Webview} from 'vscode';

export function getHtmlForWebview(
  entryName: string,
  extensionUri: Uri,
  webview: Webview,
  globals?: {[key: string]: any}
) {
  const scriptUri = webview.asWebviewUri(Uri.joinPath(extensionUri, `web-${entryName}.js`));
  const styleUri = webview.asWebviewUri(Uri.joinPath(extensionUri, `web-${entryName}.css`));
  const moniconWoffUri = webview.asWebviewUri(
    Uri.joinPath(extensionUri, 'resources/monicons.woff')
  );
  const codiconCssUri = webview.asWebviewUri(Uri.joinPath(extensionUri, 'codicons/codicon.css'));
  const codiconTtfUri = webview.asWebviewUri(Uri.joinPath(extensionUri, 'codicons/codicon.ttf'));
  const rootExtensionUri = webview.asWebviewUri(extensionUri);
  // Use a nonce to only allow a specific script to be run.
  const nonce = getNonce();

  return `<!DOCTYPE html>
			<html lang="en">
			<head>
				<meta charset="UTF-8">
				<!--
					Use a content security policy to only allow loading images from https or from our extension directory,
					and only allow scripts that have a specific nonce.
				-->
				<meta http-equiv="Content-Security-Policy" content="
          default-src ${webview.cspSource};
          font-src ${webview.cspSource} https://fonts.gstatic.com;
          img-src ${webview.cspSource} https://*.googleusercontent.com;
          frame-src https://*;
          style-src ${webview.cspSource} https://fonts.googleapis.com 'unsafe-inline';
          script-src 'nonce-${nonce}' 'unsafe-eval';">
				<meta name="viewport" content="width=device-width, initial-scale=1.0">
        <link href="${codiconCssUri.toString().replace(/%40/g, '@')}" rel="stylesheet">
				<link href="${styleUri}" rel="stylesheet">
        <link href="https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;500&display=swap" rel="stylesheet">
        <style>
          @font-face {
            font-family: "Monicons";
            src: url("${moniconWoffUri}") format("woff");
            font-weight: normal;
            font-style: normal;
          }
          /* for codicons to work in Safari, this seems to need to be inlined,
             as opposed to being pulled in via codicon.css */
          @font-face {
            font-family: "codicon";
            src: url("${codiconTtfUri}") format("truetype");
            font-weight: normal;
            font-style: normal;
          }
        </style>
			</head>
			<body>
        <div id="root"></div>
        <script nonce="${nonce}">
          window['EXTENSION_ROOT_URI'] = ${JSON.stringify(rootExtensionUri.toString())};
          ${Object.entries(globals || {})
            .map(([k, v]) => {
              return `window['${k}'] = ${JSON.stringify(v)};`;
            })
            .join('\n')}
        </script>
				<script nonce="${nonce}" src="${scriptUri}"></script>
			</body>
			</html>`;
}

function getNonce() {
  let text = '';
  const possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  for (let i = 0; i < 32; i++) {
    text += possible.charAt(Math.floor(Math.random() * possible.length));
  }
  return text;
}
