export interface ConfigProperty {
  fullId: string;
  section: string;
  property: string;
}

function parse(id: string): ConfigProperty {
  let parts = id.split('.');
  return {
    fullId: id,
    section: parts.slice(0, parts.length - 1).join('.'),
    property: parts[parts.length - 1],
  };
}
