import * as api from '../api';
import {ExternalFunction, FunctionState} from './external-function';
import {openDiffView} from './utils';
import {SystemInterface} from '../system-interface';

export class WriteFileFunction extends ExternalFunction {
  name = 'write_file' as const;
  description = 'Writes to a file.';
  parameters: api.ObjectSchema = {
    type: 'object',
    properties: {
      path: {
        type: 'string',
        description: 'The path of the file to be written. This should be a relative path.',
      },
      content: {
        type: 'string',
        description:
          'The content to write to the file. Important to not format in markdown, as this will be written to a file.',
      },
    },
    required: ['path', 'content'],
  };

  actions = {
    open_diff_view: openDiffView,
  };

  constructor(public systemInterface: SystemInterface) {
    super();
  }

  updateArgs(args: api.FunctionCallArgs): api.FunctionCallArgs {
    return {
      content: api.stringWorkaround(args.content),
      path: api.stringWorkaround(args.path),
    };
  }

  async populateMetadata(args: api.FunctionCallArgs): Promise<Record<string, any> | undefined> {
    const {path} = args;
    if (!(await this.systemInterface.fileExists(path))) {
      return {newFile: true, currentFileContent: ''};
    }

    const currentFileContent = await this.systemInterface.readFile(path);
    return {newFile: false, currentFileContent};
  }

  async call(functionState: FunctionState): Promise<void> {
    const args = functionState.args;
    const path = args.path;
    await this.systemInterface.writeFile(path, args.content);
    functionState.success('OK');
  }
}
