import {ExtensionContext} from 'vscode';
import * as protocol from '~common/messaging/modules/aichat/aichat-protocol';
import {ChatHistory} from './chat-history';
import {AICHAT_EVENTS, telemetry} from './telemetry';
import * as crypto from 'crypto';

export const lastEditTimestampSentinel: number = -1;

function idToTimestamp(id: string): number {
  const timestamp = new Date(Number.parseFloat(id)).getTime();
  return Number.isNaN(timestamp) ? 0 : timestamp;
}

export class ChatThread {
  constructor(
    public id: string,
    public uuid: string,
    public title: string,
    public history: ChatHistory,
    public suggestedNextPrompt: string,
    public areActionsEnabled: boolean,
    public lastEditTimestamp: number,
    public isSaved: boolean,
    public isSaving = false
  ) {}

  static defaultNew(id: string, actionsInChatAreEnabled: boolean) {
    return new ChatThread(
      id,
      crypto.randomUUID(),
      '',
      new ChatHistory([], []),
      '',
      actionsInChatAreEnabled,
      lastEditTimestampSentinel,
      false
    );
  }

  static fromReadOnly(thread: protocol.ChatThread): ChatThread {
    return new ChatThread(
      thread.id,
      thread.uuid,
      thread.title,
      new ChatHistory(thread.history, thread.internalHistory ?? []),
      thread.suggestedNextPrompt || '',
      thread.areActionsEnabled ?? false,
      thread.lastEditTimestamp ?? idToTimestamp(thread.id),
      true
    );
  }

  get toReadOnly(): protocol.ChatThread {
    return {
      id: this.id,
      uuid: this.uuid,
      title: this.title,
      history: this.history.user,
      internalHistory: this.history.internal,
      suggestedNextPrompt: this.suggestedNextPrompt,
      areActionsEnabled: this.areActionsEnabled,
      lastEditTimestamp: this.lastEditTimestamp,
    };
  }
}

const CHAT_THREAD_TIMESTAMPS_FILLED_IN = 'idx.ai.chatThreadTimestampsFilledIn';
const CHAT_THREAD_LIST_KEY = 'idx.ai.chatThreadList';
const CHAT_THREAD_DETAIL_PREFIX = 'idx.ai.chatThreads';
class ThreadManager {
  currentThread!: ChatThread;

  // TODO(joshualitt): Cache global state to reduce lookups when possible.
  constructor(public context: ExtensionContext, public actionsInChatAreDefault: boolean) {
    this.openNewThread();
  }

  // This is just for debugging purposes right now, though perhaps we should expose it to users
  // in some fashion.
  async deleteAllThreads() {
    const threads = this.context.globalState.get<protocol.ChatThreadSummary[]>(
      CHAT_THREAD_LIST_KEY,
      []
    );

    await this.context.globalState.update(CHAT_THREAD_LIST_KEY, []);
    for (let thread of threads) {
      await this.context.globalState.update(`${CHAT_THREAD_DETAIL_PREFIX}.${thread.id}`, undefined);
    }
  }

  // TODO(joshualitt): See note below, someday we can safely delete this code.
  async fillin(threads: protocol.ChatThreadSummary[]): Promise<void> {
    for (const thread of threads) {
      let updated = false;
      if (thread.lastEditTimestamp === undefined) {
        thread.lastEditTimestamp = idToTimestamp(thread.id);
        updated = true;
      }
      if (thread.uuid === undefined) {
        thread.uuid = crypto.randomUUID();
        updated = true;
      }
      if (updated) {
        await this.context.globalState.update(`${CHAT_THREAD_DETAIL_PREFIX}.${thread.id}`, thread);
      }
    }
    await this.context.globalState.update(CHAT_THREAD_LIST_KEY, threads);
    await this.context.globalState.update(CHAT_THREAD_TIMESTAMPS_FILLED_IN, true);
  }

  // TODO(joshualitt): Someday we can remove the fillin logic and then this function
  // can be made synchronous.
  async listThreads(): Promise<protocol.ChatThreadSummary[]> {
    const threads = this.context.globalState.get<protocol.ChatThreadSummary[]>(
      CHAT_THREAD_LIST_KEY,
      []
    );
    const timestampsFilledin = this.context.globalState.get<boolean>(
      CHAT_THREAD_TIMESTAMPS_FILLED_IN,
      false
    );
    if (!timestampsFilledin) {
      await this.fillin(threads);
    }
    // TODO(joshualitt): If this becomes a performance issue, let's start storing the threads in
    // sorted order.
    threads.sort((a, b) => b.lastEditTimestamp - a.lastEditTimestamp);
    return threads;
  }

  async deleteThread(threadId: string) {
    const threads = this.context.globalState.get<protocol.ChatThreadSummary[]>(
      CHAT_THREAD_LIST_KEY,
      []
    );

    const newThreads = threads.filter(t => t.id !== threadId);
    await this.context.globalState.update(CHAT_THREAD_LIST_KEY, newThreads);
    await this.context.globalState.update(`${CHAT_THREAD_DETAIL_PREFIX}.${threadId}`, undefined);

    telemetry?.logTelemetryEvent(
      AICHAT_EVENTS.DELETE_THREAD,
      `${CHAT_THREAD_DETAIL_PREFIX}.${threadId}`
    );
  }

  async deleteCurrentThread() {
    if (this.currentThread.isSaved) {
      await this.deleteThread(this.currentThread.id);
      this.currentThread.isSaved = false;
    }
    this.openNewThread();
  }

  changeCurrentThread(id: string): protocol.UserChatHistory {
    let thread = threadManager.getThread(id);
    if (thread) {
      this.currentThread = thread;
    } else {
      // This should never really happen, but just in case.
      this.openNewThread();
      this.currentThread.id = id;
    }
    return this.currentThread.history.user;
  }

  async saveThread(thread: ChatThread) {
    thread.isSaving = true;
    const threads = this.context.globalState.get<protocol.ChatThreadSummary[]>(
      CHAT_THREAD_LIST_KEY,
      []
    );

    let payload: protocol.ChatThread;
    if (!threads.some(t => t.id === thread.id)) {
      threads.push({
        id: thread.id,
        uuid: thread.uuid,
        title: thread.title,
        areActionsEnabled: thread.areActionsEnabled,
        lastEditTimestamp: thread.lastEditTimestamp,
      });
      await this.context.globalState.update(CHAT_THREAD_LIST_KEY, threads);
      payload = thread.toReadOnly;
    } else {
      const existingThread = threads.find(t => t.id === thread.id)!;
      payload = {
        id: existingThread.id, // override with old id
        uuid: existingThread.uuid, // overrivde with old uuid
        title: existingThread.title, // override with old title
        areActionsEnabled: existingThread.areActionsEnabled ?? false,
        lastEditTimestamp: thread.lastEditTimestamp,
        history: thread.history.user,
        suggestedNextPrompt: thread.suggestedNextPrompt,
        internalHistory: thread.history.internal,
      };
    }

    await this.context.globalState.update(`${CHAT_THREAD_DETAIL_PREFIX}.${thread.id}`, payload);
    thread.isSaved = true;
    thread.isSaving = false;
  }

  async saveCurrentThread(generateTitle: () => Promise<string | undefined>) {
    this.currentThread.isSaving = true;
    if (!this.currentThread.isSaved) {
      const now = new Date();
      const lastEditTimestamp = now.getTime();
      const generatedTitle = await generateTitle();
      this.currentThread.id = lastEditTimestamp.toString();
      this.currentThread.title = generatedTitle ?? now.toLocaleString();
      this.currentThread.lastEditTimestamp = lastEditTimestamp;
    } else {
      this.currentThread.lastEditTimestamp = Date.now();
    }
    await this.saveThread(this.currentThread);
    this.currentThread.isSaving = false;
  }

  getThread(id: string): ChatThread | undefined {
    const retrieved = this.context.globalState.get<protocol.ChatThread>(
      `${CHAT_THREAD_DETAIL_PREFIX}.${id}`
    );
    return retrieved ? ChatThread.fromReadOnly(retrieved) : undefined;
  }

  openNewThread(actionsInChatAreEnabled?: boolean) {
    let id: string;
    let enableActionsInChat: boolean;
    if (actionsInChatAreEnabled === undefined) {
      id = this.actionsInChatAreDefault ? protocol.newThreadWithActionsID : protocol.newThreadID;
      enableActionsInChat = this.actionsInChatAreDefault;
    } else if (actionsInChatAreEnabled) {
      id = protocol.newThreadWithActionsID;
      enableActionsInChat = true;
    } else {
      id = protocol.newThreadID;
      enableActionsInChat = false;
    }
    this.currentThread = ChatThread.defaultNew(id, enableActionsInChat);
  }
}

export let threadManager: ThreadManager;
export function initializeThreadManager(
  context: ExtensionContext,
  actionsInChatAreDefault: boolean
) {
  threadManager = new ThreadManager(context, actionsInChatAreDefault);
}
