import {ChatHistory} from '../chat-history';
import * as api from './api';
import {LLM, LiveLLM} from './engines/llm';
import {ExtensionContext} from '../context';
import {FunctionManager} from './function-manager';
import {ReadFileFunction} from './functions/read-file';
import {WriteFileFunction} from './functions/write-file';
import {RunTerminalCommandFunction} from './functions/run-terminal-command';
import {DeleteFileFunction} from './functions/delete-file';
import {WebSearchFunction} from './functions/web-search';
import {ListProjectFilesFunction} from './functions/list-project-files';
import {ExternalFunction, FunctionState} from './functions/external-function';
import {ChatStatus, FunctionAction} from '~common/messaging/modules/aichat/aichat-protocol';
import * as protocol from '~common/messaging/modules/aichat/aichat-protocol';
import {isRetrievalEnabled, retrieveSnippets} from '../retrieval';
import {logActionRejected, logFunctionActionTaken} from '../telemetry';
import {ExtensionBrokerImpl} from '../messaging/extension-broker';
import {INTERACTIVE_PROMPT, QUESTION_PROMPT} from '../prompt';
import {FileLogger, LogLevel} from '../logger';
import {PlaybackLLM} from './engines/playback-llm';
import {outsideOfIDX} from '../aichat';
import {ApiCallHelper} from './api-call-helper';
import {IdxAgentSettings} from './idx-agent-settings';
import {fileExists, readFile} from '../file-utils';
import * as scripts from './scripts';
import * as conversation_utils from './conversation-utils';
import {TaskManager} from './task-manager';
import {Retrieval} from './engines/retrieval';
import {getCurrentUserInfo} from '../user-info';
import {SystemInterface} from './system-interface';

export interface AidaCredentials {
  projectId: string | undefined;
  token: string | undefined;
}

// TODO(joshualitt): For now, we default this to `true`, and we read the system
// instruction from the local disk. However, this seems risky so we should
// reevaluate this before launch.
const useSystemInstructionFile = true; // outsideOfIDX;

// TODO(joshualitt): Rename this to `IdxAgent` and make `ConversationalAgent` abstract.
export class ConversationalAgent {
  llm: LLM;
  functionManager: FunctionManager;
  fileLogger: FileLogger;
  lastKnownGoodPoint: number = 0;
  systemInstruction: string | undefined;
  taskManager: TaskManager;
  retrieval: Retrieval;

  constructor(
    private readonly isDoNotTrackRegion: boolean,
    public idxAgentSettings: IdxAgentSettings,
    public systemInterface: SystemInterface,
    public broker: ExtensionBrokerImpl,
    public getAidaCredentials: () => Promise<AidaCredentials | undefined>
  ) {
    this.llm = this.idxAgentSettings.usePlaybackLlm
      ? new PlaybackLLM()
      : new LiveLLM(this.idxAgentSettings);
    const teeName = this.idxAgentSettings.fileLogToChannel ? 'actions-in-chat' : undefined;
    const filePath = outsideOfIDX ? '/tmp/idx/actions.log' : '/home/user/.idx/logs/actions.log';
    this.fileLogger = new FileLogger(filePath, LogLevel.INFO, teeName);
    this.taskManager = new TaskManager();
    this.retrieval = new Retrieval(
      this.idxAgentSettings.ragSelfAwarenessApiKey,
      this.idxAgentSettings.ragSelfAwarenessCorpusId
    );
    this.functionManager = this.initializeDefaultFunctionManager();
  }

  notifyChatHistory(chatHistory: ChatHistory) {
    this.broker.send('notifyChatHistory', chatHistory.user);
  }

  notifyPreparingResponse() {
    this.broker.send('notifyChatStatus', ChatStatus.PreparingResponse);
  }

  notifyReady() {
    this.broker.send('notifyChatStatus', ChatStatus.Ready);
  }

  initializeDefaultFunctionManager(): FunctionManager {
    const functions: ExternalFunction[] = [
      new ReadFileFunction(this.systemInterface),
      new WriteFileFunction(this.systemInterface),
      new DeleteFileFunction(this.systemInterface),
      new RunTerminalCommandFunction(this.systemInterface, this.taskManager),
      new ListProjectFilesFunction(this.systemInterface),
    ];
    if (this.idxAgentSettings.webSearchIsEnabled) {
      functions.push(new WebSearchFunction(this.idxAgentSettings, this.systemInterface, this.llm));
    }
    return new FunctionManager(functions);
  }

  async allow(history: ChatHistory, action: FunctionAction, abortSignal?: AbortSignal) {
    const functionState = history.lastFunctionState!;
    if (action === 'allow') {
      this.notifyPreparingResponse();
      await this.allowLastFunctionCall(history, functionState);
      await this.makeRemoteCall(
        history,
        undefined,
        functionState,
        await this.getAidaCredentials(),
        abortSignal
      );
      this.notifyReady();
    } else {
      await this.functionManager.performAction(functionState, action);
      logFunctionActionTaken(history.lastUserMessage?.rpcGlobalId, action, functionState);
    }
  }

  processContextualSlashCommands(
    history: ChatHistory,
    slashCommand: string | undefined,
    prompt: string
  ) {
    const interactiveCommand = INTERACTIVE_PROMPT.command;
    const interactiveSlashCommand = `/${interactiveCommand} `;
    const questionCommand = QUESTION_PROMPT.command;
    const questionSlashCommand = `/${questionCommand} `;

    // Remove contextual slash commands if present.
    let promptWithoutSlashCommands = prompt;
    let allowTools: boolean = this.idxAgentSettings.alwaysAllowTools!;
    if (slashCommand === interactiveCommand) {
      promptWithoutSlashCommands = prompt.slice(interactiveSlashCommand.length);
      allowTools = true;
    } else if (slashCommand === questionCommand) {
      promptWithoutSlashCommands = prompt.slice(questionSlashCommand.length);
      allowTools = false;
    }

    history.allowTools = allowTools;
    return promptWithoutSlashCommands;
  }

  async initializeSystemInstruction() {
    const systemInstructionFile = this.idxAgentSettings.systemInstructionFile;
    if (useSystemInstructionFile && (await fileExists(systemInstructionFile))) {
      this.systemInstruction = await readFile(systemInstructionFile);
    } else {
      this.systemInstruction = `\
You are a conversational coding assistant in an IDE. You have a couple of functions \
you can call to interact with the IDE to assist users. Answer simple question directly.\
`;
    }
  }

  async userPrompt(
    ctx: ExtensionContext,
    history: ChatHistory,
    slashCommand: string | undefined,
    originalPrompt: string,
    abortSignal?: AbortSignal
  ) {
    const aidaCredentials = await this.getAidaCredentials();
    const promptWithoutSlashCommand = this.processContextualSlashCommands(
      history,
      slashCommand,
      originalPrompt
    );
    this.notifyPreparingResponse();
    if (this.idxAgentSettings.urlIsAida && this.systemInstruction === undefined) {
      await this.initializeSystemInstruction();
    }
    if (history.isEmpty) {
      // TODO(joshualitt): For both AIDA and Gen AI, we may need some way to deal with model
      // fixation on web search when it is enabled, for example, by repeatedly asking the model to
      // prefer replying directly.
      if (this.idxAgentSettings.urlIsAida) {
        await scripts.initializeAida(history);
      } else {
        scripts.initializeGenAi(history);
      }
    }

    const useLocalRAG = this.idxAgentSettings.useRetrieval && isRetrievalEnabled();
    const useSelfAwarenessRAG = this.idxAgentSettings.ragSelfAwareness;
    const historyMark = history.internal.length;
    let userPrompt = promptWithoutSlashCommand;
    const facts: api.Fact[] = [];
    if (useLocalRAG) {
      const retrievedSnippets = await retrieveSnippets(
        ctx,
        userPrompt,
        true,
        this.idxAgentSettings.ragMaxDistance,
        this.idxAgentSettings.ragMaxResults
      );
      const activeEditorString = retrievedSnippets.activeEditorSnippets.join('\n');
      if (this.idxAgentSettings.urlIsAida) {
        for (let relevantSnippet of retrievedSnippets.relevantSnippets ?? []) {
          facts.push({text: relevantSnippet});
        }
        facts.push({text: activeEditorString});
      } else {
        userPrompt = `\
${userPrompt}
The following snippets may be helpful:
${retrievedSnippets.relevantSnippets?.join('\n\n')}
${activeEditorString}`;
      }
    }

    if (useSelfAwarenessRAG) {
      const retrievedSnippets = await this.retrieval.retrieve(userPrompt);
      if (this.idxAgentSettings.urlIsAida) {
        for (let item of retrievedSnippets.items ?? []) {
          facts.push({text: item.snippet});
        }
      } else {
        userPrompt = `\
${userPrompt}
The following snippets may be helpful:
${retrievedSnippets.items.map(item => item.snippet).join('\n\n')}`;
      }
    }

    history.pushUserMessage({
      speaker: 'user',
      userInfo: await getCurrentUserInfo(),
      body: originalPrompt,
    });
    const functionState = history.lastFunctionState;
    const rejectingFunctionCall = functionState && functionState.stateIs('requested');
    if (rejectingFunctionCall) {
      this.rejectLastFunctionCall(history, functionState, userPrompt);
    } else {
      // If the model encounters an unrecoverable error, we rollback the internal
      // history. But to where? We want to rollback to the last known good
      // point(LKGP) in the conversation. Because of how the model and user
      // interact, this turns out to be subtle.
      //
      // If the model and user are just interacting via textual messages, then
      // the LKGP will be right before the user's message that triggered the
      // error, this will allow the user to continue the conversation from a
      // safe place.
      //
      // However, function calls are more complicated. If the model returns a
      // function call, and the function response triggers an error, then we
      // need to roll back to before the user request that generated the
      // function call that generated the function response that triggered
      // the error.
      //
      // For chains of function calls that don't require permission,
      // unfortunately we have to go back all the way to before the user request
      // that generated the entire chain of function calls.
      //
      // With rejections, we can make a choice. We could go back to the
      // function which the user rejected, and allow the user to reject again.
      // However, for now it seems cleaner to roll back all the way past the
      // user request which generated the function call which the user then
      // rejected.
      this.lastKnownGoodPoint = history.internal.length;
      history.pushInternalMessage(conversation_utils.userText(userPrompt));
    }
    await this.makeRemoteCall(history, facts, undefined, aidaCredentials, abortSignal);

    // Restore original prompt if we were using RAG.
    // Note: if the call does not succeed, then the history will be popped to before the
    // historyMark, thus we have to check for this case below.
    if ((useLocalRAG || useSelfAwarenessRAG) && history.internal.length > historyMark) {
      history.internal[historyMark].parts[0].text = promptWithoutSlashCommand;
    }
    this.notifyReady();
  }

  async functionCallToUserMessage(
    history: ChatHistory,
    functionState: FunctionState | undefined,
    functionCall: api.FunctionCall,
    rpcGlobalId: string | undefined
  ) {
    // We update arguments in place when using the the Gen AI function calling API to work around
    // an issue with escapes in strings.
    if (!this.idxAgentSettings.urlIsAida) {
      this.functionManager.updateFunctionArgs(functionCall);
    }

    // We only break a function thread when a model returns a textual response.
    const requiresPermission = this.functionManager.requiresPermission(functionCall);
    if (functionState === undefined) {
      const functionFrame: protocol.FunctionFrame = {
        state: 'requested',
        call: functionCall,
        requiresPermission,
      };
      await this.functionManager.updateFunctionMetadata(functionFrame);
      history.pushUserMessage({
        speaker: 'ai',
        functionThreadState: {
          frames: [functionFrame],
          framesWhichRequirePermission: requiresPermission ? [functionFrame] : undefined,
        },
        rpcGlobalId: rpcGlobalId,
      });
    } else {
      history.lastUserMessage!.rpcGlobalId = rpcGlobalId;
      const functionFrame = functionState.addCall(functionCall, requiresPermission);
      await this.functionManager.updateFunctionMetadata(functionFrame);
    }
  }

  async handleValidResponse(
    history: ChatHistory,
    functionState: FunctionState | undefined,
    response: api.GenerateContentResponse,
    isChunk: boolean
  ) {
    const rpcGlobalId = response.responseMetadata?.rpcGlobalId;
    const message = response.candidates[0].content;
    const externalPart = message.parts[0];
    const functionCall = externalPart.functionCall;
    if (functionCall) {
      await this.functionCallToUserMessage(history, functionState, functionCall, rpcGlobalId);
    } else {
      const text = externalPart.text!;
      // Only textual messages support streaming currently.
      if (functionState) {
        if (isChunk && functionState.followup !== undefined) {
          functionState.setFollowup(`${functionState.followup}${text}`);
        } else {
          functionState.setFollowup(text);
        }
        history.lastUserMessage!.rpcGlobalId = rpcGlobalId;
      } else if (isChunk && history.lastUserMessage!.speaker === 'ai') {
        history.lastUserMessage!.body += text;
      } else {
        history.pushUserMessage({
          speaker: 'ai',
          body: text,
          usedRag: externalPart.usedRag,
          rpcGlobalId: rpcGlobalId,
        });
      }
    }

    if (isChunk && history.lastInternalMessage?.role === 'model') {
      history.lastInternalMessage!.parts[0].text += externalPart.text!;
    } else {
      history.pushInternalMessage(message);
    }
    api.debugLogger('INTERNAL HISTORY', history.internal);
    api.debugLogger('USER HISTORY', history.user);
    this.notifyChatHistory(history);
  }

  async makeRemoteCall(
    history: ChatHistory,
    facts?: api.Fact[],
    functionState?: FunctionState,
    aidaCredentials?: AidaCredentials,
    abortSignal?: AbortSignal
  ) {
    await new ApiCallHelper(
      this.idxAgentSettings,
      this.isDoNotTrackRegion,
      this.fileLogger,
      this.functionManager,
      this.llm,
      this.systemInstruction,
      facts,
      history,
      functionState,
      this.lastKnownGoodPoint,
      aidaCredentials,
      async (functionState, response, isChunk) =>
        await this.handleValidResponse(history, functionState, response, isChunk),
      () => this.notifyChatHistory(history),
      functionState => this.allowLastFunctionCall(history, functionState),
      abortSignal
    ).wrappedCallLoop();
  }

  async allowLastFunctionCall(history: ChatHistory, functionState: FunctionState) {
    // Guard the call to prevent multiple requests for the same function.
    if (functionState.stateIs('requested')) {
      functionState.executing();
      this.notifyChatHistory(history);
      await this.functionManager.callFunction(functionState, history.lastUserMessage?.rpcGlobalId);
      history.pushInternalMessage(
        conversation_utils.functionResponseToContent(functionState.response)
      );
    }
  }

  rejectLastFunctionCall(history: ChatHistory, functionState: FunctionState, reason: string) {
    functionState.reject();
    this.notifyChatHistory(history);
    scripts.rejectFunctionCall(history, functionState.response, reason);
    logActionRejected(history.lastUserMessage?.rpcGlobalId, functionState);
  }
}
