export interface BaseSchema {
  type: 'number' | 'integer' | 'string' | 'boolean' | 'array' | 'object';
  description?: string;
  nullable?: boolean;
}

export interface NumberSchema extends BaseSchema {
  type: 'number';
  format?: 'float' | 'double';
}

export interface IntegerSchema extends BaseSchema {
  type: 'integer';
  format?: 'int32' | 'int64';
}

export interface StringSchema extends BaseSchema {
  type: 'string';
}

export interface EnumSchema extends BaseSchema {
  type: 'string';
  format: 'enum';
  enum: string[];
}

export interface BooleanSchema extends BaseSchema {
  type: 'boolean';
}

export interface ArraySchema extends BaseSchema {
  type: 'array';
  items?: ValueSchema;
}

export interface ObjectSchema extends BaseSchema {
  type: 'object';
  properties?: Record<string, ValueSchema>;
  required?: readonly string[];
}

export type ValueSchema =
  | NumberSchema
  | IntegerSchema
  | StringSchema
  | EnumSchema
  | BooleanSchema
  | ArraySchema
  | ObjectSchema;

export type FunctionStatusEnum = 'success' | 'failure' | 'detached';

export interface FunctionResult {
  status: FunctionStatusEnum;
  result: string;
}

export type FunctionName =
  | 'run_terminal_command'
  | 'write_file'
  | 'read_file'
  | 'delete_file'
  | 'web_search'
  | 'change_editor_theme'
  | 'list_project_files';

export interface FunctionDeclaration {
  name: FunctionName;
  description: string;
  parameters?: ValueSchema;
}

export type FunctionCallArgs = {[key: string]: any};

export interface FunctionCall {
  name: FunctionName;
  args: FunctionCallArgs;
}

export interface FunctionResponse {
  name: FunctionName;
  response: FunctionResult;
}

export interface Part {
  text?: string;
  functionCall?: FunctionCall;
  functionResponse?: FunctionResponse;
  // TODO(joshualitt): This isn't correct and should be removed.
  usedRag?: boolean;
}

export interface Content {
  role: 'user' | 'model' | 'system';
  parts: Part[];
}

export interface Tool {
  functionDeclarations: FunctionDeclaration[];
}

export interface RequestMetadata {
  projectId: string;
  disableContentLogging: boolean;
}

export interface Fact {
  text: string;
}

export interface GenerateContentRequest {
  generationConfig: GenerationConfig;
  contents: Content[];
  tools?: Tool[];
  requestMetadata?: RequestMetadata;
  systemInstruction?: Content;
  facts?: Fact[];
}

export interface Candidate {
  content: Content;
}

export interface ResponseMetadata {
  rpcGlobalId: string;
}

export interface GenerateContentResponse {
  candidates: Candidate[];
  responseMetadata?: ResponseMetadata;
}

export interface GenerationConfig {
  maxOutputTokens?: number;
  topP?: number;
  topK?: number;
  temperature?: number;
  candidateCount?: number;
  stopSequences?: string[];
}

// Strings from the function calling API will have been escaped. Unfortunately,
// we can't just use JSON.parse, because we appear to be getting "\'" escape
// characters which are not valid in JSON. For the time being, we handle
// the common escape characters, but we ignore "\xFF" because:
//   1) It is fairly unlikely to appear in generated strings.
//   2) Handling it would require more logic.
//   3) This is hopefully just a temporary workaround anyways.
export function stringWorkaround(input: string): string {
  let previous = '';
  let current = input;
  while (current !== previous) {
    previous = current;
    current = current.replace(/\\(.)/g, (match, p1) => {
      switch (p1) {
        case 'n':
          return '\n';
        case 'r':
          return '\r';
        case 't':
          return '\t';
        case 'b':
          return '\b';
        case 'f':
          return '\f';
        case '"':
          return '"';
        case "'":
          return "'";
        case '0':
          return '\0';
        case 'v':
          return '\v';
        case '\\':
          return '\\';
        default:
          console.log(`Unrecognized escape sequence: ${match}`);
          return match;
      }
    });
  }
  return current;
}

export const debugLogger = (...logs: unknown[]) => {
  for (const log of logs) {
    if (log instanceof Error) {
      console.error('[superchat]', ...logs);
      return;
    }
  }

  console.log('[superchat]', ...logs);
};

export enum ValidationResultStatus {
  Failure,
  Success,
}

export interface ValidationResult {
  status: ValidationResultStatus;
}

export interface ValidationResultFailure extends ValidationResult {
  corrections: string[];
}

export interface ValidationResultSuccess extends ValidationResult {}

export function validationFailures(corrections: string[]): ValidationResultFailure {
  return {status: ValidationResultStatus.Failure, corrections: corrections};
}

export function validationFailure(correction: string): ValidationResultFailure {
  return validationFailures([correction]);
}

export function validationSuccess(): ValidationResultSuccess {
  return {status: ValidationResultStatus.Success};
}
