export function mergeWithExistingCodeBlockIfPresent(
  text: string,
  code: string,
  citationStr?: string
): string {
  citationStr = citationStr ? '\n' + citationStr + '\n' : '';
  const CODE_END = '\n~~~ \n';
  const CODE_START = '\n~~~\n';

  //If the current text is ending with a code block merge current code with previous
  if (text.endsWith(CODE_END)) {
    text = text.slice(0, -1 * CODE_END.length);
    text = text + citationStr + code + CODE_END;
  } else {
    text = text + CODE_START + citationStr + code + CODE_END;
  }
  return text;
}

export async function sleep(timeMs: number) {
  await new Promise<void>(resolve => setTimeout(() => resolve(), timeMs));
}

export function randomInt(max: number): number {
  return Math.floor(Math.random() * max);
}
