import * as vscode from 'vscode';

export function getErrorList(request: vscode.InteractiveEditorRequest): string | undefined {
  if (!vscode.window.activeTextEditor?.document) {
    return undefined;
  }
  let targetRange = wholeLines(!request.selection.isEmpty ? request.selection : request.wholeRange);
  let errors: string[] = [];
  if (vscode.window.activeTextEditor?.document?.uri && !targetRange.isEmpty) {
    let diagnostics = vscode.languages
      .getDiagnostics(vscode.window.activeTextEditor?.document?.uri)
      .filter(d => !d.range.intersection(targetRange)?.isEmpty);
    errors = diagnostics.length ? [diagnostics[0].message] : [];
  }
  return errors.map(e => `- ${e}`).join('\n');
}

export function hasErrorsForRange(
  range: vscode.Range,
  activeEditor = vscode.window.activeTextEditor
): boolean {
  if (activeEditor?.document?.uri && !range.isEmpty) {
    let diagnostics = vscode.languages
      .getDiagnostics(activeEditor?.document?.uri)
      .filter(d => !d.range.intersection(range)?.isEmpty);
    return diagnostics.length > 0;
  }
  return false;
}

export function errorsForRange(
  range: vscode.Range,
  activeEditor = vscode.window.activeTextEditor
): string | undefined {
  if (!activeEditor?.document.uri || range.isEmpty) {
    return '';
  }
  let diagnostics = vscode.languages
    .getDiagnostics(activeEditor.document.uri)
    .filter(d => !d.range.intersection(range)?.isEmpty);
  const errors = diagnostics.length ? [diagnostics[0].message] : [];
  return errors.map(e => `- ${e}`).join('\n');
}

function wholeLines(range: vscode.Range): vscode.Range {
  return new vscode.Range(range.start.line, 0, range.end.line, 9999);
}
