import * as vscode from 'vscode';
import {readFile as fsReadFile} from 'fs';
import * as path from 'path';
import {TextEncoder, TextDecoder} from 'util';

export function relativePathToWorkspaceUri(relativePath: string): vscode.Uri | undefined {
  if (vscode.workspace.workspaceFolders && vscode.workspace.workspaceFolders.length === 1) {
    const root = vscode.workspace.workspaceFolders[0].uri;
    return vscode.Uri.joinPath(root, relativePath);
  }
  return undefined;
}

export async function fileTypeUriExists(
  uri: vscode.Uri,
  fileType: vscode.FileType
): Promise<boolean> {
  try {
    const stat = await vscode.workspace.fs.stat(uri);
    return stat.type === fileType;
  } catch {}
  return false;
}

export async function fileUriExists(uri: vscode.Uri): Promise<boolean> {
  return fileTypeUriExists(uri, vscode.FileType.File);
}

export async function folderUriExists(uri: vscode.Uri): Promise<boolean> {
  return fileTypeUriExists(uri, vscode.FileType.Directory);
}

export async function fileTypeExists(
  relativePath: string,
  fileType: vscode.FileType
): Promise<boolean> {
  let workspaceUri = relativePathToWorkspaceUri(relativePath);
  if (workspaceUri) {
    return fileTypeUriExists(workspaceUri, fileType);
  }
  return false;
}

export async function fileExists(relativePath: string): Promise<boolean> {
  return fileTypeExists(relativePath, vscode.FileType.File);
}

export async function folderExists(relativePath: string): Promise<boolean> {
  return fileTypeExists(relativePath, vscode.FileType.Directory);
}

export async function readFile(relativePath: string): Promise<string> {
  let workspaceUri = relativePathToWorkspaceUri(relativePath);
  if (workspaceUri) {
    const decoder = new TextDecoder();
    const fileContent = await vscode.workspace.fs.readFile(workspaceUri);
    return decoder.decode(fileContent);
  }

  return 'Error: File Not Found';
}

export async function deleteFile(relativePath: string): Promise<void> {
  let workspaceUri = relativePathToWorkspaceUri(relativePath);
  if (workspaceUri) {
    await vscode.workspace.fs.delete(workspaceUri);
  }
}

export async function writeFile(relativePath: string, content: string): Promise<void> {
  let workspaceUri = relativePathToWorkspaceUri(relativePath);
  if (workspaceUri) {
    const encoder = new TextEncoder();
    console.log(`Writing content to file: ${workspaceUri}, content is : ${content}`);
    await vscode.workspace.fs.writeFile(workspaceUri, encoder.encode(''));
    console.log(`opening file: ${workspaceUri}`);
    await openDocument(relativePath);
    await vscode.workspace.fs.writeFile(workspaceUri, encoder.encode(content));
    await openDocument(relativePath);
    await vscode.commands.executeCommand('workbench.action.files.revert');
  }
}

export async function openDocument(relativePath: string): Promise<void> {
  const f = resolve(relativePath);
  if (!f) {
    throw new Error(`File: ${path} not found`);
  }
  console.log('opening text document:' + f);
  const document = await vscode.workspace.openTextDocument(f);
  await vscode.window.showTextDocument(document, vscode.ViewColumn.One);
}

export function resolve(file: string | undefined): string | undefined {
  const workspace = vscode.workspace;
  //See if we can open main.dart in the current workspace folder
  if (workspace.workspaceFolders) {
    if (workspace.workspaceFolders.length === 1) {
      const directory = workspace.workspaceFolders[0].uri.fsPath;
      if (file) {
        return path.resolve(directory, file);
      } else {
        return directory;
      }
    }
  }
  return undefined;
}

async function collectFilesRecursive(
  rootUri: vscode.Uri,
  relativePath: string,
  collectedFiles: string[],
  ignore: Set<string>
): Promise<void> {
  const configPath = vscode.Uri.joinPath(rootUri, relativePath);
  for (const entry of await vscode.workspace.fs.readDirectory(configPath)) {
    const name = entry[0];
    // Ignore anything that starts with a '.', in addition to anything we are explicitly told to
    // ignore.
    if (ignore.has(name) || name.startsWith('.')) {
      continue;
    }
    const type = entry[1];
    const path = `${relativePath}/${name}`;
    const isFile = (type & vscode.FileType.File) !== 0;
    const isDirectory = (type & vscode.FileType.Directory) !== 0;
    if (isFile) {
      collectedFiles.push(path);
    } else if (isDirectory) {
      await collectFilesRecursive(rootUri, path, collectedFiles, ignore);
    }
  }
}

export async function listFilesRecursive(
  relativePath: string,
  ignore: Set<string>
): Promise<string[]> {
  if (vscode.workspace.workspaceFolders && vscode.workspace.workspaceFolders.length === 1) {
    const root = vscode.workspace.workspaceFolders[0].uri;
    const files: string[] = [];
    await collectFilesRecursive(root, relativePath, files, ignore);
    return files;
  }
  return [];
}

export function readFileAsString(path: string): Promise<string> {
  return new Promise((resolve, reject) => {
    fsReadFile(path, 'utf8', (err, data) => {
      if (err) {
        reject(err);
      } else {
        resolve(data);
      }
    });
  });
}
