import * as api from '../api';
import {ExternalFunction, FunctionState} from './external-function';
import {SystemInterface} from '../system-interface';

export class ReadFileFunction extends ExternalFunction {
  name = 'read_file' as const;
  description = 'Reads from a file.';
  parameters: api.ObjectSchema = {
    type: 'object',
    properties: {
      path: {
        type: 'string',
        description: 'The path of the file to be read. This should be a relative path.',
      },
    },
    required: ['path'],
  };
  requiresPermission = false;

  constructor(public systemInterface: SystemInterface) {
    super();
  }

  // TODO: this needs to respect .aiexclude rules
  async call(functionState: FunctionState): Promise<void> {
    const path = functionState.args.path;
    if (await this.systemInterface.fileExists(path)) {
      const fileContent = await this.systemInterface.readFile(path);
      functionState.success(fileContent);
      functionState.readFile(path);
    } else {
      functionState.failure(`File ${path} does not exist.`);
    }
  }
}
