import {ExtensionContext} from './context';

export class FeatureFlags {
  constructor(private ctx: ExtensionContext) {}

  async isEarlyAccessCommandsEnabled() {
    return this.isEnabled('early_access_commands_enabled');
  }

  async isSlashCommandsEnabled() {
    return this.isEnabled('slash_commands_enabled');
  }

  async isActionsInChatEnabled() {
    return this.isEnabled('actions_in_chat_enabled');
  }

  async isDoNotTrackRegion() {
    return this.isEnabled('do_not_track_region');
  }

  private async isEnabled(name: string) {
    const {idx, logger} = this.ctx;
    if (!idx) {
      return false;
    }
    let isEnabled: boolean | undefined;
    try {
      isEnabled = await idx?.exports.isFeatureEnabled(name);
    } catch (e) {
      logger.error(`check to see if ${name} is enabled failed: ${e}`);
      return false;
    }
    logger.info(`${name}: ${isEnabled}`);
    return isEnabled;
  }
}
