import * as vscode from 'vscode';
import {readFile, readdir} from 'fs/promises';
import path from 'path';
import {HydratedPromptMetadata} from 'prompt-engine';

import {ExtensionContext} from './context';
import {PromptManager} from './prompt-manager';

export function setupPromptProvider(ctx: ExtensionContext) {
  const provider = new PromptProvider(ctx);
  ctx.vsc.subscriptions.push(provider);
  ctx.prompts = provider;
}

