/**
 * The PromptProvider class is responsible for managing the prompts that are available to the user.
 * It loads prompts from the global templates directory and from the current workspace directory.
 * Delegates most of the work to PromptManager which has no dependency on vscode
 */
export class PromptProvider implements vscode.Disposable {
  private disposables: vscode.Disposable[] = [];
  private manager: PromptManager;

  constructor(private ctx: ExtensionContext) {
    var that = this;
    const watcher = {
      async doesDirectoryExist(path: string): Promise<boolean> {
        try {
          // check if the directory exists.
          await vscode.workspace.fs.stat(vscode.Uri.file(path));
          return true;
        } catch {
          return false;
        }
      },

      watch(path: string, pattern: string, onChange: () => Promise<void>): void {
        that.disposables.push(
          vscode.workspace
            .createFileSystemWatcher(new vscode.RelativePattern(path, pattern))
            .onDidChange(async () => {
              onChange();
            })
        );
      },
    };
    this.manager = new PromptManager(ctx.logger, ctx.engine, watcher);
    try {
      this.manager.init(
        this.ctx.vsc.extensionUri.fsPath,
        vscode.workspace.workspaceFolders?.[0].uri.fsPath,
        true,
        this.ctx.features,
      );
    } catch (e) {
      this.ctx.logger.error(`error initializing prompt manager: ${e}`);
    }
  }

  dispose() {
    for (const w of this.disposables) {
      w.dispose();
    }
  }

  byCommand(command: string, source: PromptAvailability) {
    return this.manager.byCommand(command, source);
  }

  nameThreadPrompt() {
    return this.manager.nameThreadPrompt();
  }

  suggestNextPromptPrompt() {
    return this.manager.suggestNextPromptPrompt();
  }

  preamble() {
    return this.manager.preamble();
  }

  facts() {
    return this.manager.facts();
  }

  selfawareFacts() {
    return this.manager.selfawareFacts();
  }

  explainSelection() {
    return this.manager.explainSelection();
  }

  chatEdit() {
    return this.manager.chatEdit();
  }

  chatAddComments() {
    return this.manager.chatAddComments();
  }

  sidekickPrompts(hasErrors = false, hasSelection = false) {
    return this.manager.sidekickPrompts(hasErrors, hasSelection);
  }

  chatPrompts(hasErrors = false, hasSelection = false) {
    return this.manager.chatPrompts(hasErrors, hasSelection);
  }

  devtoolsDebug(){
    return this.manager.devtoolsDebug();
  }

  /**
   * sidekickPrompt matches a prompt for the given request, defaulting to the sidekick-create prompt
   * if a match is not found.
   */
  sidekickPrompt(request: vscode.InteractiveEditorRequest, hasSelection: boolean): Prompt {
    return this.manager.sidekickPrompt(request.prompt, hasSelection);
  }

  /**
   * execute executes a prompt with the given parameters using the engine from the context.
   */
  execute(prompt: Prompt, params: Params) {
    return this.manager.execute(prompt, params);
  }
}

export interface Prompt {
  name: string;
  content: string;
  description: string;
  command: string;
  availability: PromptAvailability[];
  action: PromptAction;
  stopSequences: string[];
  requiredContext: PromptContext[];
  meta: HydratedPromptMetadata;
}

export interface Params {
  currentLanguage?: string;
  openFilename?: string;
  openFileContent?: string;
  selectedCode?: string;
  errors?: string;
  prompt?: string;
  currentLine: number;
  [key: string]: any;
}

/**
 * PromptAvailability is where in the UI the prompt should be available
 * if it can be used as slash command.
 */
export const enum PromptAvailability {
  Inline = 'inline',
  Chat = 'chat',
}

/**
 * PromptAction describes how to handle the generated response to a prompt.
 */
export const enum PromptAction {
  /** Edit the current file with the generated code. */
  Edit = 'edit',
  /** Display the response in the AI chat window. */
  Chat = 'chat',
  /** Do nothing other than returning the result. */
  Raw = 'raw',
}

export const enum PromptContext {
  Error = 'error',
  Selection = 'selection',
  NoSelection = 'noSelection',
}
