import {implicitCasesScript} from './implicit-cases';
import {Script} from './scripts';
import {textMessage, Response, replyToUser} from './utils';

function convertImplicitCasesScript(): Response[] {
  const originalResponses = implicitCasesScript.responses;
  const convertedResponses = [];
  for (const response of originalResponses) {
    const responseText = response[0].candidates[0].content.parts[0].text;
    if (responseText) {
      convertedResponses.push(replyToUser(responseText));
    } else {
      convertedResponses.push(response);
    }
  }
  return convertedResponses;
}

export const replyToUserScript: Script = {
  name: 'reply to user',
  responses: convertImplicitCasesScript().concat([
    textMessage('testing mixed textual messages 1'),
    replyToUser('reply 1'),
    textMessage('testing mixed textual messages 2'),
    replyToUser('reply 2'),
  ]),
};
