import * as vscode from 'vscode';
import {
  CONFIG_AI_ACTIONS_IN_CHAT_DEFAULT,
  CONFIG_AI_ACTIONS_IN_CHAT_USE_RETRIEVAL,
  CONFIG_AI_ACTIONS_IN_CHAT_ALWAYS_ALLOW_TOOLS,
  CONFIG_AI_ACTIONS_IN_CHAT_CANDIDATE_RETRIES,
  CONFIG_AI_ACTIONS_IN_CHAT_FILE_LOG_TO_CHANNEL,
  CONFIG_AI_ACTIONS_IN_CHAT_USE_PLAYBACK_LLM,
  CONFIG_AI_ACTIONS_IN_CHAT_USE_STREAMING,
  CONFIG_AI_ACTIONS_IN_CHAT_URL,
  CONFIG_AI_ACTIONS_IN_CHAT_API_KEY,
  CONFIG_AI_ACTIONS_IN_CHAT_MAX_OUTPUT_TOKENS,
  CONFIG_AI_ACTIONS_IN_CHAT_MODEL,
  CONFIG_AI_ACTIONS_IN_CHAT_MIN_TEMPERATURE,
  CONFIG_AI_ACTIONS_IN_CHAT_MAX_TEMPERATURE,
  CONFIG_AI_ACTIONS_IN_CHAT_MIN_TOP_K,
  CONFIG_AI_ACTIONS_IN_CHAT_MAX_TOP_K,
  CONFIG_AI_ACTIONS_IN_CHAT_MIN_TOP_P,
  CONFIG_AI_ACTIONS_IN_CHAT_MAX_TOP_P,
  CONFIG_AI_ACTIONS_IN_CHAT_URL_IS_AIDA,
  CONFIG_AI_ACTIONS_IN_CHAT_AIDA_CANDIDATE_COUNT,
  CONFIG_AI_WEB_SEARCH_CX,
  CONFIG_AI_WEB_SEARCH_API_KEY,
  CONFIG_AI_ENABLE_WEB_SEARCH,
  CONFIG_AI_WEB_SEARCH_URL,
  CONFIG_AI_ACTIONS_IN_CHAT_INDEXSERVER_MAX_DISTANCE,
  CONFIG_AI_ACTIONS_IN_CHAT_INDEXSERVER_MAX_RESULTS,
  CONFIG_AI_ACTIONS_IN_CHAT_ENABLE_SELF_AWARENESS,
  CONFIG_AI_SLASH_IDX_RETRIEVAL_API_KEY,
  CONFIG_AI_SLASH_IDX_RETRIEVAL_CORPUS_ID,
} from 'vscode-shared/config';
import {getProperty} from './vscode-utils';
import {isRetrievalEnabled} from './retrieval';
import {IdxAgentSettings} from './agentive/idx-agent-settings';
import {SystemInterface} from './agentive/system-interface';
import {
  deleteFile,
  fileExists,
  listFilesRecursive,
  readFile,
  writeFile,
  folderExists,
} from './file-utils';

const defaultAidaCandidateRetries = 0;
const defaultCandidateRetries = 10;
const defaultLlmModel = 'gemini-pro';
const defaultLlmMaxOutputTokens = 4096;
const defaultLlmMinTemperature = 0;
const defaultLlmMaxTemperature = 0.9;
const defaultLlmMinTopK = 1;
const defaultLlmMaxTopK = 32;
const defaultLlmMinTopP = 0.1;
const defaultLlmMaxTopP = 1;
const defaultLlmCandidateCount = 1;
const defaultAidaCandidateCount = 1;
const defaultLlmUrl = 'https://generativelanguage.googleapis.com/v1beta';
const defaultWebSearchUrl = 'https://customsearch.googleapis.com/customsearch/v1';
const defaultAidaLlmUrl = 'https://monospace-pa.googleapis.com/v1/aida:GenerateContent';
const defaultSystemInstructionFile = '/home/user/.idx/prompts/actions-system-instruction.txt';
const defaultMaxRagDistance = 0.9;
const defaultMaxRagResults = 3;

export function initializeIdxAgentSettings(): IdxAgentSettings {
  // LLM specific settings.
  let url = getProperty<string>(CONFIG_AI_ACTIONS_IN_CHAT_URL, defaultLlmUrl);
  let urlIsAida = getProperty<boolean>(CONFIG_AI_ACTIONS_IN_CHAT_URL_IS_AIDA, false);
  const apiKey = getProperty<string>(CONFIG_AI_ACTIONS_IN_CHAT_API_KEY, '');
  const systemInstructionFile = getProperty<string>(
    CONFIG_AI_ACTIONS_IN_CHAT_API_KEY,
    defaultSystemInstructionFile
  );
  const llmModel = getProperty<string>(CONFIG_AI_ACTIONS_IN_CHAT_MODEL, defaultLlmModel);
  const llmMinTemperature = getProperty<number>(
    CONFIG_AI_ACTIONS_IN_CHAT_MIN_TEMPERATURE,
    defaultLlmMinTemperature
  );
  const llmMaxTemperature = getProperty<number>(
    CONFIG_AI_ACTIONS_IN_CHAT_MAX_TEMPERATURE,
    defaultLlmMaxTemperature
  );
  const llmMinTopK = getProperty<number>(CONFIG_AI_ACTIONS_IN_CHAT_MIN_TOP_K, defaultLlmMinTopK);
  const llmMaxTopK = getProperty<number>(CONFIG_AI_ACTIONS_IN_CHAT_MAX_TOP_K, defaultLlmMaxTopK);
  const llmMinTopP = getProperty<number>(CONFIG_AI_ACTIONS_IN_CHAT_MIN_TOP_P, defaultLlmMinTopP);
  const llmMaxTopP = getProperty<number>(CONFIG_AI_ACTIONS_IN_CHAT_MAX_TOP_P, defaultLlmMaxTopP);
  const llmMaxOutputTokens = getProperty<number>(
    CONFIG_AI_ACTIONS_IN_CHAT_MAX_OUTPUT_TOKENS,
    defaultLlmMaxOutputTokens
  );

  // If the user enables actions in chat, but doesn't supply an API key, then we assume they want
  // to use AIDA.
  if (apiKey === '') {
    if (url === defaultLlmUrl) {
      url = defaultAidaLlmUrl;
    }
    urlIsAida = true;
  }
  const candidateCount = getProperty<number>(
    CONFIG_AI_ACTIONS_IN_CHAT_AIDA_CANDIDATE_COUNT,
    urlIsAida ? defaultAidaCandidateCount : defaultLlmCandidateCount
  );
  const isDefault = getProperty<boolean>(CONFIG_AI_ACTIONS_IN_CHAT_DEFAULT, false);
  // If retrieval is enabled, then we assume the user wants to use it in Superchat.
  const useRetrieval = getProperty<boolean>(
    CONFIG_AI_ACTIONS_IN_CHAT_USE_RETRIEVAL,
    isRetrievalEnabled()
  );
  const alwaysAllowTools = getProperty<boolean>(CONFIG_AI_ACTIONS_IN_CHAT_ALWAYS_ALLOW_TOOLS, true);
  const candidateRetries = getProperty<number>(
    CONFIG_AI_ACTIONS_IN_CHAT_CANDIDATE_RETRIES,
    urlIsAida ? defaultAidaCandidateRetries : defaultCandidateRetries
  );
  const fileLogToChannel = getProperty<boolean>(
    CONFIG_AI_ACTIONS_IN_CHAT_FILE_LOG_TO_CHANNEL,
    false
  );
  const usePlaybackLlm = getProperty<boolean>(CONFIG_AI_ACTIONS_IN_CHAT_USE_PLAYBACK_LLM, false);
  const useStreaming = getProperty<boolean>(CONFIG_AI_ACTIONS_IN_CHAT_USE_STREAMING, false);

  // Web search related settings. These are a bit orthogonal, but for tidiness we include them in
  // the global settings.
  const webSearchIsEnabled = getProperty<boolean>(CONFIG_AI_ENABLE_WEB_SEARCH, false);
  const webSearchCx = getProperty<string>(CONFIG_AI_WEB_SEARCH_CX, '');
  const webSearchApiKey = getProperty<string>(CONFIG_AI_WEB_SEARCH_API_KEY, '');
  const webSearchUrl = getProperty<string>(CONFIG_AI_WEB_SEARCH_URL, defaultWebSearchUrl);

  // RAG settings.
  const ragMaxDistance = getProperty<number>(
    CONFIG_AI_ACTIONS_IN_CHAT_INDEXSERVER_MAX_DISTANCE,
    defaultMaxRagDistance
  );
  const ragMaxResults = getProperty<number>(
    CONFIG_AI_ACTIONS_IN_CHAT_INDEXSERVER_MAX_RESULTS,
    defaultMaxRagResults
  );
  const ragSelfAwareness = getProperty<boolean>(
    CONFIG_AI_ACTIONS_IN_CHAT_ENABLE_SELF_AWARENESS,
    // Defaulting to `false` until we've had more time to test this.
    false
  );
  const ragSelfAwarenessApiKey = getProperty<string>(CONFIG_AI_SLASH_IDX_RETRIEVAL_API_KEY, '');
  const ragSelfAwarenessCorpusId = getProperty<string>(CONFIG_AI_SLASH_IDX_RETRIEVAL_CORPUS_ID, '');
  return {
    url,
    urlIsAida,
    urlNeedsProjectId: url === defaultAidaLlmUrl,
    apiKey,
    systemInstructionFile,
    llmModel,
    llmMinTemperature,
    llmMaxTemperature,
    llmMinTopK,
    llmMaxTopK,
    llmMinTopP,
    llmMaxTopP,
    llmMaxOutputTokens,
    candidateCount,
    isDefault,
    useRetrieval,
    alwaysAllowTools,
    candidateRetries,
    fileLogToChannel,
    usePlaybackLlm,
    useStreaming,
    webSearchIsEnabled,
    webSearchCx,
    webSearchApiKey,
    webSearchUrl,
    ragMaxDistance,
    ragMaxResults,
    ragSelfAwareness,
    ragSelfAwarenessApiKey,
    ragSelfAwarenessCorpusId,
  };
}

export function initializeIdxAgentSystemInterface(): SystemInterface {
  return {
    updateConfiguration: async (property, value) =>
      vscode.workspace.getConfiguration().update(property, value),
    fileExists: fileExists,
    folderExists: folderExists,
    deleteFile: deleteFile,
    writeFile: writeFile,
    readFile: readFile,
    listFilesRecursive: listFilesRecursive,
  };
}
