import * as vscode from 'vscode';
import * as api from './agentive/api';
import {AIDA_TAB, monospaceextension} from './aichat';
import {FunctionAction, FunctionState} from './agentive/functions/external-function';
import {ChatMessage} from '~common/messaging/modules/aichat/aichat-protocol';
import {ExtensionContext} from './context';
import {CONFIG_AI_ENABLE_RETRIEVAL} from 'vscode-shared/config';
import {isRetrievalEnabled} from './retrieval';

export const enum AICHAT_EVENTS {
  // Has the option to open AI Chat.
  AICHAT_CAN_OPEN = 'aichat.canOpen',
  // Open the AI Chat Webview from *any* source.
  AICHAT_OPEN = 'aichat.open',
  // Open the AI Chat Webview from the status bar.
  AICHAT_OPEN_STATUSBAR = 'aichat.open.statusbar',
  // Open the AI Chat Webview from the hotkey.
  AICHAT_OPEN_HOTKEY = 'aichat.open.hotkey',
  AICHAT_NOT_AVAILABLE_IN_COUNTRY = 'aichat.notAvailableInCountry',
  // User opened license log
  AICHAT_OPEN_LICENSE_LOG = 'aichat.openLicenseLog',
  // TODO: No reference to this?
  AICHAT_PROMPT = 'aichat.prompt',
  // Stop generating
  AICHAT_STOP_GENERATING = 'aichat.stopGenerating',
  // Open AI chat actions menu (in the editor toolbar)
  AICHAT_ACTIONS_MENU = 'aichat.actionsMenu',
  // Trigger AI Chat explain code selection.
  AICHAT_EXPLAIN = 'aichat.explain',
  // Trigger AI Chat's do something with selection.
  AICHAT_DOSOMETHING = 'aichat.doSomething',
  // Trigger AI Chat to add comment to the code selected.
  AICHAT_COMMENT = 'aichat.commentSelection',
  // Trigger AI Chat with a new prompt.
  AICHAT_NEW_PROMPT = 'aichat.newPrompt',
  // Replace selection with code.
  AICHAT_INSERT_CODE = 'aichat.insertCode',
  // Open an external link inside chat.
  AICHAT_OPEN_LINK = 'aichat.openLink',
  // Clear the chat.
  // TODO: This is poorly named, should be merged with slash commands.
  AICHAT_COMMAND = 'aichat.command',
  AICHAT_THUMBS_UP = 'aichat.thumbsUp',
  AICHAT_THUMBS_DOWN = 'aichat.thumbsDown',
  // The detailed form after user hits thumbs up or down.
  AICHAT_THUMBS_UP_DETAIL = 'aichat.thumbsUp.detail',
  AICHAT_THUMBS_DOWN_DETAIL = 'aichat.thumbsDown.detail',
  // A prompt is submitted via AI Chat.
  // Can be 'aichat.newPrompt' or edited prompt.
  // TODO: Edited prompt should be tracked directly.
  AICHAT_INTERACTION = 'aichat.interaction',
  // Loads AI chat for the first time and onboarding needs to be shown.
  AICHAT_OPEN_ONBOARDING = 'aichat.openOnboarding',
  // Saves the onboarding settings for that workspace.
  AICHAT_COMPLETE_ONBOARDING = 'aichat.completeOnboarding',
  // Save inline completion settings from the onboarding UI.
  // The setting value is also stored in the event.
  AICHAT_SAVE_INLINE_COMPLETION_SETTING = 'aichat.saveInlineCompletionSetting',
  AICHAT_SAVE_RETRIEVAL_SETTING = 'aichat.saveRetrievalSetting',
  // Fires when AI chat loads and privacy notice needs to be shown.
  AICHAT_OPEN_PRIVACY_NOTICE = 'aichat.openPrivacyNotice',
  // Accept the privacy notice.
  AICHAT_ACCEPT_PRIVACY_NOTICE = 'aichat.acceptPrivacyNotice',
  // Opening settings from AI Chat's top toolbar.
  OPEN_SETTINGS = 'aichat.openSettings',
  // Deletes a chat thread.
  DELETE_THREAD = 'aichat.deleteThread',
  // User has taken an action from a requested function in the chat.
  SUPERCHAT_FUNCTION_ACTION_TAKEN = 'superchat.functionActionTaken',
  // Superchat function request was rejected.
  SUPERCHAT_FUNCTION_REJECTED = 'superchat.functionRejected',
  // Superchat function successfully ran.
  SUPERCHAT_FUNCTION_SUCCEEDED = 'superchat.functionSucceeded',
  // Superchat function failed.
  SUPERCHAT_FUNCTION_FAILED = 'superchat.functionFailed',
  // Superchat retried.
  SUPERCHAT_RETRY = 'superchat.retry',
  // Superchat retry failed, no details.
  SUPERCHAT_RETRY_FAILED = 'superchat.retryFailed',
  // Retrieval enablement setting was toggled.
  RAG_ENABLEMENT_SETTING_TOGGLED = 'rag.enablementSettingToggled',
  // RAG indexing serer started embedding documents.
  RAG_INDEXING_STARTED = 'rag.indexingStarted',
  // RAG indexing server finished embedding documents.
  RAG_INDEXING_FINISHED = 'rag.indexingFinished',
}

export const enum ChatSentimentType {
  SENTIMENT_UNSPECIFIED = 'unknown',
  POSITIVE = 'positive',
  NEGATIVE = 'negative',
}

export const enum ChatIssueType {
  ISSUE_TYPE_UNSPECIFIED = 'unknown',
  TOXIC = 'toxic',
  IRRELEVANT = 'irrelevant',
  TOO_VERBOSE = 'too verbose',
  WRONG_ANSWER = 'wrong answer',
}

const enum RAG_STATUS {
  ENABLED = 'enabled',
  DISABLED = 'disabled',
}

// TODO(joshualitt): To reduce the chance of mistakes, we should only log hardcoded messages
// / enums / non-string types.
export class TelemetryLogger {
  constructor(private monospaceExtension: vscode.Extension<any> | undefined) {}

  logTelemetryEvent(event: AICHAT_EVENTS, message?: string) {
    try {
      this.monospaceExtension?.exports.logTelemetryEvent(AIDA_TAB, event.toString(), message);
    } catch (e) {
      api.debugLogger(e);
    }
  }

  logTelemetryEventWithJsonable(event: AICHAT_EVENTS, payload: any) {
    this.logTelemetryEvent(event, JSON.stringify(payload));
  }

  logChatInteractionEvent(
    event: AICHAT_EVENTS,
    sentiment?: ChatSentimentType,
    feedbackIssueType?: ChatIssueType,
    feedbackMessage?: string,
    rpcGlobalId?: string
  ) {
    try {
      this.monospaceExtension?.exports.logChatInteractionEvent(
        event,
        sentiment,
        feedbackIssueType,
        feedbackMessage,
        rpcGlobalId
      );
    } catch (e) {
      api.debugLogger(e);
    }
  }
}

export let telemetry: TelemetryLogger | undefined;
export function initializeTelemetryLogger(ctx: ExtensionContext) {
  telemetry = new TelemetryLogger(monospaceextension);

  ctx.vsc.subscriptions.push(
    vscode.workspace.onDidChangeConfiguration(event => {
      if (event.affectsConfiguration(CONFIG_AI_ENABLE_RETRIEVAL.fullId)) {
        const status = isRetrievalEnabled() ? RAG_STATUS.ENABLED : RAG_STATUS.DISABLED;
        logRagEnablementSettingToggled(status);
      }
    })
  );
}

function logRagEnablementSettingToggled(status: RAG_STATUS) {
  telemetry?.logTelemetryEvent(AICHAT_EVENTS.RAG_ENABLEMENT_SETTING_TOGGLED, status);
}

export function logRetry(rpcGlobalId: string | undefined) {
  telemetry?.logTelemetryEventWithJsonable(AICHAT_EVENTS.SUPERCHAT_RETRY, {
    rpcGlobalId,
  });
}

export function logRetryFailed(rpcGlobalId: string | undefined, error: any = undefined) {
  telemetry?.logTelemetryEventWithJsonable(AICHAT_EVENTS.SUPERCHAT_RETRY_FAILED, {
    rpcGlobalId,
    error,
  });
}

// TODO(joshualitt): Attach rpc global id to function state and pass it through here. Also,
// some of these Superchat events should be interaction events.
export function logFunctionCallExecution(
  status: AICHAT_EVENTS.SUPERCHAT_FUNCTION_SUCCEEDED | AICHAT_EVENTS.SUPERCHAT_FUNCTION_FAILED,
  rpcGlobalId: string | undefined,
  functionState: FunctionState
) {
  telemetry?.logTelemetryEventWithJsonable(status, {rpcGlobalId, name: functionState.name});
}

export function logFunctionActionTaken(
  rpcGlobalId: string | undefined,
  action: FunctionAction,
  functionState: FunctionState
) {
  telemetry?.logTelemetryEventWithJsonable(AICHAT_EVENTS.SUPERCHAT_FUNCTION_ACTION_TAKEN, {
    rpcGlobalId,
    name: functionState.name,
    action,
  });
}

export function logActionRejected(rpcGlobalId: string | undefined, functionState: FunctionState) {
  telemetry?.logTelemetryEventWithJsonable(AICHAT_EVENTS.SUPERCHAT_FUNCTION_REJECTED, {
    rpcGlobalId,
    name: functionState.name,
  });
}

export function logthumbsUpOrDown(
  status: AICHAT_EVENTS.AICHAT_THUMBS_UP | AICHAT_EVENTS.AICHAT_THUMBS_DOWN,
  chatMessage: ChatMessage,
  areActionsEnabled: boolean
) {
  const {slashCommand, debugInfo, rpcGlobalId, usedRag} = chatMessage;

  const details = {
    message: slashCommand ? `slashCommand:${slashCommand}` : undefined,
    debugInfo,
    usedRag,
    areActionsEnabled,
    rpcGlobalId,
  };

  telemetry?.logTelemetryEventWithJsonable(status, details);
  telemetry?.logChatInteractionEvent(
    status,
    status === AICHAT_EVENTS.AICHAT_THUMBS_UP
      ? ChatSentimentType.POSITIVE
      : ChatSentimentType.NEGATIVE,
    undefined,
    details.message,
    rpcGlobalId
  );
}

export function logIndexingStarted(contentRoot: string) {
  telemetry?.logTelemetryEvent(AICHAT_EVENTS.RAG_INDEXING_STARTED, contentRoot);
}

export function logIndexingFinished(
  contentRoot: string,
  amountOfFiles: number | undefined,
  timeTaken: number | undefined
) {
  telemetry?.logTelemetryEvent(
    AICHAT_EVENTS.RAG_INDEXING_FINISHED,
    JSON.stringify({contentRoot, amountOfFiles, timeTaken})
  );
}
