async function setupStatusbarText(
  context: VSCExtensionContext,
  statusBarItem: vscode.StatusBarItem
) {
  const status = bypassEula
    ? true
    : context.globalState.get('idx.ai.onboardingCompleted', true /* default */);
  const text = status ? '$(mono-spark) Gemini' : '$(mono-spark) Gemini (click to get started)';
  const color = status ? undefined : new vscode.ThemeColor('terminal.ansiYellow');
  statusBarItem.text = text;
  statusBarItem.color = color;
}

async function setupIndexingStatusBar() {
  let contentRootsInProgress: string[] = [];
  let currentResolve: ((value?: unknown) => void) | undefined;
  let currentReport: // TODO: implement progress reporting using "increment"
  | vscode.Progress<{
        message?: string | undefined;
        increment?: number | undefined;
      }>['report']
    | undefined;

  function updateIndexingText() {
    if (contentRootsInProgress.length === 0) {
      currentResolve?.();
      currentResolve = undefined;
      currentReport = undefined;
      return;
    }

    if (currentResolve !== undefined) {
      return;
    }

    vscode.window.withProgress(
      {
        location: vscode.ProgressLocation.Window,
        cancellable: false,
        title: 'Gemini is indexing your project',
      },
      ({report}) =>
        new Promise(resolve => {
          currentResolve = resolve;
          currentReport = report;
        })
    );
  }

  socketIoClient.on(SocketEvents.INDEXING_STARTED, contentRoot => {
    if (contentRootsInProgress.includes(contentRoot)) {
      return;
    }

    contentRootsInProgress.push(contentRoot);
    updateIndexingText();

    logIndexingStarted(contentRoot);
  });

  socketIoClient.on(SocketEvents.INDEXING_FINISHED, (contentRoot, amountOfFiles, timeTaken) => {
    contentRootsInProgress = contentRootsInProgress.filter(cr => cr !== contentRoot);
    updateIndexingText();

    logIndexingFinished(contentRoot, amountOfFiles, timeTaken);
  });

  socketIoClient.on('connect_error', e => {
    debugLogger('Socket.io connect error', e);
  });
}

export function setupAiChat(extensionContext: ExtensionContext) {
  const context = extensionContext.vsc;
  const prompts = extensionContext.prompts;
  const broker = extensionContext.broker;
  const conversationalAgent = extensionContext.conversationalAgent;
  if (!disableRAG) {
    setupAiRetrieval(context);
    socketIoClient = io(INDEXSERVER_BASE_URL);
    setupIndexingStatusBar();
  }

  telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_CAN_OPEN);

  function errorIfHasNoSelection() {
    let selText = window.activeTextEditor?.document.getText(window.activeTextEditor.selection);
    if (!selText) {
      window.showInformationMessage('Select some text first to run this command');
      return true;
    }
    return false;
  }

  context.subscriptions.push(
    vscode.commands.registerCommand('aichat.prompt', async () => {
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_OPEN);
      ensureAndShowAiChatPanel(extensionContext);
    }),

    vscode.commands.registerCommand('aichat.actionsMenu', async () => {
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_ACTIONS_MENU);
      // vscode.commands.executeCommand('workbench.action.quickOpen', '>Gemini: ');
      let actions: (vscode.QuickPickItem & {command: string})[] = [
        {
          command: 'aichat.explainSelection',
          label: '$(comment) Explain Selected Code',
        },
        {
          command: 'aichat.commentSelection',
          label: '$(insert) Add Comments For Selected Code',
        },
        {
          command: 'aichat.doSomethingWithSelection',
          label: '$(wand) Do Anything With Selected Code',
        },
        {
          command: '',
          label: '',
          kind: vscode.QuickPickItemKind.Separator,
        },
        {
          command: 'aichat.prompt',
          label: '$(mono-spark) Chat with Gemini',
        },
        {
          command: 'inlineChat.start',
          label: '$(mono-spark) Start Inline Chat',
        },
      ];
      let action = await vscode.window.showQuickPick(actions);
      if (!action) {
        return;
      }
      vscode.commands.executeCommand(action.command!);
    }),

    vscode.commands.registerCommand('aichat.prompt.statusbar', async () => {
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_OPEN_STATUSBAR);
      vscode.commands.executeCommand('aichat.prompt');
    }),
    vscode.commands.registerCommand('aichat.prompt.hotkey', async () => {
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_OPEN_HOTKEY);
      vscode.commands.executeCommand('aichat.prompt');
    }),

    vscode.commands.registerCommand('aichat.explainSelection', async () => {
      if (errorIfHasNoSelection()) return;
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_EXPLAIN);
      openNewThread(broker);
      const res = await callAi(extensionContext, {
        prompt: prompts.explainSelection(),
      });
      await trySaveThread(prompts, broker);
      return res;
    }),
    vscode.commands.registerCommand('aichat.doSomethingWithSelection', async () => {
      if (errorIfHasNoSelection()) return;
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_DOSOMETHING);
      let actionText = await showDoSomethingDialog(context);
      if (!actionText) {
        return;
      }
      openNewThread(broker);
      const res = await callAi(extensionContext, {
        prompt: prompts.chatEdit(),
        actionText,
      });
      await trySaveThread(prompts, broker);
      return res;
    }),
    vscode.commands.registerCommand('aichat.commentSelection', async () => {
      if (errorIfHasNoSelection()) return;
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_COMMENT);
      openNewThread(broker);
      const res = await callAi(extensionContext, {
        prompt: prompts.chatAddComments(),
      });
      await trySaveThread(prompts, broker);
      return res;
    })
  );

  let statusBarItem = vscode.window.createStatusBarItem(vscode.StatusBarAlignment.Right, 10000);

  statusBarItem.text = '$(mono-spark) Gemini';
  statusBarItem.command = 'aichat.prompt.statusbar';
  statusBarItem.tooltip = 'Chat with Gemini'; // matching what's in package.json
  statusBarItem.show();
  setupStatusbarText(context, statusBarItem);

  broker.on('getOnboardingStatus', async () => {
    let status = context.globalState.get('idx.ai.onboardingCompleted', false /* default */);

    broker.send('notifyOnboardingStatus', status);

    if (!status) {
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_OPEN_ONBOARDING);
    }
  });

  broker.on('getPrivacyNoticeStatus', async () => {
    if (isPrivacyNoticeAccepted !== undefined) {
      broker.send('notifyPrivacyNoticeStatus', isPrivacyNoticeAccepted);
      return;
    }
    const isAccepted = bypassEula
      ? true
      : await monospaceextension?.exports.getPrivacyNoticeStatus();
    isPrivacyNoticeAccepted = isAccepted;
    broker.send('notifyPrivacyNoticeStatus', !!isAccepted);

    if (!isAccepted) {
      telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_OPEN_PRIVACY_NOTICE);
    }
  });

  // Fire and forget optimistically when a user accepts the privacy notice.
  // In the unfortunate events of api failure, the user will just
  // need to reaccept this in their next session.
  broker.on('acceptAiPrivacyNotice', async () => {
    isPrivacyNoticeAccepted = true;
    await monospaceextension?.exports.acceptAiPrivacyNotice();
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_ACCEPT_PRIVACY_NOTICE);
  });

  broker.on('setAIOnboardingCompleted', async () => {
    await context.globalState.update('idx.ai.onboardingCompleted', true);
    setupStatusbarText(context, statusBarItem);
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_COMPLETE_ONBOARDING);
  });

  broker.on('setInlineCompletionSetting', async (value: boolean) => {
    let {section, property} = CONFIG_AI_ENABLE_INLINE_COMPLETION;
    await vscode.workspace
      .getConfiguration(section)
      .update(property, value, vscode.ConfigurationTarget.Workspace);
    telemetry?.logTelemetryEvent(
      AICHAT_EVENTS.AICHAT_SAVE_INLINE_COMPLETION_SETTING,
      value.toString()
    );
  });

  broker.on('setRetrievalSetting', async (value: boolean) => {
    let {section, property} = CONFIG_AI_ENABLE_RETRIEVAL;
    await vscode.workspace
      .getConfiguration(section)
      .update(property, value, vscode.ConfigurationTarget.Workspace);
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_SAVE_RETRIEVAL_SETTING, value.toString());
  });

  broker.on('getChatThreadSummaries', async () => {
    const threads = await threadManager.listThreads();
    broker.send('notifyChatThreadSummaries', threads);
    broker.send('notifyCurrentThread', threadManager.currentThread.id);
  });

  let selection: vscode.Selection | undefined;
  let activeEditor: vscode.TextEditor | undefined;

  let onDidChangeTextEditorSelection = () =>
    vscode.window.onDidChangeTextEditorSelection(async e => {
      if (e.textEditor.document.uri.scheme !== 'file') {
        return;
      }

      activeEditor = e.textEditor;
      selection = e.textEditor.selection;
    });
  let onDidChangeTextEditorSelectionDisposable: vscode.Disposable | undefined;

  context.subscriptions.push(
    vscode.window.onDidChangeActiveTextEditor(async e => {
      if (e?.document.uri.scheme === 'untitled') {
        broker.send('currentFileIsExcluded', false);
      }
      if (e?.document.uri.scheme !== 'file') {
        return;
      }

      const isFileExcluded = await extensionContext.aiExclude.isExcluded(e.document.uri);
      broker.send('currentFileIsExcluded', isFileExcluded);

      if (isFileExcluded) {
        activeEditor = undefined;
        selection = undefined;

        if (onDidChangeTextEditorSelectionDisposable) {
          onDidChangeTextEditorSelectionDisposable.dispose();
          onDidChangeTextEditorSelectionDisposable = undefined;
        }

        return;
      }

      activeEditor = e;
      selection = e.selection;

      onDidChangeTextEditorSelectionDisposable = onDidChangeTextEditorSelection();
      context.subscriptions.push(onDidChangeTextEditorSelectionDisposable);
    })
  );

  broker.on('getSlashCommands', async () => {
    if (threadManager.currentThread.areActionsEnabled) {
      // Adds contextual slash commands to Superchat.
      // If the action override is enabled, then '/q' will force IDX to not use tools.
      // If the action override is disabled, then '/i' will allow IDX to use tools.
      const commands: Prompt[] = extensionContext.idxAgentSettings.alwaysAllowTools
        ? [QUESTION_PROMPT]
        : [INTERACTIVE_PROMPT];
      broker.send('setSlashCommands', commands);
    } else if (await extensionContext.features.isSlashCommandsEnabled()) {
      let hasErrors = false;
      if (selection) {
        hasErrors = hasErrorsForRange(selection, activeEditor);
      }
      const hasSelection = !!activeEditor?.document.getText(selection);
      const commands = prompts.chatPrompts(hasErrors, hasSelection);
      broker.send('setSlashCommands', commands);
    }
  });

  async function deleteCurrentThread() {
    // stop generating to avoid cross-thread state management bugs
    stopGenerating();

    const res = await vscode.window.showInformationMessage(
      'Delete current chat thread?',
      {
        modal: true,
      },
      {title: 'Cancel', isCloseAffordance: true},
      {title: 'Confirm', confirm: true}
    );
    if (!res?.confirm) {
      return;
    }

    await threadManager.deleteCurrentThread();

    const threads = await threadManager.listThreads();
    broker.send('notifyChatThreadSummaries', threads);
    broker.send('notifyCurrentThread', threadManager.currentThread.id);
    // TODO: we should include suggested option as part of the "thread" object
    areSuggestedPromptsEnabled() &&
      broker.send('notifySuggestedPrompt', threadManager.currentThread.suggestedNextPrompt);
    sendChatHistory(broker, threadManager.currentThread.history);
  }

  broker.on('openNewThread', actionsInChatAreEnabled => {
    // stop generating to avoid cross-thread state management bugs
    stopGenerating();
    openNewThread(broker, actionsInChatAreEnabled);
  });

  broker.on('openExistingThread', async (id: string) => {
    // stop generating to avoid cross-thread state management bugs
    stopGenerating();
    const history = threadManager.changeCurrentThread(id);
    //Notify change of current thread
    broker.send('notifyCurrentThread', id);
    if (history) {
      sendChatHistory(broker, threadManager.currentThread.history);
    }
    broker.send('notifyCurrentThread', id);
    // TODO: we should include suggested option as part of the "thread" object
    areSuggestedPromptsEnabled() &&
      broker.send('notifySuggestedPrompt', threadManager.currentThread.suggestedNextPrompt);
  });

  broker.on('getAvailabilityInCountry', async () => {
    const UNAVAILABLE_TEXT_MSG = 'Gemini is not enabled for your region. Stay tuned!';

    if (bypassEula) {
      isCurrentCountrySupported = true;
      return;
    }

    if (isCurrentCountrySupported === true) {
      return;
    }
    if (isCurrentCountrySupported === false) {
      broker.send('setAvailabilityFalse');
      return;
    }

    // Do a quick call to see if Gemini is enabled in user's country.
    const responseFromAida = (await extensionContext.idx?.exports.doStreamingConversation(
      'test connection',
      [],
      ''
    )) as Observable<AidaConversationResponse>;
    responseFromAida.subscribe({
      next: res => {
        if (res.text === UNAVAILABLE_TEXT_MSG) {
          broker.send('setAvailabilityFalse');
          isCurrentCountrySupported = false;
          telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_NOT_AVAILABLE_IN_COUNTRY);
        } else {
          isCurrentCountrySupported = true;
        }
      },
      done: () => {},
      error: () => {},
    });
  });

  broker.on('getChatHistory', async () => {
    sendChatHistory(broker, threadManager.currentThread.history);
  });

  broker.on('getSuggestedPrompt', async () => {
    areSuggestedPromptsEnabled() &&
      broker.send('notifySuggestedPrompt', threadManager.currentThread.suggestedNextPrompt);
  });

  broker.on('clearChatHistory', async () => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_COMMAND, 'clear');
    deleteCurrentThread();
  });

  broker.on('getChatStatus', async () => {
    broker.send('notifyChatStatus', chatStatus);
  });

  broker.on('performFunctionThreadAction', async action => {
    stopGenerating();
    stopGeneratingController = new AbortController();
    await conversationalAgent.allow(
      threadManager.currentThread.history,
      action,
      stopGeneratingController.signal
    );
    await trySaveThread(prompts, broker);
  });

  broker.on('newPrompt', async (prompt: string) => {
    let slashCommand: string | undefined;
    let message: string | undefined;
    if (prompt.startsWith('/')) {
      [slashCommand] = parseCommand(prompt);
      message = `slashCommand:${slashCommand}`;
    }
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_NEW_PROMPT, message);
    await handleConversationPrompt(extensionContext, prompt, slashCommand, activeEditor, selection);
    await trySaveThread(prompts, broker);
  });

  broker.on('newPromptAtPosition', async (prompt: string, position: number) => {
    threadManager.currentThread.history.truncateToPosition(position);
    sendChatHistory(broker, threadManager.currentThread.history);
    let slashCommand: string | undefined;
    let message: string | undefined;
    if (prompt.startsWith('/')) {
      [slashCommand] = parseCommand(prompt);
      message = `slashCommand:${slashCommand}`;
    }
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_NEW_PROMPT, message);
    await handleConversationPrompt(extensionContext, prompt, slashCommand, activeEditor, selection);
    await trySaveThread(prompts, broker);
  });

  broker.on('stopGenerating', () => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_STOP_GENERATING);
    stopGenerating();
  });

  broker.on('thumbsUp', async (position: number) => {
    logthumbsUpOrDown(
      AICHAT_EVENTS.AICHAT_THUMBS_UP,
      threadManager.currentThread.history.user[position],
      threadManager.currentThread.areActionsEnabled
    );
  });

  broker.on('thumbsDown', async (position: number) => {
    logthumbsUpOrDown(
      AICHAT_EVENTS.AICHAT_THUMBS_DOWN,
      threadManager.currentThread.history.user[position],
      threadManager.currentThread.areActionsEnabled
    );
  });

  broker.on('thumbsUpDetail', async (data: FeedbackFormState) => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_THUMBS_UP_DETAIL, JSON.stringify(data));
    // TODO: Log to Aida/Vertex once we know which one.
  });

  broker.on('thumbsDownDetail', async (data: FeedbackFormState) => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_THUMBS_DOWN_DETAIL, JSON.stringify(data));
    // TODO: Log to Aida/Vertex once we know which one.
  });

  broker.on('insertCode', async (code: string, citations: Citation[]) => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_INSERT_CODE);
    replaceSelectionWithCode(extensionContext, code, citations);
    monospaceextension?.exports.updateAIMetrics(code.length, 0); //lines added (chat,sidekick)
  });

  broker.on('onCopyCode', async (code: string) => {
    monospaceextension?.exports.updateLastCopiedCodeFromAIChat(code);
  });

  broker.on('openAiLink', async (href: string) => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_OPEN_LINK, href);
    vscode.env.openExternal(vscode.Uri.parse(href));
  });

  broker.on('navigateToFile', async (ref: string) => {
    navigateToFullFile(ref);
  });

  broker.on('openSettings', async () => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.OPEN_SETTINGS);
    vscode.commands.executeCommand('workbench.action.openSettings', 'IDX.aI');
  });

  broker.on('openLicenseLog', async () => {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_OPEN_LICENSE_LOG);
    vscode.commands.executeCommand('monospace.showLicenseAttribution');
  });

  broker.on('getActionsInChatAreEnabled', async () => {
    const isActionsInChatEnabledFromBackend =
      await extensionContext.features.isActionsInChatEnabled();
    broker.send('notifyActionsInChatAreEnabled', isActionsInChatEnabledFromBackend);
  });

  broker.on('getActionsInChatAreDefault', async () => {
    broker.send('notifyActionsInChatAreDefault', extensionContext.idxAgentSettings.isDefault);
  });

  return {
    async debugWithAI(file: string, line: string, error: string): Promise<void> {
      console.log('**Debugging with AI in the AIDA plugin!');
      navigateToFullFile(file);
      const prompt: Prompt = prompts.devtoolsDebug();
      setTimeout(function () {
        callAi(extensionContext, {
          prompt: prompt,
          actionText: error,
        });
      }, 300);
    },
  };
}

function navigateToFullFile(ref: string) {
  // find the first workspace that has the given file and open it.
  // TODO: this should be smarter, and include root directories
  if (ref == '') {
    return;
  }
  ref = ref.split('?')[0];
  for (let wf of vscode.workspace.workspaceFolders || []) {
    const fullUri = vscode.Uri.joinPath(wf.uri, ref);
    if (fs.existsSync(fullUri.fsPath)) {
      vscode.commands.executeCommand('vscode.open', fullUri, {
        viewColumn: vscode.ViewColumn.One,
      } as vscode.TextDocumentShowOptions);
    }
  }
}

interface DoSomethingQuickPickItem extends vscode.QuickPickItem {
  value?: string;
  clear?: boolean;
}

function showDoSomethingDialog(context: VSCExtensionContext): Promise<string | null> {
  let recents: string[] = context.globalState.get(DO_SOMETHING_RECENTS_KEY) || [];

  return new Promise(resolve => {
    const quickPick = vscode.window.createQuickPick<DoSomethingQuickPickItem>();
    quickPick.placeholder = 'e.g. make it more readable';
    quickPick.title = 'What do you want to do with the code?';

    function refreshItems() {
      quickPick.items = [
        ...recents.map(s => ({
          label: `$(history) "${s}"`,
          value: s,
          buttons: [
            {
              iconPath: new vscode.ThemeIcon('close'),
              tooltip: 'Remove',
            },
          ],
        })),
        ...(recents.length
          ? [
              {
                label: '',
                kind: vscode.QuickPickItemKind.Separator,
              },
              {
                label: '$(close) Clear recents',
                clear: true,
              },
            ]
          : []),
      ];
    }

    refreshItems();

    let choseClear = false;
    let value: string | undefined;
    let selected: string | undefined;
    quickPick.onDidChangeValue(e => {
      value = e;
    });
    quickPick.onDidChangeSelection(e => {
      if (e[0].clear) {
        choseClear = true;
        return;
      }
      selected = e[0].value;
    });
    quickPick.show();
    let accepted = false;
    quickPick.onDidAccept(() => {
      if (choseClear) {
        recents = [];
        context.globalState.update(DO_SOMETHING_RECENTS_KEY, undefined);
        refreshItems();
        choseClear = false;
        return;
      }
      accepted = true;
      let actionText = selected || value || '';
      recents = [actionText, ...recents.filter(s => s !== actionText)].slice(0, 5);
      context.globalState.update(DO_SOMETHING_RECENTS_KEY, recents);
      resolve(actionText);
      quickPick.hide();
      quickPick.dispose();
    });
    quickPick.onDidTriggerItemButton(e => {
      // clicked remove button
      recents = recents.filter(r => r !== e.item.value);
      context.globalState.update(DO_SOMETHING_RECENTS_KEY, recents);
      refreshItems();
    });
    quickPick.onDidHide(() => {
      if (accepted) {
        return;
      }

      resolve('');
      quickPick.dispose();
    });
  });
}

interface CallAiOptions {
  /**
   * The main prompt input.
   */
  prompt: Prompt;
  actionText?: string;
}

interface CallAiResult {
  response: string;
  skippedChat: boolean;
  error: boolean;
}

async function callAi(
  ctx: ExtensionContext,
  {prompt, actionText}: CallAiOptions
): Promise<CallAiResult> {
  let result: CallAiResult = {response: '', skippedChat: false, error: false};

  const isFileExcluded =
    window.activeTextEditor &&
    (await ctx.aiExclude.isExcluded(window.activeTextEditor.document.uri));

  const params: Params = {
    get selectedCode() {
      if (isFileExcluded) {
        return '';
      }

      let selText = window.activeTextEditor?.document.getText(window.activeTextEditor.selection);
      if (!selText) {
        window.showInformationMessage('Select some text first to run this command');
        result.error = true;
        return '';
      }
      return selText;
    },
    get currentLanguage() {
      if (isFileExcluded) {
        return undefined;
      }

      let languageId = window.activeTextEditor?.document.languageId;
      return languageId;
    },
    get currentLine() {
      return 0;
    },
    get prompt() {
      return actionText;
    },
  };

  const hydrated = await ctx.engine.hydrate(prompt.content, params);

  let promptPromise: Promise<string>;
  promptPromise = handleConversationPrompt(ctx, hydrated.prompt);

  ensureAndShowAiChatPanel(ctx);
  await new Promise(resolve => setTimeout(resolve)); // tick
  result.response = await promptPromise;

  return result;
}

/**
 * Handles an actions in chat prompt.
 */
async function handleActionsInChatPrompt(
  context: ExtensionContext,
  broker: ExtensionBrokerImpl,
  prompt: string,
  displayBody?: string,
  slashCommand?: string
): Promise<string> {
  const promptMessage: ChatMessage = {
    speaker: 'user',
    userInfo: await getCurrentUserInfo(),
    body: prompt,
    displayBody,
    slashCommand,
  };
  broker.send('notifyAddedChatMessage', promptMessage);
  await context.conversationalAgent.userPrompt(
    context,
    threadManager.currentThread.history,
    slashCommand,
    prompt,
    stopGeneratingController?.signal
  );
  return '';
}

/**
 * Handle a standard conversational prompt
 */
async function handleConversationPrompt(
  extensionContext: ExtensionContext,
  prompt: string,
  slashCommand?: string,
  activeEditor?: vscode.TextEditor,
  selection?: vscode.Selection
): Promise<string> {
  stopGenerating();

  const {broker} = extensionContext;
  broker.send('notifySuggestedPrompt', '');
  if (prompt.trim() === 'clear' || prompt.trim().startsWith('/clear')) {
    telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_COMMAND, 'clear');
    threadManager.currentThread.history.clear();
    sendChatHistory(broker, threadManager.currentThread.history);
    return '';
  }

  if (!prompt.trim()) {
    return '';
  }

  // prevent AI from interacting with excluded files
  if (activeEditor && (await extensionContext.aiExclude.isExcluded(activeEditor.document.uri))) {
    activeEditor = undefined;
    selection = undefined;
  }

  let displayBody: string | undefined;
  if (slashCommand) {
    displayBody = prompt;

    // TODO: This should be done at "broker.on('newPrompt')" level.
    // Limitting the blast radius as we are close to IO.
    const [_, query] = parseCommand(prompt);

    const template = extensionContext.prompts.byCommand(slashCommand, PromptAvailability.Chat);
    if (template) {
      const params = {
        get currentLanguage() {
          return activeEditor?.document.languageId ?? '';
        },
        get selectedCode() {
          return activeEditor?.document.getText(selection);
        },
        get errors() {
          return selection ? errorsForRange(selection) : '';
        },
        get prompt() {
          return query;
        },
        get currentLine() {
          if (!activeEditor) {
            return 0;
          }
          return activeEditor.selection.active.line + 1;
        },
      };
      prompt = (await extensionContext.engine.hydrate(template.content, params)).prompt;
    }
  }

  stopGeneratingController = new AbortController();

  if (threadManager.currentThread.areActionsEnabled) {
    return handleActionsInChatPrompt(extensionContext, broker, prompt, displayBody, slashCommand);
  }

  let message: string | undefined;
  if (slashCommand) {
    message = `slashCommand:${slashCommand}`;
  }
  telemetry?.logTelemetryEvent(AICHAT_EVENTS.AICHAT_INTERACTION, message);

  let promptMessage: ChatMessage = {
    speaker: 'user',
    userInfo: await getCurrentUserInfo(),
    body: prompt,
    displayBody,
    slashCommand,
  };
  threadManager.currentThread.history.pushUserMessage(promptMessage);
  broker.send('notifyAddedChatMessage', promptMessage);
  setChatStatus(ChatStatus.PreparingResponse, extensionContext);

  let response = '';
  try {
    if (process.env.FAKE_AI_RESPONSE === 'true') {
      const body = `This is a \`mock response\` from the AI that includes code:
\`\`\`js
// Log hello 10 times
for (let i = 0; i < 10; i++) {
  console.log('hello');
}
\`\`\`
I hope this helps!

And this is a test of a thing \`src/book/BookPage.js\` that should become a link
and \`package.json\` another one boom.
`;
      let indexedRepoFiles: string[] = [];
      if (isRetrievalEnabled()) {
        indexedRepoFiles = await getIndexedFiles();
        ({preamble: prompt} = await createPreambleFromPrompt(
          extensionContext,
          prompt,
          true,
          false
        ));
      }

      threadManager.currentThread.history.pushUserMessage({
        speaker: 'ai',
        body,
        fileReferences: indexedRepoFiles.filter(f => body.indexOf(f) >= 0),
        slashCommand,
      });
      sendChatHistory(broker, threadManager.currentThread.history);
      stopGeneratingController = undefined;
      setChatStatus(ChatStatus.Ready, extensionContext);
      return '';
    }
    return await handleBoqPrompt(extensionContext, prompt, slashCommand);
  } catch (err: any) {
    let responseMessage: ChatMessage = {
      speaker: 'ai',
      body: `Error: ${err.message || err}`,
      debugInfo: JSON.stringify(err),
      displayBody,
      slashCommand,
    };
    threadManager.currentThread.history.pushUserMessage(responseMessage);
    broker.send('notifyAddedChatMessage', responseMessage);
    debugLogger(err);
  }

  stopGeneratingController = undefined;
  setChatStatus(ChatStatus.Ready, extensionContext);
  return response;
}

async function handleBoqPrompt(
  extensionContext: ExtensionContext,
  prompt: string,
  slashCommand: string | undefined
): Promise<string> {
  const {broker} = extensionContext;
  const streamingConversationRpc = acquireStreamingRpc('streaming-conversation');
  let aborted = false;
  const aidaObservable: AidaObservable = {
    chatHistoryUpdate(response: UserChatHistory) {
      if (aborted) return; // TODO: 'deeper' abort
      threadManager.currentThread.history.set(response);
      sendChatHistory(broker, threadManager.currentThread.history);
    },
    error(e: Error) {
      if (aborted) return; // TODO: 'deeper' abort
      let errorMessage: ChatMessage = {
        speaker: 'ai',
        body: `Error: ${e.message}`,
        slashCommand,
      };
      broker.send('notifyAddedChatMessage', errorMessage);
      stopGeneratingController = undefined;
      setChatStatus(ChatStatus.Ready, extensionContext);
    },
    async done() {
      if (aborted) return; // TODO: 'deeper' abort
      stopGeneratingController = undefined;
      setChatStatus(ChatStatus.Ready, extensionContext);
      await trySaveThread(extensionContext.prompts, extensionContext.broker);
    },
  };
  stopGeneratingController?.signal.addEventListener('abort', () => {
    // TODO: append ("Response was stopped short") to the message
    aidaObservable.done();
    aborted = true;
  });
  // TODO: pass abort signal down for a 'deeper' abort
  await streamingConversationRpc.responseFromAida(
    extensionContext,
    prompt,
    slashCommand,
    threadManager.currentThread.history.user,
    aidaObservable
  );

  //Nothing to return immediately
  return '';
}

function setupWebView(broker: ExtensionBrokerImpl, webview: vscode.Webview, extensionUri: Uri) {
  broker.registerReceiver(webview);
  webview.options = {
    enableScripts: true,
    localResourceRoots: [extensionUri],
  };
  webview.html = getHtmlForWebview('aichat', extensionUri, webview);
}

function ensureAndShowAiChatPanel({vsc: context, broker}: ExtensionContext) {
  if (aiChatPanel) {
    aiChatPanel.reveal();
    broker.send('focusPrompt');
    return aiChatPanel;
  }

  aiChatPanel = window.createWebviewPanel(AICHAT_VIEW_TYPE, 'Gemini', vscode.ViewColumn.Two, {
    retainContextWhenHidden: true,
  });
  aiChatPanel.iconPath = vscode.Uri.joinPath(context.extensionUri, 'resources', 'icon-small.svg');
  aiChatPanel.onDidDispose(() => {
    aiChatPanel = null;
  });

  setupWebView(broker, aiChatPanel.webview, context.extensionUri);
}

async function replaceSelectionWithCode(
  ctx: ExtensionContext,
  code: string,
  citations: Citation[]
) {
  let editor: vscode.TextEditor | undefined;
  for (let activeEditor of vscode.window.visibleTextEditors) {
    if (activeEditor.document.languageId !== 'Log') {
      editor = activeEditor;
      break;
    }
  }
  if (!editor) {
    debugLogger('No editor');
    return '';
  }

  if (await ctx.aiExclude.isExcluded(editor.document.uri)) {
    window.showInformationMessage(
      "The current file is protected by .aiexclude. If you're sure about inserting this code there, manually copy + paste it."
    );
    debugLogger(`File ${editor.document.uri.path} is excluded in ${AI_EXCLUDE_FILENAME}`);
    return '';
  }

  let comment = '';
  if (citations && citations.length > 0) {
    const languageId = editor.document.languageId;
    const monospaceextension = vscode.extensions.getExtension('google.monospace');
    const recitations: string[] = await monospaceextension?.exports.insertRecitation(
      code,
      languageId,
      citations
    );
    recitations.forEach(recitation => {
      comment += recitation;
    });
  }
  editor.edit(b => {
    if (!editor) {
      return;
    }
    b.replace(editor.selection, comment + code);
  });
}

export function setChatHistory(broker: ExtensionBrokerImpl, chatHistoryToSet: UserChatHistory) {
  threadManager.currentThread.history.set(chatHistoryToSet);
  sendChatHistory(broker, threadManager.currentThread.history);
}

export function sendChatHistory(broker: ExtensionBrokerImpl, history: ChatHistory) {
  broker.send('notifyChatHistory', history.user);
  DEBUG && broker.send('notifyChatDebugInfo', {internalHistory: history.internal});
}

export function setChatStatusToPreparingResponse(context: ExtensionContext) {
  setChatStatus(ChatStatus.PreparingResponse, context);
}

export function stopGenerating() {
  stopGeneratingController?.abort();
  stopGeneratingController = undefined;
}

export function setChatStatusToReady(extensionContext: ExtensionContext) {
  stopGeneratingController = undefined;
  setChatStatus(ChatStatus.Ready, extensionContext);
}

function setChatStatus(status: ChatStatus, extensionContext: ExtensionContext) {
  const {logger, broker} = extensionContext;
  chatStatus = status;
  broker.send('notifyChatStatus', chatStatus);
  logger.debug('chat status = ' + status);
  if (status === ChatStatus.Ready) {
    enqueueSuggestNextPrompt(threadManager.currentThread.id, extensionContext);
  } else {
    cancelSuggestNextPrompt(threadManager.currentThread.id);
  }
}

let _suggestNextPromptTimeouts = new Map<string, NodeJS.Timeout>();

function areSuggestedPromptsEnabled() {
  let {section, property} = CONFIG_AI_ENABLE_SUGGESTED_PROMPTS;
  return !!vscode.workspace.getConfiguration(section).get(property);
}

function enqueueSuggestNextPrompt(threadId: string, extensionContext: ExtensionContext) {
  if (!areSuggestedPromptsEnabled()) return;
  const {logger, prompts, broker} = extensionContext;
  cancelSuggestNextPrompt(threadId);
  _suggestNextPromptTimeouts.set(
    threadId,
    setTimeout(async () => {
      logger.debug('Predicting next prompt for threadId ' + threadId);
      let thread = threadManager.getThread(threadId);
      try {
        if (!thread || thread.history.isEmpty) {
          return;
        }
        const response = await prompts.execute(prompts.suggestNextPromptPrompt(), {
          // TODO: slice() so as not to send the entire history... or leave it up tp the prompt?
          history: JSON.stringify(thread?.history.user),
          currentLine: 0,
        });

        let stillWantNextPrompt = _suggestNextPromptTimeouts.has(threadId);
        if (!stillWantNextPrompt || threadManager.currentThread.id !== threadId) return;

        let nextPrompt = response.llmResponse?.candidates[0]?.text || '';
        nextPrompt = nextPrompt.trim().replace(/^[\*\-]+|[\*\-]+$/g, '');
        threadManager.currentThread.suggestedNextPrompt = nextPrompt;
        await trySaveThread(prompts, broker);
        areSuggestedPromptsEnabled() && broker.send('notifySuggestedPrompt', nextPrompt);
      } catch (e) {
        logger.debug(
          `Unable to suggest next prompt for chat thread ${threadManager.currentThread.id}: ${e}`
        );
      } finally {
        cancelSuggestNextPrompt(threadId);
      }
      return undefined;
    }, 1000)
  );
}

function cancelSuggestNextPrompt(threadId: string) {
  clearTimeout(_suggestNextPromptTimeouts.get(threadId));
  _suggestNextPromptTimeouts.delete(threadId);
}

export function openNewThread(broker: ExtensionBrokerImpl, actionsInChatAreEnabled?: boolean) {
  threadManager.openNewThread(actionsInChatAreEnabled);
  broker.send('notifyCurrentThread', threadManager.currentThread.id);
  areSuggestedPromptsEnabled() &&
    broker.send('notifySuggestedPrompt', threadManager.currentThread.suggestedNextPrompt);
  sendChatHistory(broker, threadManager.currentThread.history);
}

export async function trySaveThread(prompts: PromptProvider, broker: ExtensionBrokerImpl) {
  if (threadManager.currentThread.history.isEmpty || threadManager.currentThread.isSaving) {
    return;
  }

  await threadManager.saveCurrentThread(async () => {
    const prompt = prompts.nameThreadPrompt();
    const params = {
      get history() {
        return JSON.stringify(threadManager.currentThread.history.user);
      },
      // TODO: This shouldn't be required...
      get currentLine() {
        return 0;
      },
    };
    try {
      const response = await prompts.execute(prompt, params);
      return response.llmResponse?.candidates[0]?.text;
    } catch (e) {
      debugLogger(
        `Unable to generate title for chat thread ${threadManager.currentThread.id}: ${e}`
      );
    }
    return undefined;
  });
  const threads = await threadManager.listThreads();
  broker.send('notifyChatThreadSummaries', threads);
  broker.send('notifyCurrentThread', threadManager.currentThread.id);
}

function parseCommand(prompt: string): [string, string] {
  if (prompt.startsWith('/')) {
    const [command, query] = prompt.slice(1).split(/ (.*)/s);
    return [command, query];
  }
  return ['', prompt];
}
