import {Script} from './scripts';
import {textMessage, readFile, runTerminalCommand, writeFile} from './utils';

export const implicitWithExplicitCasesScript: Script = {
  name: 'implicit with explicit cases',
  responses: [
    textMessage('tests a few implicit with explicit cases for tool use.'),
    // Setup files.
    writeFile('foo.js', 'hello foo'),
    textMessage('wrote to foo.js.'),
    writeFile('goo.js', 'hello goo'),
    textMessage('wrote to goo.js.'),
    writeFile('boo.js', 'hello boo'),
    textMessage('wrote to boo.js.'),
    writeFile('too.js', 'hello too'),
    textMessage('wrote to too.js.'),
    textMessage('wrote files'),
    // Case 1: run terminal command followed by read.
    readFile('foo.js.'),
    runTerminalCommand('ls'),
    textMessage('read foo.js and ran ls.'),
    // Case 2: run terminal command followed by read
    runTerminalCommand('ls'),
    readFile('foo.js'),
    textMessage('ran ls and read foo.js.'),
    // Case 3: Read file, run terminal command, read file.
    readFile('foo.js'),
    runTerminalCommand('ls'),
    readFile('goo.js'),
    textMessage('read foo.js, ran ls, and read goo.js.'),
    // Case 4: 2x read, run terminal command, 2x read.
    readFile('foo.js'),
    readFile('boo.js'),
    runTerminalCommand('ls'),
    readFile('goo.js'),
    readFile('too.js'),
    textMessage('read foo.js, boo.js, ran ls, and read goo.js, and too.js.'),
  ],
};

export const implicitCasesScript: Script = {
  name: 'implicit cases',
  responses: [
    textMessage('tests a few implicit cases for tool use.'),
    // Setup files.
    writeFile('foo.js', 'hello foo'),
    textMessage('wrote to foo.js.'),
    writeFile('goo.js', 'hello goo'),
    textMessage('wrote to goo.js.'),
    // Case 1: read file
    readFile('foo.js.'),
    textMessage('read foo.js.'),
    // Case 2: two read files
    readFile('foo.js'),
    readFile('goo.js'),
    textMessage('read foo.js and goo.js.'),
  ],
};
