import {
  Content,
  FunctionCall,
  FunctionCallArgs,
  FunctionName,
  FunctionResult,
  FunctionStatusEnum,
} from '../../../../src/agentive/api';
import {FeedbackFormState} from '../../../../src/aichat-feedback';
export {FunctionCall, FunctionCallArgs, FunctionName, FunctionResult, FunctionStatusEnum};

export interface AiChatWebviewToExtension {
  newPrompt(prompt: string): void;
  newPromptAtPosition(prompt: string, position: number): void;
  stopGenerating(): void;
  getChatHistory(): void;
  clearChatHistory(): void;
  getChatStatus(): void;
  getSuggestedPrompt(): void;
  getTheme(): void;
  insertCode(code: string, citations: Citation[]): void;
  onCopyCode(code: string): void;
  thumbsUp(position: number): void;
  thumbsDown(position: number): void;
  thumbsUpDetail(data: FeedbackFormState): void;
  thumbsDownDetail(data: FeedbackFormState): void;
  navigateToFile(ref: string): void;
  openAiLink(href: string): void;
  setAIOnboardingCompleted(): void;
  setInlineCompletionSetting(value: boolean): void;
  setRetrievalSetting(value: boolean): void;
  getOnboardingStatus(): void;
  getPrivacyNoticeStatus(): void;
  acceptAiPrivacyNotice(): void;
  getAvailabilityInCountry(): void;
  openSettings(): void;
  openLicenseLog(): void;
  getSlashCommands(): void;

  openNewThread(actionsInChatAreEnabled?: boolean): void;
  getChatThreadSummaries(): void;
  openExistingThread(id: string): void;
  performFunctionThreadAction(action: FunctionAction): void;
  getActionsInChatAreEnabled(): void;
  getActionsInChatAreDefault(): void;
}

export enum ChatStatus {
  Ready,
  PreparingResponse,
}

export interface Citation {
  startIndex: number;
  endIndex: number;
  repository: string;
  license: string;
  uri?: string;
}

export type FunctionAction = 'allow' | 'open_diff_view';

export type FunctionFrameEnum = 'requested' | 'rejected' | 'executing' | 'completed';

export interface FunctionFrame {
  call: FunctionCall;
  metadata?: Record<string, any>;
  state: FunctionFrameEnum;
  result?: FunctionResult;
  requiresPermission: boolean;
  showFollowup?: boolean;
}

export interface FunctionThreadState {
  frames: FunctionFrame[];
  readFiles?: string[];
  readUrls?: string[];
  followup?: string;
  framesWhichRequirePermission?: FunctionFrame[];
}

export interface UserInfo {
  displayName?: string;
  photoUrl?: string;
}

export interface ChatMessage {
  speaker: 'user' | 'ai';
  /**
   * For speaker = user, details about the user.
   */
  userInfo?: UserInfo;
  /**
   * The message body to send to the AI, or coming back from the AI
   */
  body?: string;
  /**
   * Alternate responses coming back from the AI
   */
  bodyAlternates?: string[];
  /**
   * The body to show to the user, if different from the body to send to the AI
   */
  displayBody?: string;
  /**
   * Any extra debug info
   */
  debugInfo?: string;
  /**
   * Which, if any, repo links are referenced in the body. This is used to render links
   * to the repo.
   */
  fileReferences?: string[];

  /**
   * AIDA RPC ID. In the event that this turn contains multiple model responses, then this will be
   * the last one. This is largely a UI constraint.
   * TODO(joshualitt): Do we want to consider a UI that let's a user thumbs up / down specific
   * submessages?
   */
  rpcGlobalId?: string;

  /**
   * Populated if prompt was generated with slashCommand.
   */
  slashCommand?: string;

  /**
   * Functions
   */
  functionThreadState?: FunctionThreadState;

  /**
   * Was RAG used to generate this message
   */
  usedRag?: boolean;
}

export type UserChatHistory = ChatMessage[];

export interface ChatThreadSummary {
  id: string;
  uuid: string;
  title: string;
  areActionsEnabled: boolean;
  lastEditTimestamp: number;
}

export interface ChatThread extends ChatThreadSummary {
  history: UserChatHistory;
  internalHistory: Content[];
  suggestedNextPrompt: string;
}

export interface SlashCommand {
  command: string;
  description: string;
}

export interface ThreadDebugInfo {
  internalHistory: Content[];
}

export interface AiChatExtensionToWebview {
  setAvailabilityFalse(): void;
  notifyActionsInChatAreEnabled(areEnabled: boolean): void;
  notifyActionsInChatAreDefault(areDefault: boolean): void;
  notifyOnboardingStatus(completed: boolean): void;
  notifyPrivacyNoticeStatus(accepted: boolean): void;
  notifyChatStatus(status: ChatStatus): void;
  notifyChatHistory(chatHistory: UserChatHistory): void;
  notifySuggestedPrompt(suggestedPrompt: string): void;
  notifyChatDebugInfo(debugInfo: ThreadDebugInfo): void;
  notifyAddedChatMessage(chatMessage: ChatMessage): void;
  notifyChatThreadSummaries(threads: ChatThreadSummary[]): void;
  notifyCurrentThread(id: string): void;
  notifyTheme(themeObj: any, editorFontFamily: string): void;
  focusPrompt(): void;
  setSlashCommands(slashCommands: SlashCommand[]): void;
  currentFileIsExcluded(isExcluded: boolean): void;
}

export const newThreadID = '#newThreadID';
export const newThreadWithActionsID = '#newThreadWithActionsID';
