import {
  badFunctionArgumentsScript,
  badFunctionCallsScript,
  badResponsesFromLLMScript,
  retryWithRecoverScript,
  tooManyRetriesScript,
} from './error-cases';
import {explicitCasesScript} from './explicit-cases';
import {helloWorldScript} from './hello-world';
import {implicitCasesScript, implicitWithExplicitCasesScript} from './implicit-cases';
import {replyToUserScript} from './reply-to-user';
import {errorMidStreamScript, streamingMessageScript} from './streaming-cases';
import {Response} from './utils';

export interface Script {
  name: string;
  responses: Response[];
}

function makeScripts(): Record<string, Response[]> {
  const scripts: Record<string, Response[]> = {};
  for (const script of [
    badFunctionArgumentsScript,
    badFunctionCallsScript,
    badResponsesFromLLMScript,
    errorMidStreamScript,
    explicitCasesScript,
    helloWorldScript,
    implicitCasesScript,
    implicitWithExplicitCasesScript,
    replyToUserScript,
    retryWithRecoverScript,
    streamingMessageScript,
    tooManyRetriesScript,
  ]) {
    scripts[script.name] = script.responses;
  }
  return scripts;
}

export const scripts = makeScripts();
