import {UserMetadata} from 'vscode-shared/vscodeplugin-exports';
import {UserInfo} from '~common/messaging/modules/aichat/aichat-protocol';
import {monospaceextension} from './aichat';

let cachedUserInfo: UserInfo;

const GET_CURRENT_USER_INFO_TIMEOUT = 2000;

export async function getCurrentUserInfo(): Promise<UserInfo> {
  if (cachedUserInfo) return cachedUserInfo;

  let userMetadata = await Promise.race<UserMetadata | undefined>([
    monospaceextension?.exports.getUserMetadata(),
    new Promise(resolve => setTimeout(resolve, GET_CURRENT_USER_INFO_TIMEOUT)),
  ]);

  if (userMetadata) {
    let userInfo: UserInfo = {
      displayName: userMetadata.userDisplayName,
      photoUrl: userMetadata.userPhotoUrl,
    };
    cachedUserInfo = userInfo;
    return userInfo;
  }

  return {};
}
