import * as api from '../api';
import {SystemInterface} from '../system-interface';
import {ExternalFunction, FunctionState} from './external-function';

export const ignoredNames: Set<string> = new Set([
  // node
  'node_modules',
  // flutter
  'android',
  'assets', // Might be worth including, but omitting for now.
  'build',
  'ios',
  'linux',
  'macos',
  'web',
  'windows',
  // misc
  'obj',
  'bin',
  'third_party',
  // 'lib'  // Unfortunately, Flutter stores source in lib.
]);

export class ListProjectFilesFunction extends ExternalFunction {
  name = 'list_project_files' as const;
  description = 'List project files for a given path.';
  parameters: api.ObjectSchema = {
    type: 'object',
    properties: {
      path: {
        type: 'string',
        description:
          'The path of the folder to list all of the files of this folder. This should be a relative path.',
      },
    },
    required: ['path'],
  };
  requiresPermission = false;

  constructor(public systemInterface: SystemInterface) {
    super();
  }

  async call(functionState: FunctionState): Promise<void> {
    const path = functionState.args.path;
    if (await this.systemInterface.folderExists(path)) {
      const files = await this.systemInterface.listFilesRecursive(path, ignoredNames);
      functionState.success(JSON.stringify(files));
    } else {
      functionState.failure('Folder not found.');
    }
  }
}
