import fetch from 'node-fetch';

const timeoutMs = 5000;

interface ApiData {
  stringValue: string;
}

interface ApiChunk {
  data: ApiData;
}

interface ApiItem {
  chunkRelevanceScore: number;
  chunk: ApiChunk;
}

export interface RetrievalItem {
  snippet: string;
  score: number;
}

export interface RetrievalResponse {
  items: RetrievalItem[];
}

export class Retrieval {
  constructor(public apiKey: string, public corpusId: string) {}

  async retrieve(query: string, maxDocs: number = 2): Promise<RetrievalResponse> {
    try {
      let {relevantChunks, error} = (await fetch(
        `https://generativelanguage.googleapis.com/v1beta/corpora/${this.corpusId}:query`,
        {
          method: 'post',
          headers: {
            'Content-Type': 'application/json',
            'X-Goog-Api-Key': this.apiKey,
          },
          body: JSON.stringify({
            query,
            resultsCount: maxDocs,
            metadataFilters: [],
          }),
          signal: AbortSignal.timeout(timeoutMs),
        }
      ).then(t => t.json())) as any;
      if (error) {
        return {items: []};
      }
      return {
        items: relevantChunks
          .map(
            (item: ApiItem): RetrievalItem => ({
              snippet: item.chunk?.data?.stringValue ?? '',
              score: item.chunkRelevanceScore,
            })
          )
          .slice(0, maxDocs),
      };
    } catch (e) {
      return {items: []};
    }
  }
}
