import * as vscode from 'vscode';
import * as path from 'path';
import globToRegex from 'glob-to-regexp';

import {ExtensionContext} from './context';
import {AiExclude, AI_EXCLUDE_FILENAME} from '../../ai-exclude';
import {homedir} from 'os';
import {ignoreAiExclude} from './aichat';

/**
 * AiExcludeProvider wraps the AiExclude class to provide caching for exclusion patterns
 * and watchers for changes to .aiexclude files. Reading and watching files is deferred
 * until the user takes an action that checks for an excluded file for the first time
 * to not affect startup performance of the extension.
 */
export class AiExcludeProvider {
  private patterns: Promise<string[]> | undefined;
  private regexp: [string, RegExp][] | undefined;
  private isWatching = false;

  constructor(
    private ctx: Pick<ExtensionContext, 'logger' | 'vsc'>,
    private root: string = homedir(),
    private aiExclude = new AiExclude(root)
  ) {
    vscode.commands.registerCommand('monospace.aiExclude.isExcluded', (uri: vscode.Uri) => {
      this.ctx.logger.debug(`command:monospace.aiExclude.isExcluded(${uri.fsPath})`);
      return this.isExcluded(uri);
    });
  }

  /**
   * isExcluded checks if a file within the workspace has been matches a pattern in a
   * .aiexclude file from the current directory or any of its parent directories.
   * @param uri the file to check
   * @returns
   */
  async isExcluded(uri: vscode.Uri): Promise<boolean> {
    if (ignoreAiExclude) {
      return false;
    }
    const start = performance.now();
    if (!this.isWatching) {
      this.ctx.logger.debug('watching .aiexclude files for changes');
      this.watchAiExcludeFiles();
      this.isWatching = true;
    }
    // save the list of exclusions so they aren't reprocessed each time we
    // check for them.
    if (!this.patterns) {
      this.ctx.logger.debug('cache is empty, reading .aiexclude files');
      this.patterns = this.aiExclude.getPatternsToExclude();
      this.regexp = (await this.patterns).map(p => [p, globToRegex(path.resolve(this.root, p))]);
    }
    for (const [pattern, regexp] of this.regexp ?? []) {
      if (regexp.test(uri.path)) {
        const end = performance.now();
        this.ctx.logger.debug(`isExcluded(${uri.path}) took ${end - start} ms`);
        this.ctx.logger.debug(`file ${uri.path} is excluded by pattern: ${pattern}`);
        return true;
      }
    }
    const end = performance.now();
    this.ctx.logger.debug(`isExcluded(${uri.path}) took ${end - start} ms`);
    this.ctx.logger.debug(`file ${uri.path} is not excluded`);
    return false;
  }

  /**
   * watchAiExcludeFiles watches for fs events to the AI_IGNORE_FILENAME files
   * that affect the workspace.
   * When a file is changed, created, or deleted, we delete the cached exclusions
   * patterns from the patterns. The list of exclusions is then processed again
   * when isExcluded is called.
   *
   * @param root the root of the workspace
   */
  private watchAiExcludeFiles() {
    if (ignoreAiExclude || this.isWatching) return;
    const watcher = vscode.workspace.createFileSystemWatcher(
      path.join(this.root, '**', AI_EXCLUDE_FILENAME)
    );
    this.ctx.vsc.subscriptions.push(watcher);
    watcher.onDidChange(file => {
      this.ctx.logger.debug(`${file.fsPath} has changed, removing saved patterns`);
      delete this.patterns;
      delete this.regexp;
    });
    watcher.onDidCreate(file => {
      this.ctx.logger.debug(`${file.fsPath} was created, removing saved patterns`);
      delete this.patterns;
      delete this.regexp;
    });
    watcher.onDidDelete(file => {
      this.ctx.logger.debug(`${file.fsPath} was deleted, removing saved patterns`);
      delete this.patterns;
      delete this.regexp;
    });
  }
}
