import * as api from '../api';
import {ExternalFunction, FunctionState} from './external-function';
import {TaskManager} from '../task-manager';
import {SystemInterface} from '../system-interface';

export class RunTerminalCommandFunction extends ExternalFunction {
  name = 'run_terminal_command' as const;
  description = 'Runs a terminal command';
  parameters: api.ObjectSchema = {
    type: 'object',
    properties: {
      command: {
        type: 'string',
        description: 'The command to run in the terminal.',
      },
    },
    required: ['command'],
  };

  constructor(public systemInterface: SystemInterface, private taskManager: TaskManager) {
    super();
  }

  updateArgs(args: api.FunctionCallArgs): api.FunctionCallArgs {
    return {
      command: api.stringWorkaround(args.command),
    };
  }

  async call(functionState: FunctionState): Promise<void> {
    await this.taskManager.exec(
      functionState.args.command,
      () => functionState.detach('Process is long running, please continue.'),
      (status, result) => {
        switch (status) {
          case 'success':
            functionState.success(result);
            break;
          case 'failure':
          // We really shouldn't see this callback invoked for detach, so we treat this as a failure
          // case.
          case 'detached':
            functionState.failure(result);
            break;
        }
      }
    );
  }
}
