import * as fs from 'fs';
import * as path from 'path';
import * as vscode from 'vscode';
import {ExtensionContext} from './context';

export function setupWebViewThemingSync(ctx: ExtensionContext) {
  let {vsc: context, broker} = ctx;

  let refreshTheme = () => {
    const themeName: string =
      vscode.workspace.getConfiguration('workbench').get('colorTheme') || '';
    const themeObj = getThemeObj(themeName);
    if (!themeObj['name']) {
      themeObj['name'] = themeName;
    }
    const editorFontFamily: string =
      vscode.workspace.getConfiguration('editor').get('fontFamily') || '';
    broker.send('notifyTheme', themeObj, editorFontFamily);
  };

  broker.on('getTheme', () => refreshTheme());

  context.subscriptions.push(
    vscode.workspace.onDidChangeConfiguration(e => {
      if (
        e.affectsConfiguration('workbench.colorTheme') ||
        e.affectsConfiguration('editor.fontFamily')
      ) {
        refreshTheme();
      }
    }),
    vscode.window.onDidChangeActiveColorTheme(e => {
      refreshTheme();
    })
  );
}

const getThemeObj = (themeName: string): {[scope: string]: any} => {
  const tokenColors: {[scope: string]: any} = {};
  let currentThemePath;
  for (const extension of vscode.extensions.all) {
    let themes = extension.packageJSON.contributes && extension.packageJSON.contributes.themes;
    if (!themes) {
      continue;
    }
    if (!Array.isArray(themes)) {
      themes = [themes];
    }
    const currentTheme =
      themes && themes.find((theme: any) => theme.id === themeName || theme.label === themeName);
    if (currentTheme) {
      currentThemePath = path.join(extension.extensionPath, currentTheme.path);
      break;
    }
  }
  const themePaths: string[] = [];
  if (currentThemePath) {
    themePaths.push(currentThemePath);
  }
  while (themePaths.length > 0) {
    const themePath = themePaths.pop()!;
    return JSON.parse(fs.readFileSync(themePath, {encoding: 'utf-8'}) || '{}');
  }
  return tokenColors;
};
