import * as vscode from 'vscode';
import {LlmOutputCandidate} from 'prompt-engine';

import {AIDA_TAB, setChatStatusToReady, trySaveThread} from './aichat';
import {getErrorList, hasErrorsForRange} from './error-helper';
import {setChatStatusToPreparingResponse, setChatHistory, openNewThread} from './aichat';
import {ExtensionContext} from './context';
import {PromptAction} from './prompt';
import {UserChatHistory} from '~common/messaging/modules/aichat/aichat-protocol';
import {Citation} from './aida';
import {getCurrentUserInfo} from './user-info';

export const enum SIDEKICK_EVENTS {
  SIDEKICK_OPEN = 'sidekick.open',
  SIDEKICK_OPEN_PRIVACY_NOTICE = 'sidekick.open.privacy',
  SIDEKICK_SUBMIT = 'sidekick.submit',
  SIDEKICK_THUMBS_UP = 'sidekick.thumbsUp',
  SIDEKICK_THUMBS_DOWN = 'sidekick.thumbsDown',
  SIDEKICK_UNDO = 'sidekick.undo',
  SIDEKICK_ACCEPT = 'sidekick.accept',
  SIDEKICK_ERROR = 'sidekick.error',
  SIDEKICK_NO_RESPONSE = 'sidekick.noResponse',
}

export const enum GenerationSentiment {
  SENTIMENT_UNSPECIFIED = 'unknown',
  POSITIVE = 'positive',
  NEGATIVE = 'negative',
}

export const enum GenerationEventType {
  UNKNOWN = 'unknown',
  DISPLAYED = 'displayed',
  ACCEPTED = 'accepted',
  MODIFIED = 'modified',
  FEEDBACK = 'feedback',
}

//Todo(ksat): extract all usages of monospaceextension in one place
const monospaceextension = vscode.extensions.getExtension('google.monospace');

export function setupSidekick(ctx: ExtensionContext) {
  const telemetry = new SidekickTelemetryReporter(ctx);
  const feedback = new SidekickFeedbackReporter(ctx);
  ctx.vsc.subscriptions.push(
    vscode.interactive.registerInteractiveEditorSessionProvider(
      new SidekickProvider(ctx, telemetry, feedback)
    )
  );
}

export class SidekickProvider implements vscode.InteractiveEditorSessionProvider {
  constructor(
    private ctx: ExtensionContext,
    private telemetry: SidekickTelemetryReporter,
    private feedback: SidekickFeedbackReporter
  ) {}
  async prepareInteractiveEditorSession(
    context: vscode.TextDocumentContext,
    token: vscode.CancellationToken
  ): Promise<SidekickSession> {
    try {
      const isExcludedFile = await this.ctx.aiExclude.isExcluded(context.document.uri);
      if (this.ctx.idx) {
        this.ctx.idx.exports.pauseCompletions();
        this.ctx.idx.exports
          .getPrivacyNoticeStatus()
          .then((accepted: boolean) => {
            if (!accepted) {
              this.telemetry.report(SIDEKICK_EVENTS.SIDEKICK_OPEN_PRIVACY_NOTICE);
              vscode.commands.executeCommand('aichat.prompt');
            }
          })
          .catch((e: any) => this.ctx.logger.error(e));
      }
      let hasSelection = !!context.document.getText(context.selection);
      let placeholder = 'Enter a prompt';
      if (await this.ctx.features.isSlashCommandsEnabled()) {
        placeholder = `Enter a prompt or '/' for commands`;
      }
      let message = hasSelection
        ? 'How do you want to change the code?'
        : 'What code do you want to write?';
      if (isExcludedFile) {
        message =
          '$(eye-closed) This file is hidden from AI with a .aiexclude file. ' +
          "You can generate new code but Gemini won't be able to modify existing code in this file.";
        placeholder = 'Enter a prompt';
        hasSelection = false;
      }
      //Set slash commands only when there are errors
      const hasErrors = hasErrorsForRange(context.selection);

      let slashCommands: vscode.InteractiveEditorSlashCommand[] | undefined;
      let telemetryMessage: string | undefined;
      if (await this.ctx.features.isSlashCommandsEnabled()) {
        slashCommands = this.ctx.prompts?.sidekickPrompts(hasErrors, hasSelection).map(p => ({
          command: p.command,
          detail: p.description,
        }));
        const slashCommandNames = slashCommands.map(c => c.command).join(',');
        telemetryMessage = `slashCommands:${slashCommandNames}`;
      }
      this.telemetry.report(SIDEKICK_EVENTS.SIDEKICK_OPEN, telemetryMessage);
      return {
        context,
        placeholder,
        message,
        slashCommands,
        isExcludedFile,
        history: [],
      };
    } catch (e) {
      this.telemetry.report(SIDEKICK_EVENTS.SIDEKICK_ERROR, `${e}`);
      throw e;
    }
  }
  async provideInteractiveEditorResponse(
    session: SidekickSession,
    request: vscode.InteractiveEditorRequest,
    progress: vscode.Progress<vscode.InteractiveEditorProgressItem>,
    token: vscode.CancellationToken
  ): Promise<vscode.InteractiveEditorResponse | vscode.InteractiveEditorMessageResponse> {
    try {
      // Make sure privacy notice is accepted already.
      if (!this.ctx.idx || !(await this.ctx.idx.exports.getPrivacyNoticeStatus())) {
        return {
          edits: [],
        };
      }

      const hasSelection = !!session.context.document.getText(request.selection);
      const prompt = this.ctx.prompts.sidekickPrompt(request, hasSelection);
      const usedSlashCommand = prompt.command && request.prompt.startsWith('/');
      let message: string | undefined;
      if (usedSlashCommand) {
        session.slashCommand = prompt.command;
        message = `slashCommand:${prompt.command}`;
      }
      this.telemetry.report(SIDEKICK_EVENTS.SIDEKICK_SUBMIT, message);

      if (prompt.action === PromptAction.Chat) {
        vscode.commands.executeCommand('aichat.prompt');
        if (!session.keepThread) {
          openNewThread(this.ctx.broker);
        }
        setChatStatusToPreparingResponse(this.ctx);
      }

      const params = {
        get currentLanguage() {
          return vscode.window.activeTextEditor?.document.languageId ?? '';
        },
        get openFilename() {
          if (session.isExcludedFile) {
            return '';
          }
          return vscode.window.activeTextEditor?.document.fileName ?? '';
        },
        get openFileContent() {
          if (session.isExcludedFile) {
            return '';
          }
          return session.context.document.getText();
        },
        get selectedCode() {
          if (session.isExcludedFile) {
            return '';
          }
          return session.context.document.getText(request.wholeRange);
        },
        get errors() {
          if (session.isExcludedFile) {
            return '';
          }
          return getErrorList(request);
        },
        get prompt() {
          const [command, query] = parseCommand(request.prompt);
          if (command) {
            return query;
          }
          return request.prompt;
        },
        get currentLine() {
          return vscode.window.activeTextEditor!.selection.active.line + 1;
        },
      };
      const response = await this.ctx.prompts.execute(prompt, params);
      const generateCodeResponse = response.llmResponse?.candidates;
      const codeReponse = generateCodeResponse?.[0];
      if (codeReponse?.diagnostics) {
        const diagnostics = codeReponse.diagnostics as {
          type: string;
          additionalInfo?: string;
        }[];
        return {
          contents: new vscode.MarkdownString(
            `The response was blocked due to ${diagnostics[0].type}.`
          ),
        };
      }
      if (!codeReponse?.text) {
        this.telemetry.report(SIDEKICK_EVENTS.SIDEKICK_NO_RESPONSE);
        return {contents: new vscode.MarkdownString('No response. Try another prompt.')};
      }

      if (prompt.action === PromptAction.Chat) {
        // TODO: refactor to use vscode's executeCommand() or otherwise decouple the code
        setChatStatusToReady(this.ctx);
        const chatHistory: UserChatHistory = [
          {
            speaker: 'user',
            userInfo: await getCurrentUserInfo(),
            body: request.prompt,
          },
          {
            speaker: 'ai',
            body: codeReponse.summary,
          },
        ];
        setChatHistory(this.ctx.broker, chatHistory);
        await trySaveThread(this.ctx.prompts, this.ctx.broker);
        session.keepThread = true;
        return {
          edits: [],
        };
      }

      session.history.unshift(codeReponse);
      const extractedCode = extractFirstCodeBlock(codeReponse.text);
      this.feedback.report(
        SIDEKICK_EVENTS.SIDEKICK_SUBMIT,
        GenerationEventType.DISPLAYED,
        GenerationSentiment.SENTIMENT_UNSPECIFIED,
        0,
        codeReponse.rpcGlobalId
      );

      if (!extractedCode) {
        return {
          edits: [],
        };
      }

      try {
        monospaceextension?.exports.updateAIMetrics(0, extractedCode.length); //lines added (chat,sidekick)
      } catch (e) {
        console.log('Error incrementing usage metrics for sidekick');
      }

      const comment = await getRecitation(
        codeReponse.citations,
        extractedCode,
        params.currentLanguage
      );

      let range = request.wholeRange;
      if (session.isExcludedFile) {
        range = new vscode.Range(
          new vscode.Position(range.end.line + 1, 0),
          new vscode.Position(range.end.line + 1, 0)
        );
      }
      return {
        edits: [
          {
            newText: comment + extractedCode,
            range,
          },
        ],
      };
    } catch (e) {
      this.telemetry.report(SIDEKICK_EVENTS.SIDEKICK_ERROR, `${e}`);
      this.ctx.logger.error(`provideInteractiveEditorResponse: ${e}`);
      throw Error('An error occurred – please try again later.');
    }
  }
  handleInteractiveEditorResponseFeedback?(
    session: SidekickSession,
    response: vscode.InteractiveEditorResponse | vscode.InteractiveEditorMessageResponse,
    kind: vscode.InteractiveEditorResponseFeedbackKind
  ): void {
    let action: SIDEKICK_EVENTS | undefined;
    let event = GenerationEventType.UNKNOWN;
    let sentiment = GenerationSentiment.SENTIMENT_UNSPECIFIED;
    let message: string | undefined;
    if (session.slashCommand) {
      message = `slashCommand:${session.slashCommand}`;
    }
    switch (kind) {
      case vscode.InteractiveEditorResponseFeedbackKind.Helpful:
        action = SIDEKICK_EVENTS.SIDEKICK_THUMBS_UP;
        event = GenerationEventType.FEEDBACK;
        sentiment = GenerationSentiment.POSITIVE;
        break;
      case vscode.InteractiveEditorResponseFeedbackKind.Unhelpful:
        action = SIDEKICK_EVENTS.SIDEKICK_THUMBS_DOWN;
        event = GenerationEventType.FEEDBACK;
        sentiment = GenerationSentiment.NEGATIVE;
        break;
      case vscode.InteractiveEditorResponseFeedbackKind.Undone:
        action = SIDEKICK_EVENTS.SIDEKICK_UNDO;
        event = GenerationEventType.UNKNOWN;
        sentiment = GenerationSentiment.NEGATIVE;
        this.ctx.idx?.exports.resumeCompletions();
        break;
      case vscode.InteractiveEditorResponseFeedbackKind.Accepted:
        action = SIDEKICK_EVENTS.SIDEKICK_ACCEPT;
        event = GenerationEventType.ACCEPTED;
        sentiment = GenerationSentiment.POSITIVE;
        this.ctx.idx?.exports.resumeCompletions();
        break;
    }
    if (action) {
      this.telemetry.report(action, message);
    }
    const rpcGlobalId = session.history[0]?.rpcGlobalId;
    if (action && sentiment && rpcGlobalId) {
      this.feedback.report(action, event, sentiment, 0, rpcGlobalId);
    }
  }
}

interface SidekickSession extends vscode.InteractiveEditorSession {
  context: vscode.TextDocumentContext;
  history: LlmOutputCandidate[];
  slashCommand?: string;
  keepThread?: boolean;
  isExcludedFile?: boolean;
}

export class SidekickTelemetryReporter {
  constructor(private ctx: ExtensionContext) {}
  async report(event: SIDEKICK_EVENTS, message?: string) {
    this.ctx.logger.info(`Telemetry logging: ${event} ${message}`);
    try {
      await this.ctx.idx?.exports.logTelemetryEvent(AIDA_TAB, event, message);
    } catch (e) {
      this.ctx.logger.error('Telemetry logging error: ' + e);
    }
  }
}

export class SidekickFeedbackReporter {
  constructor(private ctx: ExtensionContext) {}
  async report(
    action: SIDEKICK_EVENTS,
    event: GenerationEventType,
    sentiment: GenerationSentiment,
    latencyMs: number,
    rpcGlobalId?: string
  ) {
    this.ctx.logger.info('Feedback logging: ' + action + ' ' + event + ' ' + sentiment);
    try {
      this.ctx.idx?.exports.logGenerateInteractionEvent(
        action,
        event,
        sentiment,
        latencyMs,
        rpcGlobalId
      );
    } catch (e) {
      this.ctx.logger.error('Feedback logging error: ' + e);
    }
  }
}

export function extractFirstCodeBlock(text: string): string {
  let delimiter = '```';
  let firstDelimieterIndex = text.indexOf(delimiter);
  if (firstDelimieterIndex === -1) {
    firstDelimieterIndex = text.indexOf('`');
    if (firstDelimieterIndex === -1) {
      // If the delimiter is not found, return unaltered text.
      return text;
    }
    delimiter = '`';
  }

  const lastDelimiterIndex = text.lastIndexOf(delimiter);
  // delimiter is only at the start or end of the text
  if (lastDelimiterIndex === firstDelimieterIndex) {
    const [start, end] = text.split(delimiter);
    return start.length > end.length ? start : end;
  }

  const isMultiline = text.split('\n').length > 2;
  if (isMultiline && text.startsWith(delimiter) && text.endsWith('\n' + delimiter)) {
    const firstBreak = text.indexOf('\n');
    return text.slice(firstBreak + 1, lastDelimiterIndex);
  }

  let code = text.slice(firstDelimieterIndex + delimiter.length, lastDelimiterIndex);
  if (!code.endsWith('\n')) {
    code = code + '\n';
  }
  return code;
}

async function getRecitation(citations: Citation[], code: string, languageId: string) {
  let comment = '';
  if (citations && citations.length > 0) {
    const monospaceextension = vscode.extensions.getExtension('google.monospace');
    const recitations: string[] = await monospaceextension?.exports.insertRecitation(
      code,
      languageId,
      citations
    );
    recitations.forEach(recitation => {
      comment += recitation;
    });
  }
  return comment;
}

function parseCommand(prompt: string): [string, string] {
  if (prompt.startsWith('/')) {
    const [command, query] = prompt.slice(1).split(/ (.*)/s);
    return [command, query];
  }
  return ['', prompt];
}
