import * as api from './api';

function content(role: api.Content['role'], part: api.Part): api.Content {
  return {role, parts: [part]};
}

export function userContent(part: api.Part): api.Content {
  return content('user', part);
}

export function modelContent(part: api.Part): api.Content {
  return content('model', part);
}

export function userText(text: string): api.Content {
  return userContent({text});
}

export function modelText(text: string): api.Content {
  return modelContent({text});
}

export function functionCall(name: api.FunctionName, args: api.FunctionCallArgs): api.Content {
  return modelContent({functionCall: {name, args}});
}

export function readFileCall(path: string): api.Content {
  return functionCall('read_file', {path});
}

export function runTerminalCommandCall(command: string): api.Content {
  return functionCall('run_terminal_command', {command});
}

export function writeFileCall(path: string, content: string): api.Content {
  return functionCall('write_file', {path, content});
}

export function listProjectFilesCall(path: string): api.Content {
  return functionCall('list_project_files', {path});
}

export function functionResponseToContent(functionResponse: api.FunctionResponse): api.Content {
  return userContent({functionResponse});
}

export function functionResponse(
  name: api.FunctionName,
  response: api.FunctionResult
): api.Content {
  return functionResponseToContent({name, response});
}

export function readFileResponse(status: api.FunctionStatusEnum, result: string): api.Content {
  return functionResponse('read_file', {status, result});
}

export function runTerminalCommandResponse(
  status: api.FunctionStatusEnum,
  result: string
): api.Content {
  return functionResponse('run_terminal_command', {status, result});
}

export function writeFileResponse(status: api.FunctionStatusEnum, result: string): api.Content {
  return functionResponse('write_file', {status, result});
}

export function listProjectFilesResponse(
  status: api.FunctionStatusEnum,
  result: string
): api.Content {
  return functionResponse('list_project_files', {status, result});
}

export function readFileFailure(result: string): api.Content {
  return readFileResponse('failure', result);
}

export function runTerminalCommandFailure(result: string): api.Content {
  return runTerminalCommandResponse('failure', result);
}

export function writeFileFailure(result: string): api.Content {
  return writeFileResponse('failure', result);
}

export function listProjectFilesFailure(result: string): api.Content {
  return listProjectFilesResponse('failure', result);
}

export function readFileSuccess(result: string): api.Content {
  return readFileResponse('success', result);
}

export function runTerminalCommandSuccess(result: string): api.Content {
  return runTerminalCommandResponse('success', result);
}

export function writeFileSuccess(result: string): api.Content {
  return writeFileResponse('success', result);
}

export function listProjectFilesSuccess(result: string): api.Content {
  return listProjectFilesResponse('success', result);
}
