import * as vscode from 'vscode';

import {readFileSync, writeFileSync} from 'fs';

let licenseAttributionFile = '/home/user/.monospace_license_attributions.json';

//Note: Please don't merge with isLocal=true, use only for testing
const isLocal = false;
if (isLocal) {
  licenseAttributionFile = '/tmp/.monospace_license_attributions.json';
}

export interface LicenseAttribution {
  id: string;
  timestamp: number;
  filepath: string;
  snippet: string;
  repository: string;
  license: string;
  uri?: string;
}

export interface LicenseAttributionList {
  attributions: LicenseAttribution[];
}

const emptyAttributionList = {attributions: []};

export async function getLicenseAttributionList(): Promise<LicenseAttributionList> {
  if (!licenseAttributionFile) {
    return emptyAttributionList;
  }
  try {
    const contents = readFileSync(licenseAttributionFile);
    const json = JSON.parse(contents.toString());

    console.log(`Number of license attributions: ${json.attributions.length}`);
    return json;
  } catch (e) {}
  return emptyAttributionList;
}

export async function insertLicenseAttribution(attribution: LicenseAttribution): Promise<boolean> {
  const attributionsList = await getLicenseAttributionList();
  attributionsList?.attributions.push(attribution);

  writeFileSync(licenseAttributionFile, JSON.stringify(attributionsList, null, 2));

  return true;
}
