import * as protocol from './aichat-protocol';

export class FunctionState {
  constructor(
    public thread: protocol.FunctionThreadState,
    public functionFrame: protocol.FunctionFrame
  ) {}

  static genericFromLastFrame<T extends FunctionState>(
    type: {new (thread: protocol.FunctionThreadState, state: protocol.FunctionFrame): T},
    thread: protocol.FunctionThreadState | undefined
  ): T | undefined {
    if (thread) {
      const lastState = thread.frames[thread.frames.length - 1];
      return new type(thread, lastState);
    } else {
      return undefined;
    }
  }

  static fromLastFrame(
    thread: protocol.FunctionThreadState | undefined
  ): FunctionState | undefined {
    return this.genericFromLastFrame<FunctionState>(FunctionState, thread);
  }

  static fromLastFrameRequiringPermissionOrElseLastFrame(
    thread: protocol.FunctionThreadState | undefined
  ): FunctionState | undefined {
    let lastFrameWhichRequiresPermission: protocol.FunctionFrame | undefined;
    const framesWhichRequirePermission = thread?.framesWhichRequirePermission;
    if (framesWhichRequirePermission) {
      lastFrameWhichRequiresPermission =
        framesWhichRequirePermission[framesWhichRequirePermission.length - 1];
    }
    if (lastFrameWhichRequiresPermission) {
      return new FunctionState(thread!, lastFrameWhichRequiresPermission);
    } else {
      return this.genericFromLastFrame<FunctionState>(FunctionState, thread);
    }
  }

  fromFrame(frame: protocol.FunctionFrame): FunctionState {
    return new FunctionState(this.thread, frame);
  }

  get call(): protocol.FunctionCall {
    return this.functionFrame.call;
  }

  get metadata() {
    return this.functionFrame.metadata || {};
  }

  get name(): protocol.FunctionName {
    return this.call.name;
  }

  get args(): protocol.FunctionCallArgs {
    return this.call.args;
  }

  get state(): protocol.FunctionFrameEnum {
    return this.functionFrame.state;
  }

  get functionResult(): protocol.FunctionResult | undefined {
    return this.functionFrame.result;
  }

  get result(): string | undefined {
    return this.functionResult?.result;
  }

  get status(): protocol.FunctionStatusEnum | undefined {
    return this.functionResult?.status;
  }

  get followup(): string | undefined {
    return this.thread.followup;
  }

  get followupOnlyIfShowing(): string | undefined {
    return this.showFollowup ? this.followup : undefined;
  }

  get readFiles(): string[] | undefined {
    return this.thread.readFiles;
  }

  get readUrls(): string[] | undefined {
    return this.thread.readUrls;
  }

  get requiresPermission(): boolean {
    return this.functionFrame.requiresPermission;
  }

  get anyRequiresPermission(): boolean {
    return this.thread.framesWhichRequirePermission !== undefined;
  }

  get hasReadFiles(): boolean {
    return this.readFiles !== undefined;
  }

  get hasReadUrls(): boolean {
    return this.readUrls !== undefined;
  }

  get hasSources(): boolean {
    return this.hasReadFiles || this.hasReadUrls;
  }

  get framesRequiringPermission(): protocol.FunctionFrame[] | undefined {
    return this.thread.framesWhichRequirePermission;
  }

  get showFollowup(): boolean | undefined {
    return this.functionFrame.showFollowup;
  }
}
