import fetch from 'node-fetch';
import {IdxAgentSettings} from '../idx-agent-settings';

export class WebSearchEngine {
  constructor(public idxAgentSettings: IdxAgentSettings) {}

  async webSearch(query: string): Promise<string> {
    let encodedQuery = encodeURIComponent(query);
    let response = await fetch(
      `${this.idxAgentSettings.webSearchUrl}?cx=${this.idxAgentSettings.webSearchCx}&q=${encodedQuery}&key=${this.idxAgentSettings.webSearchApiKey}`,
      {
        method: 'GET',
        headers: {
          Accept: 'application/json',
        },
      }
    );
    let chunks = [];
    for await (const chunk of response.body!) {
      chunks.push(Buffer.from(chunk));
    }
    return Buffer.concat(chunks).toString();
  }
}
