import * as api from '../../api';

// A signal to to the PlaybackLLM to throw, useful for testing request error handling.
export const errorSentinel: api.GenerateContentResponse = undefined as any;

export type Response = api.GenerateContentResponse[];

export function apiResponse(part: api.Part): api.GenerateContentResponse {
  return {candidates: [{content: {parts: [part], role: 'model'}}]};
}

function message(part: api.Part): Response {
  return [apiResponse(part)];
}

export function textMessage(text: string): Response {
  return message({text: text});
}

export function streamingTextMessage(texts: string[]): Response {
  const responses: api.GenerateContentResponse[] = [];
  for (const text of texts) {
    responses.push(apiResponse({text: text}));
  }
  return responses;
}

export function functionCall(name: api.FunctionName, args: api.FunctionCallArgs): Response {
  return message({
    functionCall: {
      name: name,
      args: args,
    },
  });
}

export function readFile(path: string): Response {
  return functionCall('read_file', {path: path});
}

export function replyToUser(reply: string): Response {
  return functionCall('reply_to_user' as api.FunctionName, {reply: reply});
}

export function runTerminalCommand(command: string): Response {
  return functionCall('run_terminal_command', {command: command});
}

export function writeFile(path: string, content: string): Response {
  return functionCall('write_file', {path: path, content: content});
}
