import * as vscode from 'vscode';
import {UserChatHistory, ChatMessage} from '~common/messaging/modules/aichat/aichat-protocol';
import {createPreambleFromPrompt, getIndexedFiles, isRetrievalEnabled} from './retrieval';
import {ExtensionContext} from './context';
import {mergeWithExistingCodeBlockIfPresent} from './util';

const monospaceextension = vscode.extensions.getExtension('google.monospace');

interface AidaStreamingRpc {
  responseFromAida(
    ctx: ExtensionContext,
    prompt: string,
    slashCommand: string | undefined,
    chatHistory: UserChatHistory,
    aidaObservable: AidaObservable
  ): Promise<void>;
}

export function acquireStreamingRpc(type: 'streaming-conversation'): AidaStreamingRpc {
  if (type === 'streaming-conversation') {
    return new StreamingConversationRpc();
  } else {
    throw new Error('Invalid type');
  }
}

//Conversation API

export interface Citation {
  startIndex: number;
  endIndex: number;
  repository: string;
  license: string;
  uri: string;
}

export interface AidaChatHistory {
  text: string;
  entity: 'user' | 'system';
}

export interface AidaFact {
  text: string
}

export interface AidaConversationResponse {
  code?: string;
  text?: string;
  customResponse?: string;
  citations?: Citation[];
  rpcGlobalId?: string;
}

export interface AidaGenerateResponse {
  summary: string;
  citations?: Citation[];
  rpcGlobalId?: string;
}

//Streaming API
export interface AidaObservable {
  chatHistoryUpdate(response: UserChatHistory): void;
  error(e: Error): void;
  done(): void;
}

interface Observer<T> {
  next(response: T): void;
  error(e: Error): void;
  done(): void;
}

interface Unsubscribable {
  unsubscribe(): void;
}

export interface Observable<T> {
  subscribe(observer: Observer<T>): Unsubscribable;
}

export class StreamingConversationRpc implements AidaStreamingRpc {
  async responseFromAida(
    ctx: ExtensionContext,
    prompt: string,
    slashCommand: string | undefined,
    chatHistory: UserChatHistory,
    aidaObservable: AidaObservable
  ): Promise<void> {
    let preamble = '';
    let facts = '';
    let indexedRepoFiles: string[] = [];

    const useSelfawareFacts = slashCommand === 'idx';

    if (isRetrievalEnabled()) {
      indexedRepoFiles = await getIndexedFiles();
      ({preamble, facts} = await createPreambleFromPrompt(ctx, prompt, true, useSelfawareFacts));
    } else {
      ({preamble, facts} = await createPreambleFromPrompt(ctx, prompt, false, useSelfawareFacts));
    }

    //Remove the last entry of history if it's equal to user
    //See b/305088892
    let chatMessagesToSend = [...chatHistory];
    if (
      chatMessagesToSend.length > 0 &&
      chatMessagesToSend[chatMessagesToSend.length - 1].speaker === 'user'
    ) {
      chatMessagesToSend.pop();
    }

    let chatResponse = '';
    let rpcGlobalId: any = '0';
    //Do a constant update for streaming and then a final update with recitations
    const conversationResponses: AidaConversationResponse[] = [];
    const allCitations: Citation[] = [];

    const dedupe = (citations: Citation[]): Citation[] => {
      const seen: {[key: string]: boolean} = {};
      const dedupedArray: Citation[] = [];

      for (const citation of citations) {
        const key = `${citation.repository}-${citation.license}`;

        if (!seen[key]) {
          seen[key] = true;
          dedupedArray.push(citation);
        }
      }

      return dedupedArray;
    };

    const whenDone = () => {
      chatResponse = '';
      let startIndex = 0;

      conversationResponses.forEach(response => {
        rpcGlobalId = response.rpcGlobalId;
        const textForCurrent = isNotEmpty(response.code) ? response.code : response.text;
        if (!textForCurrent) {
          return;
        }
        const endIndex = startIndex + textForCurrent.length;

        //Find citations for this code
        const filteredCitations = allCitations.filter(
          citation => startIndex <= citation.endIndex && endIndex >= citation.startIndex
        );
        const citationStr = dedupe(filteredCitations)
          .map(citation => `$citation:${JSON.stringify(citation)}`)
          .join('\n');

        chatResponse = isNotEmpty(response.code)
          ? mergeWithExistingCodeBlockIfPresent(chatResponse, response.code!, citationStr)
          : `${chatResponse}${response.text}`;
        startIndex = endIndex + 1;
      });
      const chatHistoryItem: ChatMessage = {
        speaker: 'ai',
        body: chatResponse,
        fileReferences: indexedRepoFiles.filter(f => chatResponse.indexOf(f) >= 0),
        rpcGlobalId: rpcGlobalId,
        slashCommand,
      };
      aidaObservable.chatHistoryUpdate([...chatHistory, chatHistoryItem]);
    };

    let firstResponse = true;
    let startTime = new Date();
    const observer: Observer<AidaConversationResponse> = {
      next: function (response: AidaConversationResponse): void {
        let responseTime = new Date();
        let duration = responseTime.getTime() - startTime.getTime();
        if (firstResponse) {
          firstResponse = false;
          logLatencyEvent(
            'aichat.conversation.first_received',
            duration,
            startTime,
            responseTime,
            ''
          );
        }
        if (!response) {
          whenDone();
          logLatencyEvent(
            'aichat.conversation.last_received',
            duration,
            startTime,
            responseTime,
            'done'
          );
          aidaObservable.done();
          return;
        }

        chatResponse = isNotEmpty(response.code)
          ? mergeWithExistingCodeBlockIfPresent(chatResponse, response.code!)
          : `${chatResponse}${response.text}`;
        const chatHistoryItem: ChatMessage = {
          speaker: 'ai',
          body: chatResponse,
          rpcGlobalId: response.rpcGlobalId,
          slashCommand,
        };
        conversationResponses.push(response);
        if (response.citations) {
          allCitations.push(...response.citations);
        }
        aidaObservable.chatHistoryUpdate([...chatHistory, chatHistoryItem]);
      },
      error: function (e: Error): void {
        let responseTime = new Date();
        let duration = responseTime.getMilliseconds() - startTime.getMilliseconds();
        logLatencyEvent(
          'aichat.conversation.last_received',
          duration,
          startTime,
          responseTime,
          'error'
        );
        console.log('**Error');
        console.log(e);
        whenDone();
        aidaObservable.done();
      },
      done: function (): void {
        whenDone();
        let responseTime = new Date();
        let duration = responseTime.getMilliseconds() - startTime.getMilliseconds();
        logLatencyEvent(
          'aichat.conversation.last_received',
          duration,
          startTime,
          responseTime,
          'done'
        );
        console.log('done');
        aidaObservable.done();
      },
    };

    const observable = (await ctx.idx?.exports.doStreamingConversation(
      prompt,
      chatMessagesToSend.map(history => {
        return {
          text: history.body ?? '',
          entity: history.speaker === 'ai' ? 'system' : 'user',
        };
      }),
      preamble,
      facts ? [{text: facts}] : undefined
    )) as Observable<AidaConversationResponse>;
    //Todo(ksat): Unsubscribe after a timeout
    observable.subscribe(observer);
  }
}

function logLatencyEvent(
  action: string,
  elapsedTime: number,
  start_time: Date,
  end_time: Date,
  details: string
) {
  try {
    monospaceextension?.exports.logLatencyEvent(action, elapsedTime, start_time, end_time, details);
  } catch (e) {
    console.log('Telemetry logging error: ' + e);
  }
}

//Utility methods
function isNotEmpty(str?: string): boolean {
  return !isEmpty(str);
}
function isEmpty(str?: string): boolean {
  return str === undefined || str === null || str.trim() === '';
}
