import * as vscode from 'vscode';
import {TextEncoder} from 'util';
import {FunctionState} from './external-function';
import {fileUriExists, relativePathToWorkspaceUri} from '../../file-utils';

export async function openDiffView(functionState: FunctionState) {
  const {content, path} = functionState.args;
  const uri = relativePathToWorkspaceUri(path);
  const doesFileExist = uri !== undefined && (await fileUriExists(uri));

  const uriTo = vscode.Uri.file(`/tmp/${Date.now()}.suggested.${path}`);
  await vscode.workspace.fs.writeFile(uriTo, new TextEncoder().encode(content));

  let uriFrom = uri;
  if (!doesFileExist) {
    uriFrom = vscode.Uri.file(`/tmp/${Date.now()}.blank.${path}`);
    await vscode.workspace.fs.writeFile(uriFrom, new TextEncoder().encode(''));
  }

  // TODO(julien): Add functionality for editing the diff view and saving the changes
  vscode.commands.executeCommand('vscode.diff', uriFrom, uriTo, `${path} changes`);
}
