import * as path from 'path';
import * as fs from 'fs';
import {window} from 'vscode';

export const enum LogLevel {
  NONE = -1,
  ERROR = 0,
  WARN = 1,
  INFO = 2,
  DEBUG = 3,
}

export interface Logger {
  error(message: string): void;
  warn(message: string): void;
  info(message: string): void;
  debug(message: string): void;
}

export class OutputChannel implements Logger {
  constructor(
    public name: string,
    public level: LogLevel = 0,
    private channel = window.createOutputChannel(name)
  ) {}

  error(message: string): void {
    this.log(`[ERROR] ${message}`, LogLevel.ERROR);
  }

  warn(message: string): void {
    this.log(`[WARN] ${message}`, LogLevel.WARN);
  }

  info(message: string): void {
    this.log(`[INFO] ${message}`, LogLevel.INFO);
  }

  debug(message: string): void {
    this.log(`[DEBUG] ${message}`, LogLevel.DEBUG);
  }

  log(message: string, logLevel: LogLevel) {
    if (this.level >= logLevel) {
      this.channel.appendLine(message);
    }
  }
}

export class FileLogger {
  tee: OutputChannel | undefined;
  fileDescriptor: number | undefined;

  constructor(public file: string, public level: LogLevel = 0, teeName?: string) {
    if (teeName) {
      this.tee = new OutputChannel(teeName, level);
    }
  }

  createFileDescriptor() {
    const dirname = path.dirname(this.file);
    fs.mkdirSync(dirname, {recursive: true});
    fs.rmSync(this.file, {force: true});
    this.fileDescriptor = fs.openSync(this.file, 'a+');
  }

  error(prefix: string, payload: any): void {
    this.log('[ERROR]', prefix, payload, LogLevel.ERROR);
  }

  warn(prefix: string, payload: any): void {
    this.log('[WARN]', prefix, payload, LogLevel.WARN);
  }

  info(prefix: string, payload: any): void {
    this.log('[INFO]', prefix, payload, LogLevel.INFO);
  }

  debug(prefix: string, payload: any): void {
    this.log('[DEBUG]', prefix, payload, LogLevel.DEBUG);
  }

  private log(key: string, prefix: string, payload: any, logLevel: LogLevel) {
    if (this.level >= logLevel) {
      if (!this.fileDescriptor) {
        this.createFileDescriptor();
      }
      const logString = `${key} ${prefix}${JSON.stringify(payload, null, 2)}\n`;
      fs.writeSync(this.fileDescriptor!, logString);
      this.tee?.log(logString, logLevel);
    }
  }
}
