import fetch from 'node-fetch';
import * as vscode from 'vscode';
import {
  CONFIG_AI_ENABLE_RETRIEVAL,
  CONFIG_AI_INDEXSERVER_MAX_DISTANCE,
  CONFIG_AI_INDEXSERVER_MAX_RESULTS,
  CONFIG_AI_INDEXSERVER_PORT,
  CONFIG_AI_USE_LINE_NUMBERS_IN_RETRIEVAL,
} from 'vscode-shared/config';
import {ExtensionContext} from './context';
import {outsideOfIDX} from './aichat';
import {getProperty} from './vscode-utils';

const INDEXSERVER_PORT = indexServerPort();
export const INDEXSERVER_BASE_URL = `http://localhost:${INDEXSERVER_PORT}`;

const SNIPPET_WRAP_IN_TICKS = true;
let SNIPPET_LINE_NUMBERS = getProperty<boolean>(CONFIG_AI_USE_LINE_NUMBERS_IN_RETRIEVAL, true)

// indexserver has its own filters as well
const DEFAULT_MAX_DISTANCE = getProperty<number>(CONFIG_AI_INDEXSERVER_MAX_DISTANCE, 0.9);
const DEFAULT_MAX_RESULTS = getProperty<number>(CONFIG_AI_INDEXSERVER_MAX_RESULTS, 3);

let debugChannel = vscode.window.createOutputChannel('Gemini - Retrieval Debugging');

// TODO: bring in this type definition from indexserver
interface SearchResult {
  snippet: string;
  contentRoot: string;
  sourceFile?: string;
  startLine?: number;
  distance: number;
}

interface EditorSnippet {
  filename: string;
  snippet: string;
  startLine: number;
  sourceDescription: string;
}

interface EditorContext {
  activeEditorSnippets: EditorSnippet[];
}

/**
 * Does the user/workspace/etc have retrieval enabled?
 */
export function isRetrievalEnabled(scope?: vscode.Uri): boolean {
  let {section, property} = CONFIG_AI_ENABLE_RETRIEVAL;
  return !!vscode.workspace.getConfiguration(section, scope).get(property);
}

export function indexServerPort(): number {
  const {section, property} = CONFIG_AI_INDEXSERVER_PORT;
  // TODO: make this dynamically configurable or discoverable;
  return vscode.workspace.getConfiguration(section).get(property) ?? 9401;
}

/**
 * Setup related to augmented retrieval / codebase indexing
 */
export function setupAiRetrieval(context: vscode.ExtensionContext) {
  let updateWorkspaceFolderIndexing = () => {
    for (let wf of vscode.workspace.workspaceFolders || []) {
      if (wf.uri.scheme !== 'file') {
        continue;
      }

      if (!isRetrievalEnabled(wf.uri)) {
        stopAndDeleteCurrentIndexer(wf.uri.fsPath); // dont wait on the promise
        continue;
      }

      startIndexerForContentRoot(wf.uri.fsPath); // dont wait on the promise
    }
  };

  updateWorkspaceFolderIndexing();

  context.subscriptions.push(
    vscode.workspace.onDidChangeConfiguration(e => {
      if (e.affectsConfiguration(CONFIG_AI_ENABLE_RETRIEVAL.fullId)) {
        updateWorkspaceFolderIndexing();
      }
      if (e.affectsConfiguration(CONFIG_AI_USE_LINE_NUMBERS_IN_RETRIEVAL.fullId)) {
        SNIPPET_LINE_NUMBERS = getProperty(CONFIG_AI_USE_LINE_NUMBERS_IN_RETRIEVAL, true)
      }
    })
  );

  context.subscriptions.push(
    vscode.workspace.onDidChangeWorkspaceFolders(() => {
      updateWorkspaceFolderIndexing();
    })
  );
}

export interface RetrievedSnippets {
  relevantSnippets?: string[];
  activeEditorSnippets: string[];
  searchResults: SearchResult[];
}

export async function retrieveSnippets(
  ctx: ExtensionContext,
  prompt: string,
  sendSnippets: boolean,
  maxDistance?: number,
  maxResults?: number
): Promise<RetrievedSnippets> {
  let relevantSnippets: string[] | undefined;
  let searchResults: SearchResult[] = [];
  if (sendSnippets) {
    searchResults = await searchForRelevantSnippets(prompt, maxDistance, maxResults);
    relevantSnippets = searchResults.map(({snippet, startLine, sourceFile}) =>
      formatSnippet(snippet, {
        startLine,
        heading: `From ${sourceFile} in local codebase`,
      })
    );
  }

  const activeEditorSnippets = (await getEditorContext(ctx)).activeEditorSnippets.map(
    ({snippet, startLine, sourceDescription}) =>
      formatSnippet(snippet, {
        startLine,
        heading: sourceDescription,
      })
  );
  return {
    relevantSnippets: relevantSnippets,
    activeEditorSnippets: activeEditorSnippets,
    searchResults: searchResults,
  };
}

/**
 * Take the given prompt from the user and augment it with both the current IDE editor context
 * and semantic search results across the user's codebase (powered by a local indexserver)
 */
export async function createPreambleFromPrompt(
  ctx: ExtensionContext,
  prompt: string,
  sendSnippets: boolean,
  useSelfawareFacts: boolean
): Promise<{preamble: string; facts: string}> {
  const retrievedSnippets = await retrieveSnippets(ctx, prompt, sendSnippets);
  const preamble = await ctx.engine.hydrate(ctx.prompts.preamble().content, {});
  const facts = useSelfawareFacts
    ? await ctx.engine.hydrate(ctx.prompts.selfawareFacts().content, {
        prompt: prompt,
        relevantSnippets: retrievedSnippets.relevantSnippets?.join('\n\n'),
        activeEditorSnippets: retrievedSnippets.activeEditorSnippets,
      })
    : await ctx.engine.hydrate(ctx.prompts.facts().content, {
        relevantSnippets: retrievedSnippets.relevantSnippets?.join('\n\n'),
        activeEditorSnippets: retrievedSnippets.activeEditorSnippets,
      });
  debugChannel.appendLine(preamble.prompt);
  if (sendSnippets) {
    const searchResults = retrievedSnippets.searchResults;
    if (searchResults.length) {
      debugChannel.appendLine(
        `Sources (lower score = more relevant):\n${searchResults
          .map(({distance, sourceFile}) => `- (${distance.toFixed(3)}) ${sourceFile}`)
          .join('\n')}`
      );
    } else {
      debugChannel.appendLine(`No documents matched`);
    }
  }

  return {preamble: preamble.prompt, facts: facts.prompt};
}

/**
 * Initializes the indexserver for the given contentRoot
 */
async function startIndexerForContentRoot(root: string): Promise<void> {
  try {
    await fetch(`${INDEXSERVER_BASE_URL}/indexers/start`, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({root}),
    }).then(r => r.json());
  } catch (e) {
    debugChannel.appendLine(`Couldn't start indexer for ${root}`);
  }
}

/**
 * Stops all indexers and deletes any indexes that exist
 */
async function stopAndDeleteAllIndexers(): Promise<void> {
  try {
    await fetch(`${INDEXSERVER_BASE_URL}/indexers/stop-all`, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({delete: true}),
    }).then(r => r.json());
  } catch (e) {
    debugChannel.appendLine(`Couldn't stop and delete indexers`);
  }
}

/**
 * Stops indexer and deletes index
 */
async function stopAndDeleteCurrentIndexer(contentRoot: string): Promise<void> {
  try {
    await fetch(`${INDEXSERVER_BASE_URL}/indexers/stop`, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({delete: true, root: contentRoot}),
    }).then(r => r.json());
  } catch (e) {
    debugChannel.appendLine(`Couldn't stop and delete indexers`);
  }
}

/**
 * Semantic search... query indexserver for files that match a given query.
 */
async function searchForRelevantSnippets(
  query: string,
  maxDistance: number | undefined,
  maxResults: number | undefined
): Promise<SearchResult[]> {
  let root = getContentRootsForIndexServer()?.[0] || '';

  // TODO: this needs support for multiple content roots, and likely should return absolute paths.
  if (!root) {
    return [];
  }

  try {
    let {results} = (await fetch(`${INDEXSERVER_BASE_URL}/search`, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        roots: [root],
        query,
        maxDistance: maxDistance ?? DEFAULT_MAX_DISTANCE,
        maxResults: maxResults ?? DEFAULT_MAX_RESULTS,
      }),
    }).then(r => r.json())) as {results: SearchResult[]};
    return results;
  } catch (e) {
    debugChannel.appendLine('Error communicating with indexserver: ' + String(e));
    return [];
  }
}

/**
 * Gets editor context to pass to the LLM, unrelated to vector search / embeddings
 */
async function getEditorContext(extensionContext: ExtensionContext): Promise<EditorContext> {
  let context: EditorContext = {activeEditorSnippets: []};

  for (let editor of vscode.window.visibleTextEditors) {
    if (editor.document.uri?.scheme !== 'file') {
      // skip output (logs)
      continue;
    }

    if (await extensionContext.aiExclude.isExcluded(editor.document.uri)) {
      continue;
    }

    let filePath = vscode.workspace.asRelativePath(editor.document.fileName);
    let sourceDescription = `The user is currently editing this part of a file named \`${
      filePath || 'Untitled file'
    }\``;
    if (!editor.selection.isEmpty) {
      // has selected text - send that
      let range = editor.selection;
      context.activeEditorSnippets.push({
        filename: filePath,
        snippet: editor.document.getText(range),
        startLine: range.start.line + 1,
        sourceDescription, // don't customize for now
      });
    } else if (editor.visibleRanges.length) {
      // no selected text, use the visible range
      let range = editor.visibleRanges[0];
      context.activeEditorSnippets.push({
        filename: filePath,
        snippet: editor.document.getText(range),
        startLine: range.start.line + 1,
        sourceDescription, // don't customize for now
      });
    }
  }

  return context;
}

/**
 * Forms code snippets for injection into prompt
 */
function formatSnippet(
  snippet: string,
  {startLine, heading}: {startLine?: number; heading?: string} = {}
) {
  const formatLine = SNIPPET_LINE_NUMBERS
    ? (line: string, idx: number) =>
        (startLine ? `Line ${startLine + idx}:`.padEnd(10, ' ') : '') + '    ' + line
    : (line: string) => line;
  snippet = snippet
    .split('\n')
    .slice(1) // remove the first line, which is always "File: ...." (see go.mjs)
    .map(formatLine)
    .join('\n');
  if (SNIPPET_WRAP_IN_TICKS) {
    snippet = '```\n' + snippet.replace(/`/g, '\\`') + '\n```';
  }
  if (heading) {
    snippet = heading + ':\n' + snippet;
  }
  return snippet;
}

/**
 * Template literal-based helper for prompt formatting
 */
function p(parts: TemplateStringsArray, ...substitutions: any[]) {
  let p = [];
  for (let i = 0; i < parts.length; i++) {
    p.push(parts[i]);
    if (i < substitutions.length) {
      let v = substitutions[i] || '';
      if (Array.isArray(v)) {
        v = v.join('');
      }
      p.push(v.trim());
    }
  }
  return p.join('').trim().replace(/\n\n+/g, '\n\n');
}

/**
 * Simply return a list of all indexed files; this is used for rendering links
 * to files in the project
 */
export async function getIndexedFiles(): Promise<string[]> {
  let root = getContentRootsForIndexServer()?.[0] || '';

  // TODO: this needs support for multiple content roots, and likely should return absolute paths.
  if (!root) {
    return [];
  }

  try {
    let {files} = (await fetch(`${INDEXSERVER_BASE_URL}/indexed-files`, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({root}),
    }).then(r => r.json())) as {files: string[]};
    return files;
  } catch (e) {
    debugChannel.appendLine('Error communicating with indexserver: ' + String(e));
    return [];
  }
}

function getContentRootsForIndexServer(): string[] {
  // TODO: handle virtual workspaces
  return vscode.workspace.workspaceFolders?.map(w => w.uri.fsPath) || [];
}
