import {AICHAT_EVENTS, logFunctionCallExecution} from '../telemetry';
import * as api from './api';
import * as protocol from '~common/messaging/modules/aichat/aichat-protocol';
import {ExternalFunction, FunctionAction, FunctionState} from './functions/external-function';

export class FunctionManager {
  functionMap: Record<string, ExternalFunction> = {};
  toolDefinitions: api.FunctionDeclaration[] = [];

  constructor(public functions: ExternalFunction[]) {
    this.buildFunctionActions();
    this.buildToolDefinitions();
  }

  buildFunctionActions() {
    this.functionMap = {};
    for (const f of this.functions) {
      this.functionMap[f.name] = f;
    }
  }

  buildToolDefinitions() {
    this.toolDefinitions = [];
    for (const f of this.functions) {
      this.toolDefinitions.push({
        name: f.name,
        description: f.description,
        parameters: f.parameters,
      });
    }
  }

  getFunction(name: string): ExternalFunction {
    return this.functionMap[name];
  }

  getFunctionFromCall(functionCall: api.FunctionCall): ExternalFunction {
    return this.getFunction(functionCall.name);
  }

  requiresPermission(functionCall: api.FunctionCall) {
    return this.getFunctionFromCall(functionCall).requiresPermission;
  }

  recursivelyValidate(args: any, schema: api.ValueSchema, retryMessages: string[]): boolean {
    switch (schema.type) {
      case 'string':
        if (
          'format' in schema &&
          schema.format === 'enum' &&
          (typeof args !== 'string' || schema.enum.indexOf(args) === -1)
        ) {
          retryMessages.push(
            'Ensure enum values to function calls are one of the values specified in the schema.'
          );
          return false;
        } else if (typeof args !== 'string') {
          retryMessages.push('Ensure string values to function calls are strings.');
          return false;
        }
        break;
      case 'integer':
        if (typeof args !== 'number' || !Number.isInteger(args)) {
          retryMessages.push('Ensure integer values to function calls are integers.');
          return false;
        }
        break;
      case 'number':
        if (typeof args !== 'number') {
          retryMessages.push('Ensure number values to function calls are numbers.');
          return false;
        }
        break;
      case 'boolean':
        if (typeof args !== 'boolean') {
          retryMessages.push('Ensure boolean values to function calls are booleans.');
          return false;
        }
        break;
      case 'array':
        if (!Array.isArray(args)) {
          retryMessages.push('Ensure array values to function calls are arrays.');
          return false;
        }
        const items = (schema as api.ArraySchema).items;
        if (items !== undefined) {
          for (const arg of args) {
            if (!this.recursivelyValidate(arg, items, retryMessages)) {
              return false;
            }
          }
        }
        break;
      case 'object':
        if (typeof args !== 'object' || Array.isArray(args)) {
          retryMessages.push('Ensure object values to function calls are objects.');
          return false;
        }

        let isValid = true;
        const names = Object.getOwnPropertyNames(args);
        const requiredNames = schema.required;
        if (requiredNames !== undefined) {
          for (const name of requiredNames) {
            if (names.indexOf(name) === -1) {
              retryMessages.push(
                'Ensure object values to function calls have all of the required names.'
              );
              isValid = false;
            }
          }
        }
        if (schema.properties !== undefined) {
          for (const name of names) {
            const arg = args[name];
            const propertyScheme = schema.properties[name];
            if (propertyScheme === undefined) {
              retryMessages.push(
                'Ensure object values to function calls do not have unnecessary names.'
              );
              isValid = false;
            } else if (!this.recursivelyValidate(arg, propertyScheme, retryMessages)) {
              isValid = false;
            }
          }
        }
        return isValid;
    }
    return true;
  }

  validateFunctionCall(functionCall: api.FunctionCall): api.ValidationResult {
    const functionName = functionCall.name;
    if (functionName === undefined) {
      return api.validationFailure('Ensure function calls have a name.');
    }

    const theFunction = this.functionMap[functionName];
    if (theFunction === undefined) {
      return api.validationFailure(`Only call the functions provided.`);
    }

    const args = functionCall.args;
    if (theFunction.parameters === undefined) {
      if (args === undefined) {
        return api.validationSuccess();
      } else {
        return api.validationFailure(`Ensure a call to ${functionName} has no arguments.`);
      }
    } else if (args !== undefined) {
      const retryMessages: string[] = [];
      if (this.recursivelyValidate(args, theFunction.parameters!, retryMessages)) {
        return api.validationSuccess();
      } else {
        return api.validationFailures(retryMessages);
      }
    } else {
      return api.validationFailure(`Ensure a call to ${functionName} has the necessary arguments.`);
    }
  }

  updateFunctionArgs(functionCall: api.FunctionCall) {
    functionCall.args = this.getFunctionFromCall(functionCall).updateArgs(functionCall.args);
  }

  async updateFunctionMetadata(functionFrame: protocol.FunctionFrame) {
    functionFrame.metadata = await this.getFunctionFromCall(functionFrame.call).populateMetadata?.(
      functionFrame.call.args
    );
  }

  async callFunction(functionState: FunctionState, rpcGlobalId?: string): Promise<void> {
    try {
      await this.getFunction(functionState.name).call(functionState);
      logFunctionCallExecution(
        AICHAT_EVENTS.SUPERCHAT_FUNCTION_SUCCEEDED,
        rpcGlobalId,
        functionState
      );
    } catch (e) {
      console.log(e);
      functionState.failure('system failure');
      logFunctionCallExecution(AICHAT_EVENTS.SUPERCHAT_FUNCTION_FAILED, rpcGlobalId, functionState);
    }
  }

  async performAction(functionState: FunctionState, action: FunctionAction) {
    await this.getFunction(functionState.name).actions[action]!(functionState);
  }
}
