import * as user from '~common/messaging/modules/aichat/aichat-protocol';
import * as internal from './agentive/api';
import {FunctionState} from './agentive/functions/external-function';

export class ChatHistory {
  internalMarks: number[] = [];
  public allowTools = true;

  constructor(
    private userFacing: user.UserChatHistory,
    private internalFacing: internal.Content[]
  ) {}

  get lastInternalMessage(): internal.Content | undefined {
    return this.internalFacing.length === 0
      ? undefined
      : this.internalFacing[this.internalFacing.length - 1];
  }

  get lastInternalPart(): internal.Part | undefined {
    return this.lastInternalMessage?.parts[0];
  }

  get lastInternalFunctionCall(): internal.FunctionCall | undefined {
    return this.lastInternalPart?.functionCall;
  }

  get isEmpty(): boolean {
    return this.userFacing.length === 0;
  }

  get lastUserMessage(): user.ChatMessage | undefined {
    return this.isEmpty ? undefined : this.userFacing[this.userFacing.length - 1];
  }

  get lastFunctionState(): FunctionState | undefined {
    return FunctionState.fromLastFrame(this.lastUserMessage?.functionThreadState);
  }

  clear() {
    this.userFacing = [];
    this.internalFacing = [];
    this.internalMarks = [];
    this.allowTools = true;
  }

  // The truncation logic expects that a user message is pushed first, then
  // all of the external messages associated with that user message are pushed after.
  truncateToPosition(position: number) {
    this.userFacing = this.userFacing.slice(0, position);
    const popTo = this.internalMarks[position];
    this.internalMarks = this.internalMarks.slice(0, position);
    this.internalFacing = this.internalFacing.slice(0, popTo);
  }

  popBack() {
    this.truncateToPosition(this.userFacing.length - 1);
  }

  set(history: user.UserChatHistory) {
    this.userFacing = history;
    this.internalFacing = [];
  }

  get user(): user.UserChatHistory {
    return this.userFacing;
  }

  get internal(): internal.Content[] {
    //If tools are not requested, don't send a preamble
    //(first two messages, Ref: see initializeConversation)
    return this.allowTools ? this.internalFacing : this.internalFacing.slice(2);
  }

  pushUserMessage(message: user.ChatMessage) {
    this.userFacing.push(message);
    this.internalMarks.push(this.internalFacing.length);
  }

  pushInternalMessage(message: internal.Content) {
    this.internalFacing.push(message);
  }
}
