import {ChatHistory} from '../chat-history';
import * as api from './api';
import {LLM} from './engines/llm';
import {FunctionManager} from './function-manager';
import {logRetry, logRetryFailed} from '../telemetry';
import {FileLogger} from '../logger';
import {IdxAgentSettings} from './idx-agent-settings';
import {FunctionState} from './functions/external-function';
import {randomInt, sleep} from '../util';
import {AidaCredentials} from './conversational-agent';

// Generated by Gemini.
// TODO(joshualitt): Having hardcoded English strings in source files is not ideal.
const retryErrorStrings = [
  "Could you elaborate on that a bit more? I'm not sure I understood.",
  "I didn't quite understand that. Could you try rephrasing that?",
  'Could you try rephrasing that with a bit more detail?',
  "Sorry, I'm not sure I follow. Could you try rephrasing that?",
];

const abortedErrorString = 'aborted';
const unknownErrorString = 'IDX AI received an error, please try again later.';

export class ApiCallHelper {
  candidateRetries: number;
  initialCandidateRetries: number;
  originalUserPreamble!: string;
  originalAgentPreamble!: string;
  originalLastUserMessage!: string;
  failedResponses: api.GenerateContentResponse[] = [];

  constructor(
    public idxAgentSettings: IdxAgentSettings,
    private readonly disableContentLogging: boolean,
    public fileLogger: FileLogger | undefined,
    public functionManager: FunctionManager,
    public llm: LLM,
    public systemInstruction: string | undefined,
    public facts: api.Fact[] | undefined,
    public history: ChatHistory,
    public functionState: FunctionState | undefined,
    public lastKnownGoodPoint: number,
    public aidaCredentials: AidaCredentials | undefined,
    public handleValidResponse: (
      functionState: FunctionState | undefined,
      response: api.GenerateContentResponse,
      isChunk: boolean
    ) => Promise<void>,
    public notifyChatHistory: () => void,
    public allowLastFunctionCall: (functionState: FunctionState) => Promise<void>,
    public abortSignal?: AbortSignal
  ) {
    this.candidateRetries = this.initialCandidateRetries = this.idxAgentSettings.candidateRetries;
    // For AIDA we put corrections on the most recent user message.
    if (this.idxAgentSettings.urlIsAida) {
      this.originalLastUserMessage = this.history.internal[lastKnownGoodPoint].parts[0].text!;
    } else {
      this.originalUserPreamble = this.history.internal[0].parts[0].text!;
      this.originalAgentPreamble = this.history.internal[1].parts[0].text!;
    }
  }

  validateResponse(response: api.GenerateContentResponse | undefined): api.ValidationResult {
    if (response === undefined) {
      return api.validationFailure('Ensure a response is generated.');
    }

    const candidates = response.candidates;
    if (candidates === undefined || candidates.length === 0) {
      return api.validationFailure('Ensure candidates are generated.');
    }

    if (!this.idxAgentSettings.urlIsAida && candidates.length > 1) {
      return api.validationFailure('Ensure only text or a function call but not both.');
    }

    const message = response.candidates[0].content;
    if (message === undefined) {
      return api.validationFailure('Ensure content is generated.');
    }

    if (message.parts === undefined || message.parts[0] === undefined) {
      return api.validationFailure('Ensure parts are generated.');
    }

    const externalPart = message.parts[0];
    const functionCall = externalPart?.functionCall;
    if (functionCall) {
      return this.functionManager.validateFunctionCall(functionCall);
    } else if (externalPart.text) {
      return api.validationSuccess();
    } else {
      return api.validationFailure('Ensure one of either a function call or text is generated.');
    }
  }

  restoreHistoryIfNecessary() {
    if (this.candidateRetries < this.initialCandidateRetries) {
      if (this.idxAgentSettings.urlIsAida) {
        this.history.internal[this.lastKnownGoodPoint].parts[0].text = this.originalLastUserMessage;
      } else {
        this.history.internal[0].parts[0].text = this.originalUserPreamble;
        this.history.internal[1].parts[0].text = this.originalAgentPreamble;
      }
    }
  }

  async handleRetryFailure(errorMessage: string, rpcGlobalId: string | undefined) {
    this.restoreHistoryIfNecessary();
    logRetryFailed(rpcGlobalId);

    // If we haven't yet responded to the user at all, insert a message with a generic error
    // message. If we've already started a function thread, then simply add a follow on with the
    // failure. Otherwise, set the text of the message to the error message.
    //
    // For now, we keep the error message generic because it is unlikely it will be helpful to
    // users. We can always revisit this if we think there is value in surfacing specific error
    // codes.
    const lastUserMessage = this.history.lastUserMessage!;
    if (lastUserMessage.speaker === 'user') {
      this.history.pushUserMessage({
        speaker: 'ai',
        body: errorMessage,
      });
    } else if (lastUserMessage.functionThreadState) {
      lastUserMessage.functionThreadState.followup = errorMessage;
    } else {
      lastUserMessage.body = errorMessage;
    }

    // Putting errors into the internal history can pattern the model, and it
    // will start returning error messages on its own. Instead, we reset the
    // internal history to the last known good point in the conversation.
    this.history.internal.length = this.lastKnownGoodPoint;
    this.notifyChatHistory();
  }

  handleStreamFailure() {
    const lastUserMessage = this.history.lastUserMessage;
    if (lastUserMessage?.speaker === 'ai') {
      this.history.popBack();
      this.notifyChatHistory();
    }
  }

  handleRetry(
    request: api.GenerateContentRequest,
    response: api.GenerateContentResponse | undefined,
    isChunk: boolean,
    corrections: string[]
  ) {
    if (isChunk) {
      this.handleStreamFailure();
    }
    this.fileLogger?.warn('Request with error:\n', request);
    this.fileLogger?.warn('Response with error:\n', response);
    logRetry(response?.responseMetadata?.rpcGlobalId);
    this.failedResponses.push(response!);

    // Update the preamble with corrections.
    if (this.idxAgentSettings.urlIsAida) {
      const currentLastUserMessage = this.history.internal[this.lastKnownGoodPoint].parts[0].text;
      const newLastUserMessage = `${currentLastUserMessage} ${corrections.join(' ')}`;
      this.history.internal[this.lastKnownGoodPoint].parts[0].text = newLastUserMessage;
    } else {
      const currentUserPreamble = this.history.internal[0].parts[0].text;
      const currentAgentPreamble = this.history.internal[1].parts[0].text;
      const newUserPreamble = `${currentUserPreamble} ${corrections.join(' ')}`;
      const newAgentPreamble = `${currentAgentPreamble} ${corrections.join(' ')}`;
      this.history.internal[0].parts[0].text = newUserPreamble;
      this.history.internal[1].parts[0].text = newAgentPreamble;
    }
  }

  async handleResponse(
    request: api.GenerateContentRequest,
    response: api.GenerateContentResponse | undefined,
    isChunk: boolean
  ): Promise<boolean> {
    const result = this.validateResponse(response);
    const succeeded = result.status === api.ValidationResultStatus.Success;
    if (succeeded) {
      this.restoreHistoryIfNecessary();
      await this.handleValidResponse(this.functionState, response!, isChunk);
    } else if (this.candidateRetries === 0) {
      this.fileLogger?.error('Request with failure:\n', request);
      this.fileLogger?.error('Response with failure:\n', response);
      this.handleRetryFailure(
        retryErrorStrings[randomInt(retryErrorStrings.length - 1)],
        response?.responseMetadata?.rpcGlobalId
      );
    } else {
      this.handleRetry(
        request,
        response,
        isChunk,
        (result as api.ValidationResultFailure).corrections
      );
    }
    return succeeded;
  }

  getGenerationConfig(): api.GenerationConfig {
    const n = this.initialCandidateRetries;
    const x = this.initialCandidateRetries - this.candidateRetries;
    const t = x / n;
    const temperature = lerp(
      t,
      this.idxAgentSettings.llmMinTemperature,
      this.idxAgentSettings.llmMaxTemperature
    );
    const topK = Math.floor(
      lerp(t, this.idxAgentSettings.llmMinTopK, this.idxAgentSettings.llmMaxTopK)
    );
    const topP = lerp(t, this.idxAgentSettings.llmMinTopP, this.idxAgentSettings.llmMaxTopP);
    const candidateCount = this.idxAgentSettings.candidateCount;
    return {candidateCount, temperature, topK, topP};
  }

  async call(): Promise<boolean> {
    const request: api.GenerateContentRequest = {
      generationConfig: this.llm.getGenerationConfig(this.getGenerationConfig()),
      contents: this.history.internal,
      tools: this.history.allowTools
        ? [{functionDeclarations: this.functionManager.toolDefinitions}]
        : [],
    };

    if (this.idxAgentSettings.urlIsAida) {
      if (this.aidaCredentials !== undefined && this.idxAgentSettings.urlNeedsProjectId) {
        request.requestMetadata = {
          projectId: this.aidaCredentials.projectId!,
          disableContentLogging: this.disableContentLogging,
        };
      }
      request.systemInstruction = {parts: [{text: this.systemInstruction}], role: 'system'};
      if (this.facts && this.facts.length > 0) {
        request.facts = this.facts;
      }
    }

    this.fileLogger?.info('Request:\n', request);
    if (this.idxAgentSettings.useStreaming) {
      const streamingResponse = await this.llm.streamGenerateContent(request, this.abortSignal);
      const streamingResponseReader = streamingResponse.getReader();
      while (true) {
        const {done, value} = await streamingResponseReader.read();
        if (done) {
          return true;
        } else {
          this.fileLogger?.info('Response chunk:\n', value);
          if (!(await this.handleResponse(request, value, true))) {
            return false;
          }
        }
      }
    } else {
      const response = await this.llm.generateContent(
        request,
        this.aidaCredentials?.token,
        this.abortSignal
      );
      this.fileLogger?.info('Response:\n', response);
      if (!(await this.handleResponse(request, response, false))) {
        return false;
      }
    }
    return true;
  }

  async callLoop() {
    // Loop until:
    //   1) The model returns a function call that requires permission, or is the replyToUser tool.
    //   2) The model returns a textual response.
    // Note: Currently, it's possible this could go into an infinite loop. However, until we see
    //       this behavior in the wild, we'd like to keep this logic simple.
    let allowLastFunctionCall: boolean;
    do {
      // Retry in a loop until we get a response from the model that passes validation or run out
      // of retries, in which case we just give up.
      this.candidateRetries = this.initialCandidateRetries;
      while (this.candidateRetries >= 0) {
        if (await this.call()) {
          break;
        } else if (this.candidateRetries === 0) {
          return;
        }
        this.candidateRetries--;
        // Sleep for a brief moment to avoid throttling.
        await sleep(50);
      }
      this.functionState = this.history.lastFunctionState;
      allowLastFunctionCall =
        this.functionState?.requiresPermission === false && this.functionState.stateIs('requested');
      if (allowLastFunctionCall) {
        await this.allowLastFunctionCall(this.functionState!);
      }
    } while (allowLastFunctionCall);
  }

  async wrappedCallLoop() {
    // Wrap the retry loop in an exception handler. The inner loop itself will any errors actually
    // received from the server, so this try / catch is just for otherwise unhandled exceptions,
    // i.e. a problem connecting to the server. We don't currently retry these. We could by adding
    // exponential backoff logic, which would help resolve intermittent connection issues, but
    // because this logic could result in long wait times for the user, we'd need to update the UI
    // to reflect this additional error state.
    try {
      await this.callLoop();
    } catch (err: any) {
      let errorString: string;
      if (this.abortSignal?.aborted) {
        errorString = abortedErrorString;
      } else {
        errorString = unknownErrorString;
        console.error(err);
      }
      this.handleRetryFailure(errorString, undefined);
    }
  }
}

function lerp(t: number, min: number, max: number) {
  return (1 - t) * min + t * max;
}
