import {readFile} from 'fs/promises';
import {posix} from 'path';
import {homedir} from 'os';
import {GlobbyOptions, default as globby} from 'globby';

export const AI_EXCLUDE_FILENAME = '.aiexclude';
export const IGNORE_ALL_PATTERN = '**';
export const ALWAYS_IGNORE = [
  // TODO: better way to list known-binary extensions or mimetypes
  // although the mime-types package flags .ts files as video/*
  '**/*.jpg',
  '**/*.png',
  '**/*.pdf',
  '**/*.gif',
  '**/*.ico',
  '**/*.mp3',
  '**/*.m4a',
  '**/*.mp4',
  '**/*.mov',
  '**/*.wav',
  '**/*.ttf',
  '**/*.eot',
  '**/*.woff',
  '**/*.woff2',
  '**/*.svg',
  '**/node_modules/**',
  '.git/**',
  '**/.gitignore',
  '**/package-lock.json',
  '**/yarn.lock',
];

/**
 * Parse and transform patterns from .aiexclude files, check excluded files.
 *
 * .aiexclude follows the same syntax as a .gitignore file with the following differences:
 *
 *  - Unlike an empty .gitignore file, an empty .aiexclude file blocks all files in its directory and
 * all sub-directories, recursively. This is the same as if the file contained just * or **.
 *
 *  - .aiexclude files don't support negation (prefixing patterns with !).
 *
 * @see https://developer.android.com/studio/preview/studio-bot/data-and-privacy#aiexclude
 * @see https://git-scm.com/docs/gitignore#_pattern_format
 */
export class AiExclude {
  private readonly contentRoot: string;
  private readonly globbyOptions: GlobbyOptions;

  constructor(contentRoot?: string) {
    this.contentRoot = contentRoot || homedir();

    this.globbyOptions = {
      cwd: this.contentRoot,
      ignore: ALWAYS_IGNORE,
      onlyFiles: true,
      gitignore: true,
      followSymbolicLinks: false,
      absolute: true,
    };
  }

  async getPatternsToExclude(globbyOptions: GlobbyOptions = this.globbyOptions): Promise<string[]> {
    const aiExcludeFiles = await this.searchAiExcludeFiles(globbyOptions);

    const aiExcludeFilesMap = new Map<string, string>();

    await Promise.all(
      aiExcludeFiles.map(file =>
        readFile(file, 'utf-8').then(contents => aiExcludeFilesMap.set(file, contents))
      )
    );

    const allPatterns: string[] = (
      await Promise.all(
        Array.from(aiExcludeFilesMap.entries()).map(([file, contents]) => {
          // nothing to ignore
          if (contents === undefined) return [];

          // unlike gitignore, .aiexclude explicitly treats empty files as ignoring the entire
          // contents of its containing directory
          if (contents.trim() === '') {
            return this.getCleanPattern(IGNORE_ALL_PATTERN, file, globbyOptions);
          }

          return this.getPatternsFromFileContent(file, contents, globbyOptions);
        })
      )
    ).flat();

    return Array.from(new Set(allPatterns));
  }

  /**
   * Whether a file is excluded by .aiexclude files.
   *
   * TODO: cache the patterns either here or where it's called. Currently every call requires
   * searching every .aiexclude file and parse their patterns to find whether a file is excluded.
   */
  async expensiveIsFileExcluded(
    filename: string,
    ignore?: string[],
    options?: GlobbyOptions
  ): Promise<boolean> {
    if (!ignore) ignore = await this.getPatternsToExclude();

    const files = await globby(filename, {...this.globbyOptions, ...options, ignore});

    return files.length === 0;
  }

  private async searchAiExcludeFiles(
    globbyOptions: GlobbyOptions = this.globbyOptions
  ): Promise<string[]> {
    return globby(`**/${AI_EXCLUDE_FILENAME}`, {
      ...this.globbyOptions,
      ...globbyOptions,
      absolute: true, // must use absolute path
    });
  }

  private async getPatternsFromFileContent(
    excludeFilePath: string,
    content: string,
    globbyOptions: GlobbyOptions
  ): Promise<string[]> {
    const cleanPatterns = content
      .split('\n')
      .filter(pattern => {
        pattern = pattern.trim();
        // ignore comments and empty
        return !pattern.startsWith('#') && pattern.length;
      })
      .map(pattern => this.getCleanPattern(pattern, excludeFilePath, globbyOptions));

    return cleanPatterns;
  }

  private getCleanPattern(
    pattern: string,
    excludeFilePath: string,
    globbyOptions: GlobbyOptions = this.globbyOptions
  ): string {
    pattern = pattern.trim();

    // ignore anything after whitespace: https://git-scm.com/docs/gitignore#_pattern_format
    if (pattern.includes(' ')) {
      pattern = pattern.split(' ')[0];
    }

    // Unlike an empty .gitignore file, an empty .aiexclude file blocks all files in its directory and
    // all sub-directories, recursively. This is the same as if the file contained just * or **.
    if (pattern === '*') {
      pattern = IGNORE_ALL_PATTERN;
    }
    /**
     * Each condition below handles transformations needed to make the pattern work with the
     * glob library. Each condition and example is taken from the Android Studio documentation.
     * https://developer.android.com/studio/preview/studio-bot/data-and-privacy#aiexclude
     */

    // - KEYS blocks all files with the name "KEYS" with no file extension at or below the
    // .aiexclude directory.
    // - KEYS.* blocks all files called "KEYS" with any file extension at or below the
    // .aiexclude directory.
    // - *.kt blocks all Kotlin files at or below the .aiexclude directory.
    else if (!pattern.includes('/') && pattern !== IGNORE_ALL_PATTERN) {
      pattern = `./**/${pattern}`;
    } else {
      // /*.kt blocks all Kotlin files in the.aiexclude directory, but not below.
      if (pattern.startsWith('/')) {
        // add current directory to beginning of pattern to make it relative to the current directory
        pattern = `./${pattern.slice(1)}`;
      }

      // my/sensitive/dir/ blocks all files in the my/sensitive/dir directory and below,
      // relative to the .aiexclude directory.
      if (pattern.endsWith('/')) {
        pattern = `${pattern}**/*`;
      }
    }

    // These cases are already handled by the glob library:
    // - my/sensitive/dir/**/*.txt blocks all .txt files in or under my/sensitive/dir.
    // - my/sensitive/dir/*.txt blocks all .txt files in my/sensitive/dir, but not in sub-directories.

    // pattern with absolute path
    if (globbyOptions.absolute) return posix.join(posix.dirname(excludeFilePath), pattern);

    // pattern with relative path
    return posix.join(posix.dirname(excludeFilePath.replace(`${this.contentRoot}/`, '')), pattern);
  }
}
