import * as api from '../api';
import {WebSearchEngine} from '../engines/web-search';
import {LLM} from '../engines/llm';
import {ExternalFunction, FunctionState} from './external-function';
import {IdxAgentSettings} from '../idx-agent-settings';
import {SystemInterface} from '../system-interface';

interface QueryResultItem {
  link: string;
}

interface QueryResult {
  items: QueryResultItem[];
}

export class WebSearchFunction extends ExternalFunction {
  name = 'web_search' as const;
  description = 'Searches the web.';
  parameters: api.ObjectSchema = {
    type: 'object',
    properties: {
      query: {
        type: 'string',
        description: 'The query to search for.',
      },
    },
    required: ['query'],
  };
  requiresPermission = false;

  webSearchEngine: WebSearchEngine;

  constructor(
    public idxAgentSetting: IdxAgentSettings,
    public systemInterface: SystemInterface,
    public llm: LLM
  ) {
    super();
    this.webSearchEngine = new WebSearchEngine(idxAgentSetting);
  }

  async call(functionState: FunctionState): Promise<void> {
    const result = await this.search(functionState);
    functionState.success(result);
  }

  async search(functionState: FunctionState): Promise<string> {
    const query = functionState.args.query;
    const rawSearchResults = await this.webSearchEngine.webSearch(query);
    const queryResults = JSON.parse(rawSearchResults) as QueryResult;
    queryResults.items.map(item => functionState.addUrl(item.link));
    const cleanSearchResults = await this.llm.generateContent({
      generationConfig: this.llm.getGenerationConfig(),
      contents: [
        {
          role: 'user',
          parts: [
            {
              text: `\
Given this query: ${query}
And these search JSON search results:
${rawSearchResults}
What is the best answer?`,
            },
          ],
        },
      ],
    });
    return cleanSearchResults!.candidates[0].content.parts[0].text!;
  }
}
