import {Prompt, PromptAction, Params, PromptAvailability, PromptContext} from './prompt';
import {Logger} from './logger';
import path from 'path';
import {readFile, readdir} from 'fs/promises';
import {makePromptEngine} from 'prompt-engine';
import * as vscode from 'vscode';
import {CONFIG_AI_ENABLE_PREVIEW_COMMANDS} from 'vscode-shared/config';
import { FeatureFlags } from './features';

const ext = '.njk';
const globalTemplates = (root: string) => path.resolve(root, 'ai', 'prompts');
const workspaceTemplates = (root: string) => path.resolve(root, '.idx', 'ai', 'prompts');

export interface Watcher {
  doesDirectoryExist(path: string): Promise<boolean>;
  watch(path: string, pattern: string, onChange: () => Promise<void>): void;
}

/**
 * Manages Prompts without depedency on vscode
 */
export class PromptManager {
  private prompts: Record<string, Prompt> = {};
  private logger: Logger;
  public engine: ReturnType<typeof makePromptEngine>;
  private watcher: Watcher;

  constructor(logger: Logger, engine: ReturnType<typeof makePromptEngine>, watcher: Watcher) {
    this.logger = logger;
    this.engine = engine;
    this.watcher = watcher;
  }

  preamble() {
    return this.prompts['chat-preamble'];
  }

  facts() {
    return this.prompts['chat-facts'];
  }

  selfawareFacts() {
    return this.prompts['chat-facts-selfaware'];
  }

  explainSelection() {
    return this.prompts['explain-selection'];
  }

  chatEdit() {
    return this.prompts['chat-edit'];
  }

  chatAddComments() {
    return this.prompts['chat-add-comments'];
  }

  nameThreadPrompt() {
    return this.prompts['chat-name-thread'];
  }

  suggestNextPromptPrompt() {
    return this.prompts['chat-suggest-next-prompt'];
  }

  devtoolsDebug() {
    return this.prompts['devtools-debug']
  }

  /**
   * sidekickPrompts returns the prompts that are available to the sidekick.
   */
  sidekickPrompts(hasErrors = false, hasSelection = false) {
    let prompts = Object.values(this.prompts);
    prompts = prompts.filter(p => p.availability.includes(PromptAvailability.Inline));
    if (!hasErrors) {
      prompts = prompts.filter(p => !p.requiredContext.includes(PromptContext.Error));
    }
    if (hasSelection) {
      prompts = prompts.filter(p => !p.requiredContext.includes(PromptContext.NoSelection));
    } else {
      prompts = prompts.filter(p => !p.requiredContext.includes(PromptContext.Selection));
    }
    return prompts;
  }

  /**
   * Returns prompts that are available to chat
   */
  chatPrompts(hasErrors = false, hasSelection = false) {
    let prompts = Object.values(this.prompts);
    prompts = prompts.filter(p => p.availability.includes(PromptAvailability.Chat));
    if (!hasErrors) {
      prompts = prompts.filter(p => !p.requiredContext.includes(PromptContext.Error));
    }
    if (hasSelection) {
      prompts = prompts.filter(p => !p.requiredContext.includes(PromptContext.NoSelection));
    } else {
      prompts = prompts.filter(p => !p.requiredContext.includes(PromptContext.Selection));
    }
    return prompts;
  }

  async init(extensionPath: string, workspaceDir: string | undefined, watchForChanges: boolean, features: FeatureFlags) {
    const dirs = [globalTemplates(extensionPath)];
    const isEarlyAccessCommandsEnabled = await features.isEarlyAccessCommandsEnabled();
    if (!!workspaceDir) {
      dirs.push(workspaceTemplates(workspaceDir));
    }
    for (const dir of dirs) {
      this.logger.info(`loading ai prompts from ${dir}`);
      this.prompts = {...this.prompts, ...(await this.parseTemplates(dir, isEarlyAccessCommandsEnabled))};
      if (watchForChanges) {
        this.watchTemplates(dir, isEarlyAccessCommandsEnabled);
      }
    }
  }

  /**
   * sidekickPrompt matches a prompt for the given request, defaulting to the sidekick-create prompt
   * if a match is not found.
   */
  sidekickPrompt(requestPrompt: string, hasSelection: boolean): Prompt {
    let prompt: Prompt = this.prompts['sidekick-create'];
    if (hasSelection) {
      prompt = this.prompts['sidekick-edit'];
    }
    if (requestPrompt.startsWith('/')) {
      const [command] = requestPrompt.slice(1).split(' ');
      prompt = this.byCommand(command, PromptAvailability.Inline) || prompt;
    }
    return prompt;
  }

  byCommand(command: string, source: PromptAvailability) {
    return Object.values(this.prompts).find(
      p => p.command === command && p.availability.includes(source)
    );
  }

  private async parseTemplates(dir: string, isEarlyAccessCommandsEnabled: boolean): Promise<Record<string, Prompt>> {
    if (!(await this.watcher.doesDirectoryExist(dir))) {
      return {};
    }

    const prompts: Record<string, Prompt> = {};
    const entries = await readdir(dir);
    for (const f of entries) {
      if (path.extname(f) !== ext) {
        continue;
      }
      const name = path.basename(f, ext);
      const content = await readFile(path.join(dir, f), 'utf8');
      const meta = await this.engine.parse(content);

      // Skip preview commands if preview flag is not on.
      if (meta.frontmatter?.preview && !isPreviewCommandsEnabled()) {
        continue;
      } else if(meta.frontmatter?.earlyAccess && !isEarlyAccessCommandsEnabled) {
        continue;
      }

      prompts[name] = {
        name,
        meta,
        content,
        command: meta.frontmatter?.command ?? '',
        description: meta.frontmatter?.description ?? '',
        availability: meta.frontmatter?.availability ?? [],
        action: meta.frontmatter?.action ?? PromptAction.Edit,
        stopSequences: meta.frontmatter?.stopSequences ?? [],
        requiredContext: meta.frontmatter?.requiredContext ?? [],
      };
    }
    return prompts;
  }

  private watchTemplates(dir: string, isEarlyAccessCommandsEnabled: boolean) {
    this.watcher.watch(dir, `*${ext}`, async () => {
      this.logger.info(`reloading ai prompts from ${dir}`);
      this.prompts = {...this.prompts, ...(await this.parseTemplates(dir, isEarlyAccessCommandsEnabled))};
    });
  }

  /**
   * execute executes a prompt with the given parameters using the engine from the context.
   */
  execute(prompt: Prompt, params: Params) {
    return this.engine.execute(prompt.content, params);
  }
}

/**
 * Enable commands with preview field set to true.
 */
function isPreviewCommandsEnabled(): boolean {
  let {section, property} = CONFIG_AI_ENABLE_PREVIEW_COMMANDS;
  return !!vscode.workspace.getConfiguration(section).get(property);
}
