import {Script} from './scripts';
import {textMessage, readFile, runTerminalCommand, writeFile} from './utils';

export const explicitCasesScript: Script = {
  name: 'explicit cases',
  responses: [
    textMessage('tests a few explicit cases for tool use.'),
    // Case 1: write file, followed by run terminal
    writeFile('foo.js.', 'bar'),
    runTerminalCommand('ls'),
    textMessage('wrote foo.js and ran ls.'),
    // Case 2: read file, run terminal command followed by write file, followed by read file.
    readFile('foo.js'),
    runTerminalCommand('ls'),
    writeFile('boo.js.', 'baz'),
    readFile('boo.js'),
    textMessage('read file, run terminal command followed by write file, followed by read file.'),
    // Case 3: Read file, run terminal command, read file, write file, read file.
    readFile('foo.js'),
    runTerminalCommand('ls'),
    readFile('boo.js'),
    writeFile('too.js.', 'blu'),
    readFile('too.js'),
    textMessage('Read file, run terminal command, read file, write file, read file.'),
  ],
};
