import * as api from '../api';
import {
  FunctionAction,
  FunctionFrameEnum,
  FunctionStatusEnum,
  FunctionThreadState,
} from '~common/messaging/modules/aichat/aichat-protocol';
import * as protocol from '~common/messaging/modules/aichat/aichat-protocol';
export {
  FunctionAction,
  FunctionCall,
  FunctionFrame,
  FunctionCallArgs,
  FunctionName,
  FunctionResult,
} from '~common/messaging/modules/aichat/aichat-protocol';
import * as shared from '~common/messaging/modules/aichat/aichat-common';
import {SystemInterface} from '../system-interface';

export abstract class ExternalFunction {
  abstract systemInterface: SystemInterface;
  abstract name: api.FunctionName;
  abstract description: string;
  abstract parameters: api.ValueSchema;
  abstract call(functionState: FunctionState): Promise<void>;
  readonly actions: Partial<
    Record<FunctionAction, (functionState: FunctionState) => Promise<void>>
  > = {};
  readonly requiresPermission: boolean = true;

  updateArgs(args: api.FunctionCallArgs): api.FunctionCallArgs {
    return args;
  }

  async populateMetadata?(args: api.FunctionCallArgs): Promise<Record<string, any> | undefined> {
    return undefined;
  }
}

export class FunctionState extends shared.FunctionState {
  constructor(thread: FunctionThreadState, functionState: protocol.FunctionFrame) {
    super(thread, functionState);
  }

  static fromLastFrame(
    thread: protocol.FunctionThreadState | undefined
  ): FunctionState | undefined {
    return this.genericFromLastFrame<FunctionState>(FunctionState, thread);
  }

  get response(): api.FunctionResponse {
    return {
      name: this.name,
      response: this.functionResult!,
    };
  }

  executing() {
    this.update('executing');
  }

  detach(result: string) {
    // We detach functions to handle timeouts. In the case of detach, we race two promises, but only
    // the true completion will overwrite this state should it ever actually occur.
    if (this.state === 'executing') {
      this.complete('detached', result);
    }
  }

  reject() {
    this.failure('rejected');
    this.update('rejected'); // force state to rejected, not failed
  }

  update(state: FunctionFrameEnum) {
    this.functionFrame.state = state;
  }

  success(result: string) {
    this.complete('success', result);
  }

  failure(result: string) {
    this.complete('failure', result);
  }

  complete(status: FunctionStatusEnum, result: string) {
    this.setResult('completed', status, result);
  }

  setResult(state: FunctionFrameEnum, status: FunctionStatusEnum, result: string) {
    this.functionFrame.result = {status: status, result: result};
    this.update(state);
  }

  readFile(path: string) {
    (this.thread.readFiles ??= []).push(path);
  }

  addUrl(url: string) {
    (this.thread.readUrls ??= []).push(url);
  }

  stateIs(state: FunctionFrameEnum) {
    return this.state === state;
  }

  addCall(call: api.FunctionCall, requiresPermission: boolean): protocol.FunctionFrame {
    const functionFrame: protocol.FunctionFrame = {call, state: 'requested', requiresPermission};
    this.thread.frames.push(functionFrame);
    if (requiresPermission) {
      (this.thread.framesWhichRequirePermission ??= []).push(functionFrame);
    }
    return functionFrame;
  }

  setFollowup(followup: string) {
    const framesWhichRequirePermission = this.thread.framesWhichRequirePermission;
    if (framesWhichRequirePermission) {
      framesWhichRequirePermission[framesWhichRequirePermission.length - 1].showFollowup = true;
    }
    this.thread.followup = followup;
  }
}
