import * as vscode from 'vscode';
import {FunctionStatusEnum} from '~common/messaging/modules/aichat/aichat-protocol';
import {readFileAsString} from '../file-utils';

// We set a timeout for actions. For now, we just unblock the agent so it can
// continue helping the user, but we may want to do more later to distinguish
// between the various cases, e.g. interactive, timed process, detached process.
// For example, we could give the agent different tools, or have the agent
// interact with the terminal via code.
const defaultTaskTimeoutMs = 10000;

class RunningTask {
  task: vscode.Task | undefined;
  isDetached: boolean = false;
  isFinished: boolean = false;
  taskExecution: vscode.TaskExecution | undefined;
  logFile: string;
  runningCommand: string;

  constructor(
    public taskId: number,
    public originalCommand: string,
    private notifyDetached: () => void,
    private notifyFinished: (status: FunctionStatusEnum, result: string) => void
  ) {
    this.logFile = `/tmp/ai.${taskId}.log`;
    this.runningCommand = `(set -o pipefail && ${originalCommand} 2>&1 | tee ${this.logFile})`;
  }

  initialize(task: vscode.Task, taskExecution: vscode.TaskExecution) {
    this.task = task;
    this.taskExecution = taskExecution;
  }

  async finish(status: FunctionStatusEnum) {
    const result = await readFileAsString(this.logFile);
    this.isFinished = true;
    this.notifyFinished(status, result);
  }

  async failure() {
    return this.finish('failure');
  }

  async success() {
    return this.finish('success');
  }

  detach() {
    if (!this.isFinished) {
      this.isDetached = true;
      this.notifyDetached();
    }
  }
}

const noTaskStartedSentinel = 0;
export const terminalTaskType = 'terminalTask';
interface TerminalTask extends vscode.TaskDefinition {
  id: number;
}

export class TaskManager {
  runningTasks: Record<number, RunningTask> = {};
  taskId = noTaskStartedSentinel;

  constructor(private taskTimeoutMs: number = defaultTaskTimeoutMs) {}

  detach(taskId: number) {
    this.runningTasks[taskId].detach();
  }

  getTaskId(): number {
    if (this.taskId !== noTaskStartedSentinel && this.runningTasks[this.taskId].isFinished) {
      return this.taskId;
    } else {
      return ++this.taskId;
    }
  }

  exec(
    command: string,
    notifyDetached: () => void,
    notifyFinished: (status: FunctionStatusEnum, result: string) => void
  ): Promise<void> {
    const taskId = this.getTaskId();
    const runningTask = new RunningTask(taskId, command, notifyDetached, notifyFinished);
    this.runningTasks[taskId] = runningTask;
    const that = this;
    const executingTask = new Promise<void>(async resolve => {
      const task = new vscode.Task(
        {type: terminalTaskType, id: taskId},
        vscode.TaskScope.Workspace,
        `AI: ${runningTask.originalCommand}`,
        'monospace',
        new vscode.ShellExecution(runningTask.runningCommand)
      );

      vscode.tasks.onDidEndTaskProcess(async e => {
        if (e.execution.task.definition.id === runningTask.taskId) {
          e.execution.terminate();
          if (e.exitCode === 0) {
            await runningTask.success();
            resolve();
          } else {
            await runningTask.failure();
            resolve();
          }
        }
      });

      const taskExecution = await vscode.tasks.executeTask(task);
      runningTask.initialize(task, taskExecution);
    });
    return Promise.race([
      executingTask,
      new Promise<void>(resolve => {
        setTimeout(() => {
          resolve();
          that.detach(taskId);
        }, this.taskTimeoutMs);
      }),
    ]);
  }
}
